import NextLink from 'next/link';
import { ForwardedRef, forwardRef, ReactNode } from 'react';

import { LinkProps } from './types.d';

const Link = (props: LinkProps, ref: ForwardedRef<ReactNode>) => {
  // We copy the props in an attribute object
  // in order to remove the properties we want to assign manually.
  // As we don't know which attribute can be applied, we have to rely on any type

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const attributes: any = Object.assign({}, props);
  delete attributes.className;
  delete attributes.to;
  delete attributes.children;
  const { to, children, className } = props;

  if (!to) {
    console.warn(`Warning: Bad Link via ${props} at ref ${ref}`);
    return null;
  }

  if (to.type === 'internal' && typeof to.url !== 'undefined') {
    const href = to.hash ? `${to.url}${to.hash}` : to.url;
    return (
      <NextLink href={href}>
        <a href={href} {...attributes} className={className} ref={ref}>
          {children}
        </a>
      </NextLink>
    );
  } else if (
    to.type === 'external' ||
    to.type === 'download' ||
    to.type === 'phoneNumber'
  ) {
    return (
      <a
        href={to.url}
        {...attributes}
        className={className}
        // All frame.io links (even different sub domains) should open in the same window
        target={
          to.url.startsWith('#') ||
          to.url.includes('frame.io') ||
          to.type === 'download' ||
          to.type === 'phoneNumber'
            ? '_self'
            : '_blank'
        }
        rel="noreferrer"
        ref={ref}
      >
        {children}
      </a>
    );
  }

  return null;
};

export default forwardRef<ReactNode, LinkProps>(Link);
