import { CSSProperties, useState } from 'react';

import SvgCaret from '~/assets/svg/caret.svg';
import ButtonPlay from '~/components/atoms/Buttons/UI/ButtonPlay';
import ButtonToggle from '~/components/atoms/Buttons/UI/ButtonToggle';
import Image from '~/components/atoms/image';
import Observer from '~/components/atoms/observer';
import ModalVideo from '~/components/molecules/Modal/ModalVideo';
import PortableText from '~/components/molecules/portableText';
import Shadow from '~/components/molecules/shadow';
import { dict } from '~/data/stores/Dictionary';
import { GradientOpacity } from '~/types/decorations.d';
import { cn } from '~/utils';
import gradientClassesMap from '~/utils/maps/gradientClasses';

import styles from './VideoPlaylist.styles.module.css';
import {
  CMSModuleVideoPlaylist,
  CMSVideoPlaylistVideo,
} from './VideoPlaylist.types.d';

const VideoPlaylist = ({
  content,
  gradient = GradientOpacity.HEAVY,
  playButtonLabel,
  videosList,
  background,
  className,
  slug,
}: CMSModuleVideoPlaylist) => {
  const [isPlaylistExpanded, setIsPlaylistExpanded] = useState(false);
  const [showVideoModal, setShowVideoModal] = useState(false);
  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  const [currentItemIndex, setCurrentItemIndex] = useState<number>(0);

  const renderClasses = cn(
    styles.videoPlaylist,
    isPlaylistExpanded && styles.isPlaylistExpanded,
    className,
  );

  const selectItemAndShowVideo = (item: CMSVideoPlaylistVideo) => {
    setCurrentItemIndex(
      videosList.findIndex((videoItem) => videoItem === item),
    );

    setShowVideoModal(true);
  };

  return (
    <section
      className={cn(renderClasses, background && styles.hasBackground)}
      style={
        {
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <div className={cn(styles.container)}>
        <PortableText
          isAutoGrid={false}
          content={content}
          className={styles.content}
          options={{
            block: {
              bodies: {
                className: styles.bodies,
              },
            },
          }}
        />
      </div>
      <Observer
        callback={updateIsInView}
        options={{ rootMargin: '100% 0%' }}
        className={cn(styles.container, styles.containerVideoPlaylist)}
      >
        <div className={styles.columnFeatured}>
          <Shadow className={styles.dropShadow}>
            <button
              className={cn(
                styles.featuredMediaWrapper,
                gradient && styles[gradientClassesMap[gradient]],
              )}
              onClick={() => selectItemAndShowVideo(videosList[0])}
              data-button-play-trigger
            >
              <Image
                className={styles.featuredPoster}
                source={videosList && videosList[0].poster}
                isDisplayed={isInView !== false}
              />

              <ButtonPlay
                className={cn(styles.buttonPlay, styles.featuredButton)}
                aria-label={playButtonLabel}
                tag={'div'}
              />

              <div className={styles.featuredLabel}>
                <span className={styles.featuredTitle}>
                  {videosList[0].title}
                </span>
                <span className={styles.featuredSubtitle}>
                  {videosList[0].subtitle}
                </span>
                <span className={styles.featuredSubtitleDetail}>
                  {videosList[0].subtitleDetail}
                </span>
              </div>
            </button>
          </Shadow>
        </div>
        <div className={styles.columnVideoList}>
          <ul className={styles.videoListWrapper}>
            {videosList &&
              videosList.map((videoListItem, index) => {
                if (index === 0) return null;
                return (
                  <li
                    key={videoListItem._key}
                    className={styles.videoListItem}
                    data-button-play-trigger
                  >
                    <button
                      className={styles.itemThumbnailButton}
                      onClick={() => selectItemAndShowVideo(videoListItem)}
                      // TODO: Add button label to dictionary
                      aria-label={dict('clickToWatchVideo')}
                    >
                      <Image
                        className={styles.itemThumbnail}
                        source={
                          videoListItem.poster || videoListItem.video.thumbnail
                        }
                        isDisplayed={isInView !== false}
                      />
                      <ButtonPlay
                        className={styles.itemPlayButton}
                        size="small"
                        tag={'div'}
                      />
                    </button>
                    <div className={styles.itemDescription}>
                      <button
                        className={styles.itemDescriptionButton}
                        onClick={() => selectItemAndShowVideo(videoListItem)}
                      >
                        <h4 className={styles.itemTitle}>
                          {videoListItem.title}
                        </h4>

                        <span className={styles.itemSubtitleWrapper}>
                          <h5 className={styles.itemSubtitle}>
                            {videoListItem.subtitle}
                          </h5>
                          <span className={styles.itemSubtitleDetail}>
                            {videoListItem.subtitleDetail}
                          </span>
                        </span>
                      </button>
                    </div>
                  </li>
                );
              })}
          </ul>
          <ButtonToggle
            icon={<SvgCaret />}
            onClick={(isExpanded) => setIsPlaylistExpanded(isExpanded)}
            className={styles.viewAllButton}
            labelIdle={dict('viewLess')}
            labelActive={dict('viewAll')}
          />
        </div>
      </Observer>

      <ModalVideo
        selectedItemIndex={currentItemIndex}
        videosList={videosList}
        modalTriggeredByClick={showVideoModal}
        onModalClose={() => setShowVideoModal(false)}
      />
    </section>
  );
};

export default VideoPlaylist;
