import { gsap } from 'gsap';
import {
  CSSProperties,
  KeyboardEvent,
  MouseEvent,
  useEffect,
  useRef,
  useState,
} from 'react';

import SvgPlusIcon from '~/assets/svg/plus-icon.svg';
import Image from '~/components/atoms/image';
import Observer from '~/components/atoms/observer';
import Glow from '~/components/molecules/glow';
import { GlowPosition } from '~/components/molecules/glow/types.d';
import { MemoPortableText as PortableText } from '~/components/molecules/portableText';
import Shadow from '~/components/molecules/shadow';
import { dict } from '~/data/stores/Dictionary';
import useUIStore from '~/state/ui';
import { RaggingType } from '~/types/decorations.d';
import { cn, useIsomorphicLayoutEffect as useLayoutEffect } from '~/utils';

import { CMSFancyTestimonialsProps, FancyTestimonial } from '../types';
import { animateIn, animateOut } from './animation';
import styles from './styles.module.css';
import TestimonialsModal from './testimonialsModal';

const FancyHoverTestimonials = ({
  title,
  text,
  testimonials,
  className,
}: CMSFancyTestimonialsProps) => {
  const $cards = useRef<HTMLDivElement[]>(Array(testimonials.length));
  const $cardsInner = useRef<HTMLButtonElement[]>(Array(testimonials.length));
  const $titles = useRef<HTMLButtonElement[]>(Array(testimonials.length));
  const $list = useRef<HTMLUListElement>(null);
  const [currentCardRect, setCurrentCardRect] = useState<DOMRect>();
  const [showCard, setShowCard] = useState<boolean>(false);
  const [showModal, setShowModal] = useState<boolean>(false);
  const [currentIndex, setCurrentIndex] = useState<number>();
  const listRect = useRef<DOMRect>();
  const preloadedImages = useRef<boolean[]>(
    Array(testimonials.length).fill(false),
  );

  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  const mousePositionXSetter = useRef<(value: number) => void>();
  const mousePositionYSetter = useRef<(value: number) => void>();
  const windowWidth = useUIStore((state) => state.windowWidth);

  const handleImageReady = () => {
    if (currentIndex !== undefined)
      preloadedImages.current[currentIndex] = true;
  };

  const handleHover = (index: number) => {
    if (index === currentIndex) return;

    setCurrentIndex(index);
    setShowCard(true);
  };
  const handleMouseLeave = () => {
    setShowCard(false);
    setCurrentIndex(undefined);
  };

  const handleClick = (index: number) => {
    const target = $cardsInner.current[index] as HTMLButtonElement;
    gsap.ticker.add(
      () => {
        const rect = target.getBoundingClientRect();
        setShowModal(true);
        setCurrentIndex(index);
        setCurrentCardRect(rect);
      },
      true,
      true,
    );
  };

  const handleMouseMove = (event: MouseEvent<HTMLButtonElement>) => {
    if (listRect.current) {
      const max = listRect.current.width;
      const x = gsap.utils.mapRange(
        0,
        max,
        0,
        max / 1.5,
        event.clientX - listRect.current.x,
      );
      const y = gsap.utils.mapRange(
        0,
        max,
        0,
        max / 1.5,
        event.nativeEvent.offsetY,
      );
      if (mousePositionXSetter.current) mousePositionXSetter.current(x);
      if (mousePositionYSetter.current) mousePositionYSetter.current(y);
    }
  };

  const handleKeyDown = (
    event: KeyboardEvent<HTMLButtonElement | HTMLLIElement>,
  ) => {
    if (event.code === 'Enter') {
      setShowModal(true);
    }
  };

  useEffect(() => {
    $cards.current.forEach(($card, index) => {
      if (index === currentIndex) return;
      animateOut($card, {
        // add delay to prevent flashing when opening modal
        delay: showModal ? 0.15 : 0,
      });
    });

    if (currentIndex !== undefined) {
      animateIn($cards.current[currentIndex]);
    }
    // intentionally omitting showModal state because it creates double animation triggers
  }, [currentIndex]);

  useEffect(() => {
    // when resize, reset preload flags to false so that we trigger preload again
    preloadedImages.current.fill(false);
    gsap.ticker.add(
      () => {
        listRect.current = $list.current?.getBoundingClientRect();
      },
      true,
      true,
    );
  }, [windowWidth]);

  useLayoutEffect(() => {
    mousePositionXSetter.current = gsap.quickSetter(
      $list.current,
      '--mouse-x',
      'px',
    ) as (value: number) => void;
    mousePositionYSetter.current = gsap.quickSetter(
      $list.current,
      '--mouse-y',
      'px',
    ) as (value: number) => void;
  }, []);

  const canShowDummyElement =
    currentIndex !== undefined && preloadedImages.current.includes(false);

  return (
    <div
      className={cn(
        styles.hoverTestimonials,
        showModal && styles.modalOpen,
        className,
      )}
    >
      <h4 className={styles.title}>{title}</h4>
      <PortableText
        content={text}
        isAutoGrid={false}
        className={styles.content}
        ragging={RaggingType.TIGHT}
        options={{
          block: {
            titles: {
              className: styles.titles,
            },
          },
        }}
      />
      <Observer
        callback={updateIsInView}
        options={{ rootMargin: '200% 0%' }}
        className={styles.testimonialsWrapper}
      >
        <ul
          ref={$list}
          className={styles.testimonials}
          onMouseLeave={handleMouseLeave}
        >
          {testimonials.map((testimonial: FancyTestimonial, index: number) => (
            <li
              className={styles.testimonial}
              style={{ '--index-fancy-testimonial': index } as CSSProperties}
              key={testimonial._key}
              onMouseOver={() => handleHover(index)}
              onFocus={() => handleHover(index)}
            >
              {/* Title */}
              <button
                ref={(node: HTMLButtonElement) =>
                  ($titles.current[index] = node)
                }
                className={styles.testimonialTitleWrapper}
                onMouseMove={handleMouseMove}
                onClick={() => handleClick(index)}
                onKeyDown={handleKeyDown}
              >
                <p className={styles.testimonialTitle}>{testimonial.label}</p>
              </button>
              {/* Card */}
              <div
                ref={(node: HTMLDivElement) => ($cards.current[index] = node)}
                className={styles.testimonialCardWrapper}
              >
                <Glow
                  className={styles.testimonialGlowWrapper}
                  config={{
                    type: 'COLOR',
                    color:
                      testimonial.image.asset.lightMutedBackground || '#FFFFFF',
                    position: GlowPosition.BOTTOM_RIGHT,
                    intensity: 0.7,
                    scale: 1.4,
                  }}
                >
                  <Shadow className={styles.testimonialShadowWrapper}>
                    <button
                      ref={(node: HTMLButtonElement) =>
                        ($cardsInner.current[index] = node)
                      }
                      className={cn(
                        styles.testimonialHoverCard,
                        showCard && index === currentIndex && styles.active,
                      )}
                      onKeyDown={handleKeyDown}
                      aria-label={dict('clickToViewTestimonial')}
                    >
                      <Image
                        className={styles.testimonialMedia}
                        source={testimonial.image}
                        isDisplayed={isInView !== false}
                      />
                      {testimonial.logoImage && (
                        <Image
                          className={styles.testimonialLogo}
                          source={testimonial.logoImage}
                          isDisplayed={isInView !== false}
                        />
                      )}

                      <div data-index={index} className={styles.moreButton}>
                        <SvgPlusIcon className={styles.buttonIcon} />
                      </div>
                    </button>
                  </Shadow>
                </Glow>
              </div>
            </li>
          ))}
        </ul>
        <ul
          className={cn(
            styles.testimonials,
            styles.testimonialsGradients,
            showCard && styles.hidden,
          )}
          aria-hidden="true"
        >
          {testimonials.map((testimonial: FancyTestimonial) => (
            <li className={styles.testimonial} key={testimonial._key}>
              <p className={styles.testimonialTitle}>{testimonial.label}</p>
            </li>
          ))}
        </ul>
      </Observer>
      {showModal && currentCardRect && (
        <TestimonialsModal
          currentIndex={currentIndex}
          sourceRect={currentCardRect}
          testimonials={testimonials}
          onClose={() => setShowModal(false)}
        />
      )}
      {/* Dummy element to preload rightly sized image for modal */}
      {canShowDummyElement && (
        <div className={styles.preload} aria-hidden="true">
          <Image
            className={styles.preloadImage}
            source={testimonials[currentIndex].image}
            onReady={handleImageReady}
            isDisplayed={isInView !== false}
          />
        </div>
      )}
    </div>
  );
};

export default FancyHoverTestimonials;
