import { ForwardedRef, forwardRef } from 'react';

import Image from '~/components/atoms/image';
import GlobalData from '~/data/stores/GlobalData';
import { cn } from '~/utils';

import styles from './styles.module.css';
import { ShadowProps } from './types';

const Shadow = (
  { children, className }: ShadowProps,
  ref: ForwardedRef<HTMLDivElement>,
) => {
  if (GlobalData.data?.assets) {
    const sources = {
      bl: GlobalData.data.assets['shadow-bl'],
      bm: GlobalData.data.assets['shadow-bm'],
      br: GlobalData.data.assets['shadow-br'],
      tr: GlobalData.data.assets['shadow-tr'],
      mr: GlobalData.data.assets['shadow-mr'],
    };

    return (
      <div
        className={cn(
          className,
          styles.shadowWrapper,
          typeof children === 'undefined' && styles.standalone,
        )}
        ref={ref}
      >
        <div className={styles.shadow}>
          <div className={styles.bottom}>
            {sources.bl && (
              <Image
                source={sources.bl}
                className={styles.bottomLeft}
                quality={80}
                preload={true}
                sizes={{
                  height: 600,
                  width: 405,
                }}
              />
            )}
            {sources.bm && (
              <Image
                source={sources.bm}
                className={styles.bottomMiddle}
                quality={80}
                preload={true}
                sizes={{
                  height: 600,
                  width: 4,
                }}
              />
            )}
            {sources.br && (
              <Image
                source={sources.br}
                quality={80}
                className={styles.bottomRight}
                preload={true}
                sizes={{
                  height: 600,
                  width: 405,
                }}
              />
            )}
          </div>
          <div className={styles.right}>
            {sources.tr && (
              <Image
                source={sources.tr}
                className={styles.topRight}
                quality={80}
                preload={true}
                sizes={{
                  height: 413,
                  width: 405,
                }}
              />
            )}
            <div className={styles.middleRight}>
              {sources.mr && (
                <Image
                  source={sources.mr}
                  className={styles.middleRightImg}
                  quality={80}
                  preload={true}
                  sizes={{
                    height: 4,
                    width: 405,
                  }}
                />
              )}
            </div>
          </div>
        </div>
        {children}
      </div>
    );
  }
  return (
    <div
      className={cn(
        className,
        styles.shadowWrapper,
        typeof children === 'undefined' && styles.standalone,
      )}
      ref={ref}
    >
      <div />
      {children}
    </div>
  );
};

export default forwardRef<HTMLDivElement, ShadowProps>(Shadow);
