import { MutableRefObject } from 'react';

const addToRefArray = <T>(
  element: T,
  array: MutableRefObject<T[]>,
  index = -1,
) => {
  if (
    typeof element !== 'undefined' &&
    element &&
    typeof array !== 'undefined' &&
    array
  ) {
    if (array && Array.isArray(array.current)) {
      if (index === -1) {
        array.current.push(element);
      } else {
        array.current[index] = element;
      }
    } else {
      console.warn(
        'Cannot add to Ref Array. Ensure ref is properly defined. Example: useRef([] as HTMLDivElement[]);',
      );
    }
  }
};

export default addToRefArray;
