import '~/styles/index.css';
import '../../scripts/wdyr';

import { usePreviewSubscription } from 'data/sanity';
import type { AppContext, AppProps } from 'next/app';
import dynamic from 'next/dynamic';
import Head from 'next/head';
import Link from 'next/link';
import { useRouter } from 'next/router';

import DomUtils from '~/components/atoms/domUtils';
import Navigation from '~/components/organisms/navigation';
import { getGlobalConfig } from '~/data';
import fieldFormatters from '~/data/formatters/fields';
import DictionaryData from '~/data/stores/Dictionary';
import GlobalData from '~/data/stores/GlobalData';
import { CMSApp } from '~/types/index.d';

import styles from './styles.module.css';

const Debug = dynamic(() => import('~/components/atoms/debug'), { ssr: false });
const NEXT_PUBLIC_SANITY_STUDIO_API_USE_DRAFTS =
  process.env.NEXT_PUBLIC_SANITY_STUDIO_API_USE_DRAFTS;

type Props = {
  Component: AppProps['Component'];
  props: any;
  appProps: CMSApp;
  pageProps: any;
};

const Analytics = dynamic(() => import('~/components/organisms/analytics'), {
  ssr: false,
});

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
const App = ({ Component, pageProps, props }: Props & AppProps) => {
  const router = useRouter();

  const { data: globalData } = usePreviewSubscription(props.data?.query, {
    params: {},
    initialData: props.data?.globalData,
    enabled: props.preview,
  });

  if (!globalData) return null;

  // run formatters on global data objects
  for (const key in globalData) {
    const globalModule = globalData[key];
    if (typeof globalModule !== 'undefined' && globalModule) {
      for (const formatter of fieldFormatters) {
        formatter(globalModule);
      }
    }
  }

  GlobalData.initialize(globalData);

  DictionaryData.initialize(globalData.dictionary);

  return (
    <>
      <Analytics />
      <Head>
        <meta
          name="viewport"
          content="width=device-width, initial-scale=1.0, viewport-fit=cover"
        />
        <link rel="icon" href="/favicon.svg" />
        <link rel="mask-icon" href="mask-icon.svg" color="#000000" />
        <link rel="apple-touch-icon" href="/favicon.png" />
        <link rel="manifest" href="manifest.json" />
        <meta name="theme-color" content="#000000" />
      </Head>
      {router.isPreview && (
        <div>
          <div className={styles.previewBanner}>
            <p>
              <b>PREVIEW MODE ENABLED</b>, click{' '}
              <Link href="/api/exit-preview">
                <a>here</a>
              </Link>{' '}
              to exit
            </p>
          </div>
          <hr />
        </div>
      )}
      <Navigation
        navigation={globalData.navigation}
        globalData={globalData}
        hideAnnouncementBanner={pageProps.data?.page?.hideAnnouncementBanner}
      />
      <div className={`${styles.modulesWrapper} scrollable-container`}>
        <Component {...pageProps} globalData={globalData} />
      </div>
      <DomUtils accentColor={pageProps?.data?.page?.accentColor} />
      {process.env.NEXT_PUBLIC_VERCEL_ENV !== 'production' && <Debug />}
    </>
  );
};

App.getInitialProps = async (ctx: AppContext) => {
  const { data, query } = await getGlobalConfig(
    NEXT_PUBLIC_SANITY_STUDIO_API_USE_DRAFTS === 'true' || ctx.router.isPreview,
  );

  return {
    props: {
      preview: ctx.router.isPreview,
      data: { globalData: data, query },
    },
  };
};

export default App;
