import { CSSProperties } from 'react';

import RadioButtons from '~/components/atoms/radioButtons/RadioButtons';
import Glow from '~/components/molecules/glow';
import { GlowType } from '~/components/molecules/glow/types.d';
import PortableText from '~/components/molecules/portableText';
import Shadow from '~/components/molecules/shadow';
import GlobalData from '~/data/stores/GlobalData';
import usePricingStore from '~/state/pricing';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { cn, getSpacersStyles } from '~/utils';

import { PricePeriodValue } from '../global/types.d';
import PricingOverviewCard from './PricingOverviewCard';
import styles from './styles.module.css';
import { CMSModulePricingOverview } from './types';

const PricingOverview = ({
  text,
  radioButtons,
  hasSpacers,
  accentColor,
  background,
  className,
  slug,
}: CMSModulePricingOverview) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  const globalPricing = GlobalData.data.pricing;
  const pricePeriod = usePricingStore((state) => state.pricePeriod);
  const setPricePeriod = usePricingStore((state) => state.setPricePeriod);

  const onRadioButtonChange = (index: number) => {
    const radioValue = radioButtons[index].value;

    if (
      Object.values(PricePeriodValue).includes(radioValue as PricePeriodValue)
    ) {
      setPricePeriod(radioValue as PricePeriodValue);
    }
  };

  return (
    <Shadow>
      <section
        className={cn(
          styles.pricingOverview,
          background && styles.hasBackground,
          hasSpacers?.before && decorationsStyles.hasSpacerBefore,
          hasSpacers?.after && decorationsStyles.hasSpacerAfter,
          className,
        )}
        style={
          {
            ...(spacerValues as CSSProperties),
            ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
            ...(background ? { '--module-background': background } : {}),
          } as CSSProperties
        }
        id={slug}
      >
        <Glow
          className={styles.topRightGlow}
          type={GlowType.BAKED_CANVAS}
          config={{
            intensity: 0.5,
            scale: 1.4,
            color: '#9947EB',
            type: 'COLOR',
          }}
        />
        <PortableText
          animate={false}
          isAutoGrid={false}
          content={text}
          className={styles.text}
          options={{
            block: {
              titles: {
                className: styles.titles,
              },
              bodies: {
                className: styles.bodies,
              },
            },
            types: {
              'block.buttons': {
                className: styles.buttons,
              },
              'block.form': {
                className: styles.forms,
              },
            },
          }}
        />
        <RadioButtons
          className={styles.radioButtons}
          buttons={radioButtons}
          onChange={onRadioButtonChange}
        />

        <div className={styles.pricingCardsWrapper}>
          {globalPricing.products.flatMap((product) =>
            !product.secondaryTitle ? (
              <PricingOverviewCard
                key={product._key}
                product={product}
                pricePeriod={pricePeriod}
              />
            ) : null,
          )}
        </div>

        <Glow
          className={styles.centerRightGlow}
          type={GlowType.BAKED_CANVAS}
          config={{
            intensity: 0.6,
            scale: 1.3,
            color: '#9947EB',
            type: 'COLOR',
          }}
        />

        <div className={styles.customCardsWrapper}>
          {globalPricing.products.flatMap((product) =>
            product.secondaryTitle ? (
              <PricingOverviewCard
                key={product._key}
                product={product}
                pricePeriod={pricePeriod}
              />
            ) : null,
          )}
        </div>
      </section>
    </Shadow>
  );
};

export default PricingOverview;
