import { mergeRefs } from '@react-aria/utils';
import { gsap } from 'gsap';
import {
  CSSProperties,
  ForwardedRef,
  forwardRef,
  useCallback,
  useRef,
} from 'react';

import HoverCard from '~/components/molecules/hoverCard';
import {
  cn,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';

import { CMSAsymmetricalCardItem } from '../../types';
// import { parallaxAnimation } from './animations';
import styles from './styles.module.css';
import { RowProps } from './types';

const Row = (
  { cards, rowIndex, className }: RowProps,
  ref: ForwardedRef<HTMLDivElement>,
) => {
  const $wrapper = useRef<HTMLDivElement>(null);
  const progressSetter = useRef<(value: number) => void>();
  const onProgress = useCallback((progress: number) => {
    if (progressSetter.current) {
      progressSetter.current(50 + progress * -90);
    }
  }, []);

  useScrollProgress($wrapper, onProgress);

  useLayoutEffect(() => {
    progressSetter.current = gsap.quickSetter(
      $wrapper.current,
      '--row-scroll-progress',
      '%',
    ) as (value: number) => void;
  }, []);

  return (
    <div
      className={cn(styles.row, className)}
      ref={mergeRefs(ref, $wrapper)}
      style={
        {
          '--row-index': rowIndex,
        } as CSSProperties
      }
    >
      {cards.map((card: CMSAsymmetricalCardItem) => (
        <HoverCard
          _key={card._key}
          content={card.content}
          gradient="asymmetrical"
          image={card.image}
          key={card._key}
          title={card.title}
        />
      ))}
    </div>
  );
};

export default forwardRef<HTMLDivElement, RowProps>(Row);
