import { CSSProperties } from 'react';

import Glow from '~/components/molecules/glow';
import { GlowType } from '~/components/molecules/glow/types.d';
import PortableText from '~/components/molecules/portableText';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { cn, getSpacersStyles } from '~/utils';

import styles from './styles.module.css';
import { CMSModuleLargeStats } from './types';

const LargeStats = ({
  content,
  stats,
  hasSpacers,
  accentColor,
  background,
  className,
  slug,
}: CMSModuleLargeStats) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  const renderClass = cn(
    styles.largeStats,
    hasSpacers?.before && decorationsStyles.hasSpacerBefore,
    hasSpacers?.after && decorationsStyles.hasSpacerAfter,
    className,
  );
  // TODO: reduce need for multiple moduleStyles to one for entire options block
  return (
    <section
      className={cn(renderClass, background && styles.hasBackground)}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <Glow
        className={styles.topLeftGlow}
        type={GlowType.BAKED_CANVAS}
        config={{
          intensity: 0.3,
          scale: 1.4,
          color: '#58372B',
          type: 'COLOR',
        }}
      />

      <div className={styles.container}>
        <div className={styles.columnLeft}>
          <PortableText
            isAutoGrid={false}
            content={content}
            className={styles.content}
            options={{
              block: {
                eyebrow: {
                  className: styles.eyebrow,
                  moduleStyles: styles,
                },
                title5: {
                  tagName: 'h3',
                },
                titles: {
                  className: styles.titles,
                  moduleStyles: styles,
                },
                bodies: {
                  className: styles.bodies,
                  moduleStyles: styles,
                },
              },
              marks: {
                em: {
                  className: styles.em,
                },
              },
            }}
          />
        </div>
        <div className={styles.columnRight}>
          <PortableText
            isAutoGrid={false}
            content={stats}
            className={styles.statsContent}
            options={{
              types: {
                'block.stats': {
                  className: styles.stats,
                  moduleStyles: styles,
                },
                'block.touts': {
                  className: styles.touts,
                  moduleStyles: styles,
                },
              },
            }}
          />
        </div>
      </div>
      <Glow
        className={styles.rightGlow}
        type={GlowType.BAKED_CANVAS}
        config={{
          intensity: 0.8,
          scale: 1.6,
          gradient: {
            startColor: '#452C24',
            stopColor: '#666049',
            startPosition: 0,
            stopPosition: 1,
            orientation: Math.PI / 2,
          },
          type: 'GRADIENT',
        }}
      />
    </section>
  );
};

export default LargeStats;
