import imageFragment from '~/components/atoms/image/fragment';
import videoFragment from '~/components/atoms/video/fragment';

const fragment = `
  name,
  'sanityMedia': {
    mediaType == 'image' => {
      image != null => image { ${imageFragment} }
    },
    mediaType == 'video' => {
      defined(video.asset) => video { ${videoFragment} },
    },
  }
`;

export default fragment;
