import { gsap } from 'gsap';
import { useKeenSlider } from 'keen-slider/react';
import { useCallback, useRef, useState } from 'react';

import buttonStyles from '~/components/atoms/Buttons/Ctas/ButtonBase.styles.module.css';
import ModalCarouselArrows from '~/components/atoms/carouselControls/modalCarouselArrows';
import Link from '~/components/atoms/link';
import PaginationDots from '~/components/atoms/pagination/dots/';
import { CMSLogoTextItem } from '~/components/molecules/logoTextItem/types';
import Modal from '~/components/molecules/Modal';
import PortableText from '~/components/molecules/portableText';
import SvgOrImage from '~/components/molecules/svgOrImage';
import { cn, keenSliderConfig } from '~/utils';
import addToRefArray from '~/utils/addToRefArray';

import styles from './styles.module.css';
import { IntegrationsModalProps } from './types';

const IntegrationsModal = ({
  selectedIntegrationGroup,
  selectedIntegrationItemIndex,
  onClose,
}: IntegrationsModalProps) => {
  const [activeIndex, setActiveIndex] = useState(selectedIntegrationItemIndex);

  const totalItems = selectedIntegrationGroup.length;
  const refSlides = useRef<HTMLLIElement[]>(Array(totalItems));

  // initialize carousel
  const [sliderRef, sliderInstanceRef] = useKeenSlider({
    ...keenSliderConfig.defaultConfig,

    initial: selectedIntegrationItemIndex,
    detailsChanged(slider) {
      slider.track.details?.slides.map((slide, i) => {
        gsap.set(refSlides.current[i], { opacity: slide.portion });
      });
    },
    slideChanged: (slider) => {
      const currentIndex = slider.track.details?.rel;
      setActiveIndex(currentIndex);
    },
    breakpoints: {
      [keenSliderConfig.breakpoints.base.mediaQuery]: {
        selector: '.slide',
        slides: {
          perView: 'auto',
        },
      },
      [keenSliderConfig.breakpoints.sm.mediaQuery]: {
        slides: totalItems,
      },
    },
  });
  const scrollPrev = useCallback(() => {
    sliderInstanceRef.current?.prev();
  }, [sliderInstanceRef]);

  const scrollNext = useCallback(() => {
    sliderInstanceRef.current?.next();
  }, [sliderInstanceRef]);

  const goToIndex = useCallback(
    (index: number) => {
      sliderInstanceRef.current?.moveToIdx(index);
    },
    [sliderInstanceRef],
  );

  const renderItem = (item: CMSLogoTextItem, index: number) => {
    return (
      <li
        key={`integration-item-${index}`}
        className={cn(
          styles.slideItem,
          'slide',
          index === activeIndex && styles.active,
        )}
        ref={(ref: HTMLLIElement) => {
          addToRefArray(ref, refSlides, index);
        }}
        aria-hidden={activeIndex === index ? false : true}
      >
        <header className={styles.modalHeader}>
          <SvgOrImage {...item.svgOrImage} className={styles.icon} />
          <h4 className={styles.label}>{item.label}</h4>
        </header>

        {item.description && (
          <PortableText
            animate={false}
            className={styles.description}
            isAutoGrid={false}
            content={item.description}
            options={{
              blocks: {
                titles: {
                  className: styles.titles,
                },
              },
              marks: {
                link: {
                  className: styles.link,
                },
                internalLink: {
                  className: styles.link,
                },
              },
            }}
          />
        )}

        {item.link && (
          <div className={styles.linkWrapper}>
            <Link
              to={item.link}
              className={cn(styles.link, buttonStyles.textLink)}
              tabIndex={activeIndex === index ? 0 : -1}
            >
              {item.link.label}
            </Link>
          </div>
        )}
      </li>
    );
  };
  return (
    <Modal onClose={onClose} wrapperClassName={styles.modal} hasShadow={false}>
      <div className={styles.modalContent}>
        <ul className={styles.slidesContainer} ref={sliderRef}>
          {selectedIntegrationGroup.map((item, index) => {
            return renderItem(item, index);
          })}
        </ul>

        {/* desktop only */}
        <div className={styles.carouselArrowsContainer}>
          <ModalCarouselArrows
            activeIndex={activeIndex}
            total={totalItems}
            className={styles.carouselArrows}
            handleNext={scrollNext}
            handlePrevious={scrollPrev}
            prevTitle={selectedIntegrationGroup[activeIndex - 1]?.label}
            nextTitle={selectedIntegrationGroup[activeIndex + 1]?.label}
          />
        </div>
        {/* mobile only */}
        <PaginationDots
          className={styles.carouselPagination}
          total={totalItems}
          activeIndex={activeIndex}
          onClick={goToIndex}
        />
      </div>
    </Modal>
  );
};

export default IntegrationsModal;
