import { gsap } from 'gsap';
import {
  ForwardedRef,
  forwardRef,
  useCallback,
  useImperativeHandle,
  useMemo,
  useRef,
} from 'react';

import Image from '~/components/atoms/image';
import Glow from '~/components/molecules/glow';
import {
  GlowPosition,
  GlowRef,
  GlowType,
} from '~/components/molecules/glow/types.d';
import Logo from '~/components/molecules/logo';
import { MemoPortableText } from '~/components/molecules/portableText';
import Shadow from '~/components/molecules/shadow';
import { GradientOpacity } from '~/types/decorations.d';
import { cn, useIsomorphicLayoutEffect as useLayoutEffect } from '~/utils';
import gradientClassesMap from '~/utils/maps/gradientClasses';
import { EaseType } from '~/utils/singletons/Easing';

import styles from './styles.module.css';
import {
  CarouselTestimonialSlideProps,
  ForwardedTestimonialSlideRef,
} from './types';

const TestimonialSlide = (
  {
    className,
    slideIndex,
    gradient = GradientOpacity.MEDIUM,
    gradientMobile = GradientOpacity.MEDIUM,
    isQuoted,
    logo,
    text,
    image,
    onClick,
    isInView,
  }: CarouselTestimonialSlideProps,
  ref: ForwardedRef<ForwardedTestimonialSlideRef>,
) => {
  const $wrapper = useRef<HTMLDivElement>(null);
  const $portableText = useRef<HTMLDivElement>(null);
  const $glow = useRef<GlowRef>(null);
  const $staggerTextElements = useRef<NodeList>();
  const $image = useRef<HTMLElement>(null);
  const $logo = useRef<HTMLElement | SVGElement>(null);
  // eslint-disable-next-line @typescript-eslint/ban-types
  const progressQuickSet = useRef<Function>();

  useImperativeHandle(
    ref,
    () => ({
      $element: $wrapper,
      setActive: (active: boolean, direction?: number) => {
        // console.log('set active', $wrapper, active);
        if (active) {
          if ($wrapper.current) {
            $wrapper.current.setAttribute('data-active', `${active}`);
            $wrapper.current.ariaHidden = 'false';
          }

          if ($logo.current && $staggerTextElements.current) {
            const tl = gsap.timeline();
            tl.fromTo(
              $image.current,
              {
                '--active-progress': 0 - (direction || -1),
              },
              {
                '--active-progress': 0,
                duration: 0.6,
                ease: EaseType.OUT,
              },
            );
            tl.fromTo(
              [$logo.current, ...$staggerTextElements.current],
              {
                '--active-progress': 0 - (direction || -1),
                opacity: 0,
              },
              {
                '--active-progress': 0,
                opacity: 1,
                duration: 0.6,
                stagger: 0.05,
                ease: EaseType.BASIC_BUTTER,
              },
              '<+0.1',
            );
          }
        } else {
          if ($wrapper.current) {
            $wrapper.current.removeAttribute('data-active');
            $wrapper.current.ariaHidden = 'true';
          }

          if ($logo.current && $staggerTextElements.current) {
            const tl = gsap.timeline();
            tl.to([$logo.current, ...$staggerTextElements.current], {
              opacity: 0,
              duration: 0.6,
              ease: EaseType.BASIC_BUTTER,
            });
          }
        }
      },
      setProgress: (progress: number) => {
        if (progressQuickSet.current) progressQuickSet.current(progress);
      },
    }),
    [],
  );

  useLayoutEffect(() => {
    progressQuickSet.current = gsap.quickSetter(
      $wrapper.current,
      '--scroll-progress',
    );

    $staggerTextElements.current =
      $wrapper.current?.querySelectorAll('.stagger');
  }, []);

  const handleReady = useCallback(
    (element: HTMLImageElement | HTMLVideoElement) => {
      $glow.current?.startGlow(element);
    },
    [],
  );

  const handleClick = () => {
    if (onClick) onClick(slideIndex);
  };

  const overlayClasses = cn(
    styles.slideImageOverlay,
    gradient && styles[gradientClassesMap[gradient]],
    gradientMobile && styles[`${gradientClassesMap[gradientMobile]}Mobile`],
  );

  // Need to memoize this object, otherwise portable text gets re-rendered even with memo
  const memoizedPortableTextOptions = useMemo(() => {
    return {
      block: {
        titles: {
          className: cn(styles.titles, styles.stagger, 'stagger'),
        },
        title4: {
          className: styles.title4,
          tagName: 'h3',
        },
        bodies: {
          className: cn(styles.bodies, styles.stagger, 'stagger'),
        },
        label: {
          className: cn(styles.label, styles.stagger, 'stagger'),
        },
      },
      marks: {
        em: {
          className: styles.em,
        },
      },
    };
  }, []);

  const onReady = useCallback(
    (element: HTMLImageElement | HTMLVideoElement) => handleReady(element),
    [handleReady],
  );

  return (
    <div
      className={cn(styles.slide, isQuoted && styles.quoted, className)}
      ref={$wrapper}
      onClick={handleClick}
      onKeyDown={handleClick}
      role="button"
      tabIndex={0}
    >
      <Glow
        className={styles.glow}
        backgroundClassName={styles.slideGlow}
        ref={$glow}
        type={GlowType.BAKED_CANVAS}
        config={{
          type: 'IMAGE',
          intensity: 0.4,
          scale: 1.1,
          position: GlowPosition.TOP_RIGHT,
        }}
      />
      <Shadow className={styles.slideInner}>
        <Image
          ref={$image}
          className={styles.slideImageWrapper}
          source={image}
          onReady={onReady}
          isDisplayed={isInView}
        />
        <figure className={overlayClasses} />
        {logo && (
          <>
            <Logo
              ref={$logo}
              svg={logo.svg}
              image={
                logo?.image && {
                  source: logo.image,
                }
              }
              className={cn(
                styles.slideLogo,
                logo.svg && styles.slideLogoSvg,
                logo.image && styles.slideLogoImage,
              )}
              name={logo.name}
              isInView={isInView !== false}
            />
            <div className={styles.lineBreak} />
          </>
        )}
        <MemoPortableText
          ref={$portableText}
          isAutoGrid={false}
          animate={false}
          content={text}
          className={styles.slideText}
          options={memoizedPortableTextOptions}
        />
      </Shadow>
    </div>
  );
};

const ForwardedTestimonialSlide = forwardRef(TestimonialSlide);

// TODO: FIGURE THIS THING OUT
// memo() is supposed to prevent useless re-renders from parent components, which was preventing me to set things in portable text DOM (not ideal, but no other choice)
// BUT because of using addToRefArray, it still triggers re-render despite using memo

export default ForwardedTestimonialSlide;
