import {
  PortableTextMarkComponent,
  PortableTextTypeComponent,
} from '@portabletext/react';
import {
  PortableTextBlock,
  PortableTextListItemBlock,
} from '@portabletext/types';
import { ComponentType, MutableRefObject, ReactNode } from 'react';

import { RaggingType } from '~/types/decorations.d';

// PORTABLE TEXT COMPONENT TYPES ---------------------------------------------------------------  /

export type CssStyles = { readonly [key: string]: string } | null;

// common props to all portable text modules and sub-components
export type PortableTextModuleBase = {
  _key?: string;
  ref?: PortableTextObjectRef;
  className?: string;
  moduleStyles?: CssStyles;
};

// primary underlying props for every portable text TYPE component
export type PortableTextCustomComponent = PortableTextCustomContent &
  PortableTextModuleBase;

// content from Sanity to be used as `content` in PortableTextToReact
export type CMSPortableTextCustomContent = PortableTextBlock & {
  button?: PortableTextButtonProps;
  buttons?: PortableTextButtonProps[];
  gridColumnIndex?: number;
  gridColumnChildIndex?: number;
  gridColumnsTotal?: number;
};

// CMS data to populate portable text - used when a module goes through ModularComponent
// this is NOT what portable text wants as the 'content' prop - see CMSPortableTextCustomContent
export type CMSPortableTextProps = {
  _key?: string;
  _type?: string;
  className?: string;
  content: CMSPortableTextCustomContent | CMSPortableTextCustomContent[];
  options?: PropOptions;
};

// fulfilling the `content` prop in PortableTextToReact
export type PortableTextCustomContent = PortableTextBlock & {
  content?: unknown;
  gridColumnIndex?: number;
  gridColumnChildIndex?: number;
  gridColumnsTotal?: number;
};

// props for PortableText.tsx and makeStuff utilities
export type PortableTextProps = PortableTextModuleBase & {
  alignment?: 'left' | 'center' | 'centerAll';
  animate?: TextAnimations | false;
  className?: string;
  content: PortableTextCustomContent | PortableTextCustomContent[];
  isAutoGrid?: boolean;
  isAutoRag?: boolean;
  isAutoStyles?: boolean;
  options?: PropOptions;
  ragging?: RaggingType;
  customTag?: string;
};

// PORTABLE TEXT CONFIG TYPES --------------------------------------------------------------------  /

// options are composed of PropOptions
export enum BlockType {
  block = 'block',
  list = 'list',
  marks = 'marks',
}

export enum TextAnimations {
  LIGHT = 'light',
  FULL = 'full',
}

export type PropOptions = Record<string, PropOptionNested | PropOptionObject>;

export type PropOptionNested = Record<
  string,
  Record<string, BlockElementProps>
>;
export type PropOptionObject = Record<string, BlockElementProps>;

// props for elements used within portable text
export type PortableTextObjectRef = MutableRefObject<T[]>;

export type BlockElementProps = PortableTextModuleBase & {
  tagName?: string;
  group?: string;
  ref?: PortableTextObjectRef;
  render?: ComponentType<JsxRenderProps>;
};

export type BlockRenderElement =
  | PortableTextCustomComponent<JsxRenderProps>
  | PortableTextCustomComponent<BlockTemplateConfigProps>;

// Custom record types

export type MarkRenderElement = PortableTextCustomComponent<JsxRenderProps>;

// Individual record types

export type BlockRecord =
  | Record<string, BlockRenderElement>
  | BlockRenderElement;

export type MarksRecord = Record<string, MarkRenderElement>;

export type ListRecord =
  | Record<string, BlockRenderElement>
  | BlockRenderElement;

export type ListItemRecord =
  | Record<string, BlockRenderElement>
  | BlockRenderElement;

export type TypesRecord = Record<string, PortableTextTypeComponent>;

// PortableTextConfig is based on @portabletext/types/PortableTextReactComponents
// the only reason we aren't using PortableTextReactComponents is because all its props are required

export type PortableTextConfig = {
  block?: BlockRecord;
  hardBreak?: ComponentType<string, unknown> | false;
  list?: ListRecord;
  listItem?: ListItemRecord;
  marks?: MarksRecord;
  types?: TypesRecord;
  unknownBlockStyle?: PortableTextComponent<PortableTextBlock>;
  unknownList?: PortableTextComponent<ReactPortableTextList>;
  unknownListItem?: PortableTextComponent<PortableTextListItemBlock>;
  unknownMark?: PortableTextMarkComponent;
  unknownType?: PortableTextComponent<UnknownNodeType>;
};

// PortableTextValueProps represents all possible props passed into 'value' in makeBlock

type PortableTextValueProps = {
  _key?: string;
  _type?: string;
  email?: string;
  href?: string;
  blank?: boolean;
};

// JsxRenderProps represent the props used in the custom `render` prop

export type JsxRenderProps = {
  children: ReactNode;
  className?: string;
  markType?: string;
  text?: string;
  value?: PortableTextValueProps;
};

// JsxPortableTextProps are the props used in the JSX return to build each React component element

export type JsxPortableTextProps = {
  blockName: string;
  children: ReactNode;
  className?: string;
  markType?: string;
  node?: PortableTextValueProps;
  tagName?: string;
  text?: string;
  value?: PortableTextValueProps;
};

// BlockValueProps is used for the data passed into <Block> components

export type BlockComponentProps = {
  _key: string;
  blockGroupName?: string;
  blockName: string;
  children: ReactNode;
  className?: string;
  module: CMSPortableTextProps;
  tagName?: string;
};

// BlockTemplateConfigProps is used to pass configuration props from makeBlock() to <Block>

export type BlockTemplateConfigProps = {
  _key: string;
  blockGroupName?: string;
  blockName: string;
  className?: string;
  module: CMSPortableTextProps;
  render?: ComponentType<JsxRenderProps>;
  tagName?: string;
};
