import { useRef } from 'react';

import PortableText from '~/components/molecules/portableText/PortableText';
import { RaggingType } from '~/types/decorations.d';
import { cn } from '~/utils';

import { ContentProps } from '../types';
import styles from './styles.module.css';

const TextWithComponents = ({
  className,
  content,
  alignment,
  textAnimationType,
  ragging = RaggingType.DEFAULT,
}: ContentProps) => {
  const $buttons = useRef([] as HTMLDivElement[]);
  const $forms = useRef([] as HTMLDivElement[]);
  const $spacers = useRef([] as HTMLDivElement[]);
  const $stats = useRef([] as HTMLDivElement[]);
  const $touts = useRef([] as HTMLDivElement[]);

  return (
    <PortableText
      alignment={alignment}
      className={cn(
        styles.content,
        className,
        alignment === 'center' && styles.alignCenter,
      )}
      animate={textAnimationType}
      content={content}
      ragging={ragging}
      options={{
        block: {
          title0: {
            className: styles.title0,
          },
          title5: {
            tagName: 'h3',
          },
          eyebrow: {
            className: styles.eyebrow,
          },
          accents: {
            className: styles.accents,
          },
          titles: {
            className: styles.titles,
          },
          bodies: {
            className: styles.bodies,
          },
        },
        list: {
          bullet: {
            className: styles.list,
          },
          number: {
            className: styles.list,
          },
        },
        types: {
          'block.buttons': {
            className: styles.button,
            ref: $buttons,
          },
          'block.driftButton': {
            className: styles.button,
            ref: $buttons,
          },
          'block.form': {
            className: styles.forms,
            ref: $forms,
          },
          'block.touts': {
            ref: $touts,
          },
          'block.spacer': {
            className: styles.spacers,
            ref: $spacers,
          },
          'block.stats': {
            ref: $stats,
          },
        },
      }}
    />
  );
};

export default TextWithComponents;
