import { gsap } from 'gsap';
import {
  ForwardedRef,
  forwardRef,
  useCallback,
  useEffect,
  useImperativeHandle,
  useRef,
  useState,
} from 'react';

import ButtonExpand from '~/components/atoms/Buttons/UI/ButtonExpand';
import Image from '~/components/atoms/image';
import Glow from '~/components/molecules/glow';
import { GlowPosition, GlowRef } from '~/components/molecules/glow/types.d';
import PortableText from '~/components/molecules/portableText';
import Shadow from '~/components/molecules/shadow';
import { dict } from '~/data/stores/Dictionary';
import { cn } from '~/utils';

import SvgOrImage from '../svgOrImage';
import styles from './styles.module.css';
import { HoverCardProps, HoverCardRef } from './types';

const HoverCard = (
  {
    _key,
    className,
    content,
    eyebrow = '',
    image,
    logo,
    squaredAspectRatio = false,
    title,
    isInView,
  }: HoverCardProps,
  ref: ForwardedRef<HoverCardRef>,
) => {
  const $card = useRef<HTMLDivElement>(null);
  const $glow = useRef<GlowRef>(null);
  const textHeightSetter = useRef<(value: number) => void>();
  const $bodyWrapper = useRef<HTMLParagraphElement>(null);
  const [isOpen, setIsOpen] = useState(false);

  const renderClass = cn(
    styles.hoverCard,
    className,
    isOpen && styles.active,
    logo && cn(styles.hasGradientCategory, styles.hasBorderCategory),
    !logo && cn(styles.hasGradientAsymmetrical, styles.hasBorderAsymmetrical),
    squaredAspectRatio ? styles.squaredAspectRatio : styles.categoryAspectRatio,
  );

  useImperativeHandle(
    ref,
    () => {
      return {
        collapseDetails: () => setIsOpen(false),
        $card,
      };
    },
    [],
  );

  useEffect(() => {
    if ($card.current) {
      textHeightSetter.current = gsap.quickSetter(
        $card.current,
        '--translate-offset',
        'px',
      ) as (value: number) => void;
    }
  }, []);

  useEffect(() => {
    if (textHeightSetter.current) {
      textHeightSetter.current(
        isOpen ? 0 - ($bodyWrapper.current?.offsetHeight ?? 0) : 0,
      );
    }
  }, [isOpen]);

  const glowColor = image && image.asset.dominantBackground;
  const handleReady = useCallback(
    ($element: HTMLImageElement | HTMLVideoElement) => {
      // on element loaded, start glow and pass element
      if ($glow && $glow.current) $glow.current.startGlow($element);
    },
    [],
  );

  return (
    <div ref={$card} className={renderClass}>
      {glowColor && (
        <Glow
          className={styles.glow}
          ref={$glow}
          config={{
            type: 'COLOR',
            color: glowColor,
            position: GlowPosition.BOTTOM_LEFT,
            intensity: 0.8,
            scale: 1.1,
          }}
        />
      )}
      <Shadow className={styles.shadowContainer}>
        <div className={styles.hoverCardInner}>
          <button
            className={styles.buttonOverlay}
            onClick={() => {
              setIsOpen(!isOpen);
            }}
            aria-label={isOpen ? dict('clickToClose') : dict('clickToOpen')}
            aria-controls={`${_key}-text`}
            aria-expanded={isOpen}
            key={_key}
          ></button>
          {eyebrow && <div className={styles.eyebrow}>{eyebrow}</div>}
          <div className={styles.textContainer} id={`${_key}-text`}>
            <div className={styles.titleWrapper}>
              {logo && <SvgOrImage {...logo} className={styles.logo} />}
              <h4
                className={cn(
                  styles.title,
                  logo ? styles.titleWithLogo : styles.titleWithoutLogo,
                )}
              >
                {title.split('\n').map((titleText) => (
                  <>
                    {titleText}
                    <br />
                  </>
                ))}
              </h4>
            </div>
            <div className={styles.bodyWrapper} ref={$bodyWrapper}>
              <PortableText
                aria-hidden={!isOpen}
                content={content}
                className={styles.body}
                isAutoGrid={false}
                isAutoStyles={false}
                options={{
                  block: {
                    bodies: {
                      className: styles.bodies,
                    },
                    accents: {
                      className: styles.accents,
                    },
                    titles: {
                      className: styles.titles,
                    },
                  },
                  marks: {
                    link: {
                      className: styles.link,
                    },
                    internalLink: {
                      className: styles.link,
                    },
                    externalLink: {
                      className: styles.link,
                    },
                  },
                }}
              />
            </div>
          </div>
          <Image
            source={image}
            className={styles.imageContainer}
            onReady={handleReady}
            isDisplayed={isInView}
          />
          <ButtonExpand
            className={styles.button}
            size="small"
            tag="div"
            open={isOpen}
          />
        </div>
      </Shadow>
    </div>
  );
};

export default forwardRef<HoverCardRef, HoverCardProps>(HoverCard);
