import { gsap } from 'gsap';
import { useCallback, useEffect, useRef, useState } from 'react';

import SvgDrawerIcon from '~/assets/svg/drawer.svg';
import useUIStore from '~/state/ui';
import { cn } from '~/utils';

import styles from './styles.module.css';
import { AccordionItemProps } from './types';

const AccordionItem = ({
  iconSize = 'large',
  header,
  children,
  className,
  classNameHeader,
  classNameContent,
  onToggle,
}: AccordionItemProps) => {
  const outerRef = useRef<HTMLDivElement>(null);
  const windowWidth = useUIStore((state) => state.windowWidth);
  const isOpen = useRef(false);
  const [open, setOpen] = useState(false);

  const onClick = useCallback(() => {
    setOpen(!open);
    onToggle && onToggle(!open);
  }, [open, onToggle]);

  useEffect(() => {
    if (outerRef.current) {
      gsap.set(outerRef.current, {
        height: open ? `${outerRef.current.scrollHeight}px` : 0,
      });
      isOpen.current = open;
    }
  }, [open]);

  useEffect(() => {
    if (isOpen.current && outerRef.current) {
      outerRef.current.style.height = 'auto';
      gsap.set(outerRef.current, {
        height: isOpen.current ? `${outerRef.current.scrollHeight}px` : 0,
      });
    }
  }, [windowWidth]);

  return (
    <div className={cn(styles.accordionItem, className, open && styles.isOpen)}>
      <button className={cn(styles.header, classNameHeader)} onClick={onClick}>
        {header}
        <SvgDrawerIcon
          className={cn(
            styles.accordionIcon,
            iconSize === 'small' && styles.small,
          )}
        />
      </button>
      <div ref={outerRef} className={cn(styles.content, classNameContent)}>
        {children}
      </div>
    </div>
  );
};

export default AccordionItem;
