/**
 * Base wrapper for the cta button components. Handles shared functionality and applies variant and color scheme options
 * @param buttonVariant The variant of the cta, selected in the CMS, defaults to "pill"
 * @param buttonColorScheme The cta's color scheme, only applies to pill variant, selected in the CMS
 * @param to The cta's link, determines whether component will render as <a> or <button>, set in the CMS
 * @param disabled If the cta is disabled
 * @param onClick Callback function for when the button is clicked
 * @param customTrackingEvent Custom event for tracking, set in the CMS
 * @param asNonInteractive Renders a <span> instead of a button or anchor tag. Used in cases where the interactive element is the parent of the cta
 * @param className
 * @example <ButtonBase> {children} </ButtonBase>
 */

import {
  ForwardedRef,
  forwardRef,
  useCallback,
  useImperativeHandle,
  useRef,
} from 'react';

import getBaseTrackingData from '~/analytics/getBaseTrackingData';
import ButtonIcon from '~/components/atoms/Buttons/Ctas/ButtonIcon';
import Link from '~/components/atoms/link';
import { cn, getNodeText } from '~/utils';

import styles from './ButtonBase.styles.module.css';
import { BaseButtonProps, ForwardedButtonRef } from './ButtonBase.types.d';

const ButtonBase = (
  props: BaseButtonProps,
  ref: ForwardedRef<ForwardedButtonRef>,
) => {
  const {
    buttonVariant = 'pill',
    buttonColorScheme,
    to,
    disabled,
    onClick,
    customTrackingEvent,
    asNonInteractive,
    className,
    children,
  } = props;

  const $button = useRef(null);
  const $icon = useRef(null);

  useImperativeHandle(
    ref,
    () => ({
      $button,
      $icon,
    }),
    [],
  );

  const renderClasses = cn(
    buttonVariant === 'pill' && buttonColorScheme
      ? styles[buttonColorScheme]
      : styles.textLink,
    to?.type === 'phoneNumber' && styles.phoneNumber,
    className,
    disabled && styles.disabled,
  );

  const trackedOnClick = useCallback(() => {
    if (window.analytics) {
      const label = getNodeText(children);
      window.analytics.track('button-clicked', {
        title: label,
        ...getBaseTrackingData(),
      });
      if (customTrackingEvent) {
        window.analytics.track(customTrackingEvent, {
          title: label,
          ...getBaseTrackingData(),
        });
      }
    }
    if (onClick) {
      onClick();
    }
  }, [onClick, children, customTrackingEvent]);

  const RenderTag = asNonInteractive
    ? 'span'
    : typeof to !== 'undefined' && to.type && (to.hash || to.url)
    ? Link
    : 'button';

  return (
    <RenderTag
      className={renderClasses}
      onClick={trackedOnClick}
      to={to}
      ref={$button}
    >
      {children}
      <ButtonIcon to={to} buttonVariant={buttonVariant} ref={$icon} />
    </RenderTag>
  );
};

export default forwardRef<ForwardedButtonRef, BaseButtonProps>(ButtonBase);
