import React, { ForwardedRef, forwardRef } from 'react';

import Image from '~/components/atoms/image';
import Svg from '~/components/atoms/svg';

import { SvgOrImageProps } from './types';

const SvgOrImage = (
  {
    alt,
    className,
    contain,
    cover,
    image,
    sizes,
    svg,
    isInView,
  }: SvgOrImageProps,
  ref: ForwardedRef<HTMLElement> | null,
) => {
  if (svg) {
    return (
      <Svg
        {...svg}
        alt={alt || svg.alt || image?.alt || ''}
        className={className}
        ref={ref as ForwardedRef<SVGElement>}
      />
    );
  }

  if (image) {
    let fit: { cover: true } | { contain: true } | Record<string, never> = {};

    if (contain) {
      fit = { contain: true };
    } else if (cover) {
      fit = { cover: true };
    }

    // override image alt if it's specified in SvgOrImage
    image.alt = alt || image.alt;

    return (
      <Image
        source={image}
        sizes={sizes}
        className={className}
        {...fit}
        ref={ref as ForwardedRef<HTMLElement>}
        isDisplayed={isInView}
      />
    );
  }

  return null;
};

export default forwardRef(SvgOrImage);
