import {
  ForwardedRef,
  forwardRef,
  Fragment,
  useCallback,
  useImperativeHandle,
  useRef,
} from 'react';

import { VideoRef } from '~/components/atoms/video/types';
import Media from '~/components/molecules/media';
import Shadow from '~/components/molecules/shadow';

import Glow from '../glow/Glow';
import { GlowRef, GlowType } from '../glow/types.d';
import { GlowMediaProps, GlowMediaRef } from './types.d';

const GlowMedia = (props: GlowMediaProps, ref: ForwardedRef<GlowMediaRef>) => {
  const {
    onReady,
    glowBackgroundClassName,
    className,
    children,
    aspectRatio,
    glowConfig,
    elementClassName,
    containerClassName,
    style,
  } = props;
  const $glow = useRef<GlowRef>(null);
  const $media = useRef<HTMLElement | VideoRef>(null);

  const handleReady = useCallback(
    ($element: HTMLImageElement | HTMLVideoElement) => {
      // on element loaded, start glow and pass element
      if ($glow && $glow.current) {
        $glow.current.startGlow($element);
      }
      if (onReady) {
        onReady($element);
      }
    },
    [onReady],
  );

  useImperativeHandle(
    ref,
    () => ({
      $glow: $glow,
      $wrapper: $glow.current?.$wrapper,
      $media,
    }),
    [],
  );
  const Wrapper = props.hasShadow ? Shadow : Fragment;

  const Container = props.containerClassName ? 'div' : Fragment;

  const glowType = GlowType.BAKED_CANVAS;

  return (
    <Glow
      ref={$glow}
      type={glowType}
      style={style}
      backgroundClassName={glowBackgroundClassName}
      className={className}
      config={{
        type: 'IMAGE',
        ...glowConfig,
      }}
      hasMedia
    >
      <Wrapper {...props.shadowConfig}>
        <>
          {children}
          <Container
            {...(containerClassName ? { className: containerClassName } : {})}
          >
            <Media
              {...props}
              ref={$media}
              className={elementClassName}
              aspectRatio={aspectRatio}
              onReady={handleReady}
            />
          </Container>
        </>
      </Wrapper>
    </Glow>
  );
};

export default forwardRef(GlowMedia);
