/* eslint-disable react-hooks/rules-of-hooks */
import { gsap } from 'gsap';
import Link from 'next/link';
import { useRouter } from 'next/router';
import { useCallback, useEffect, useRef, useState } from 'react';
import shallow from 'zustand/shallow';

import SvgFrameLogo from '~/assets/svg/logo-frame.svg';
import AdobeLogo from '~/components/atoms/adobeLogo';
import Button from '~/components/atoms/Buttons/Ctas/Button';
import AnnouncementBanner from '~/components/molecules/announcementBanner';
import NavGroup from '~/components/organisms/navigation/navGroup';
import NavItem from '~/components/organisms/navigation/navItem';
import NavMobile from '~/components/organisms/navigation/navMobile';
import useUIStore from '~/state/ui';
import { cn, killTimeline } from '~/utils';
import ScrollPosition from '~/utils/domEvents/ScrollPosition';
import { getBreakpointFromLabel } from '~/utils/getBreakpoint';
import isBreakpointOrGreater from '~/utils/isBreakpointOrGreater';
import isPortableTextContentEmpty from '~/utils/isPortableTextContentEmpty';

import { dismissBannerAnimation } from './animations';
import useNavState from './navState';
import styles from './styles.module.css';
import { CMSModuleNavigationProps, CMSNavigationNavItemProps } from './types';

const DELAY = 250;
const RESET_ACTIVE_DELAY = 500;

const getElBounds = ($el: HTMLDivElement | null) => {
  return $el?.getBoundingClientRect();
};

const Navigation = (props: CMSModuleNavigationProps) => {
  const router = useRouter();

  const { globalData, navigation, hideAnnouncementBanner } = props;
  if (globalData) {
    const { announcementBanner } = globalData;
    const bannerAnimation = useRef<GSAPTimeline>();

    const $el = useRef<HTMLDivElement>(null);

    const $mainNavEl = useRef<HTMLDivElement>(null);
    const $subNavEl = useRef<HTMLDivElement>(null);
    const $bannerEl = useRef<HTMLDivElement>(null);
    const $backdrop = useRef<HTMLDivElement>(null);

    const mdBreakpoint = getBreakpointFromLabel('md')?.min;

    const hideSubNavTimeout = useRef<ReturnType<typeof setTimeout>>();
    const resetActiveItemHighlight = useRef<ReturnType<typeof setTimeout>>();

    const [disableMainNavItemActive, setDisableMainNavItemActive] =
      useState(false);

    const breakpoint = useUIStore((state) => state.breakpoint);
    const isNavigationOpaque = useUIStore((state) => state.isNavigationOpaque);
    const setNavigationHeight = useUIStore(
      (state) => state.setNavigationHeight,
    );
    const windowWidth = useUIStore((state) => state.windowWidth);
    const windowHeight = useUIStore((state) => state.windowHeight);
    const isNavigationDisabled = useUIStore(
      (state) => state.isNavigationDisabled,
    );
    const setIsDesktopNavigationShowing = useUIStore(
      (state) => state.setIsDesktopNavigationShowing,
    );
    const bannerDismissed = useUIStore((state) => state.bannerDismissed);
    const setBannerDismissed = useUIStore((state) => state.setBannerDismissed);
    const setBannerHeight = useUIStore((state) => state.setBannerHeight);

    const breakpointRef = useRef(useUIStore((state) => state.breakpoint));

    const mainNavHeightRef = useRef(0);
    const subNavHeightRef = useRef(0);
    const bannerHeightRef = useRef(0);
    const mainNavTriggerStartRef = useRef(0);
    const mainNavTriggerEndRef = useRef(0);
    const mainNavBannerDistanceRef = useRef(0);
    const subNavTriggerStartRef = useRef(0);
    const subNavMainNavDistanceRef = useRef(0);
    const bannerTriggerStartRef = useRef(0);
    const bannerTriggerEndRef = useRef(0);

    const navOffsetRef = useRef(0);

    // how many pixels scrolling back up to trigger nav to reappear
    const maxScrollUpDiff = useRef(0);

    const hasBlur = useRef(true);

    const [
      currentGroup,
      setCurrentGroup,
      whichSubNavShowing,
      setWhichSubNavShowing,
      isMobileNav,
      setIsMobileNav,
    ] = useNavState(
      (state) => [
        state.currentGroup,
        state.setCurrentGroup,
        state.whichSubNavShowing,
        state.setWhichSubNavShowing,
        state.isMobileNav,
        state.setIsMobileNav,
      ],
      shallow,
    );

    useEffect(() => {
      breakpointRef.current = breakpoint;
    }, [breakpoint]);

    useEffect(() => {
      // TODO: using window.innerWidth until the ui store's window width matches window innerwidth instead of document.body width
      // fixes issue with desktop subnav dropdown not working between 1240 and 1255
      if (mdBreakpoint && window.innerWidth > mdBreakpoint) {
        setIsMobileNav(false);
      } else {
        setIsMobileNav(true);
      }
    }, [windowWidth, mdBreakpoint, setIsMobileNav]);

    // on load and route change figure out if we are currently on a subnav page
    useEffect(() => {
      setCurrentGroup(null);

      navigation.primaryLinks.forEach((item: CMSNavigationNavItemProps) => {
        if (item._type === 'navGroup') {
          const currentPath = router.asPath.split('/')[1];
          const isCurrentGroup = item.groupItemLinks.includes(currentPath);
          if (isCurrentGroup) {
            if (item._key) {
              setCurrentGroup(item._key);
            }
          }
        }
      });
    }, [navigation.primaryLinks, router.asPath, setCurrentGroup]);

    useEffect(() => {
      setWhichSubNavShowing(currentGroup);
    }, [currentGroup, setWhichSubNavShowing]);

    function clearDelay() {
      if (hideSubNavTimeout.current) {
        clearTimeout(hideSubNavTimeout.current);
      }
    }

    function clearActiveStateDelay() {
      if (resetActiveItemHighlight.current) {
        clearTimeout(resetActiveItemHighlight.current);
      }
    }

    const removeBlur = useCallback(() => {
      hasBlur.current = false;
      gsap.set($el.current, {
        '--nav-text-color': 'var(--nav-text-color-light)',
        '--nav-bg': 'var(--nav-gradient-when-unblurred)',
        '--nav-filter': 'none',
      });
    }, []);

    const resetBlur = useCallback(() => {
      hasBlur.current = true;
      gsap.set($el.current, {
        '--nav-text-color': 'var(--nav-text-color-dark)',
        '--nav-bg': 'var(--nav-gradient-when-blurred)',
        '--nav-filter': 'saturate(var(--nav-saturate)) blur(var(--nav-blur))',
      });
    }, []);

    useEffect(() => {
      // clear time of showing subnav when route changes
      clearDelay();

      // clear time of resetting link active state highlight
      clearActiveStateDelay();

      resetBlur();
    }, [router.asPath, resetBlur]);

    const showDesktopSubNav = useCallback(
      (id: string) => {
        if (isMobileNav) return;
        clearDelay();
        setWhichSubNavShowing(id);
      },
      [setWhichSubNavShowing, isMobileNav],
    );

    const hideDesktopSubNav = useCallback(() => {
      if (isMobileNav) return;

      hideSubNavTimeout.current = setTimeout(() => {
        setWhichSubNavShowing(currentGroup || null);
      }, DELAY);
    }, [setWhichSubNavShowing, currentGroup, isMobileNav]);

    const onMouseOverMainNavItem = () => {
      if (breakpoint && !isBreakpointOrGreater(breakpoint, 'md')) return;
      clearActiveStateDelay();
      setDisableMainNavItemActive(true);
    };

    const onMouseOutMainNavItem = () => {
      if (breakpoint && !isBreakpointOrGreater(breakpoint, 'md')) return;
      resetActiveItemHighlight.current = setTimeout(() => {
        setDisableMainNavItemActive(false);
      }, RESET_ACTIVE_DELAY);
    };

    const onBannerDismissed = useCallback(() => {
      setBannerDismissed(true);
      if (bannerAnimation.current) killTimeline(bannerAnimation.current);
    }, [setBannerDismissed]);

    useEffect(() => {
      gsap.ticker.add(
        () => {
          mainNavHeightRef.current =
            getElBounds($mainNavEl.current)?.height || 0;
          subNavHeightRef.current = getElBounds($subNavEl.current)?.height || 0;
          bannerHeightRef.current = getElBounds($bannerEl.current)?.height || 0;

          setNavigationHeight(mainNavHeightRef.current);

          const wh = windowHeight || 0;

          bannerTriggerStartRef.current = wh / 4;
          bannerTriggerEndRef.current =
            bannerTriggerStartRef.current + bannerHeightRef.current;

          mainNavTriggerStartRef.current = wh;
          mainNavTriggerEndRef.current = wh + mainNavHeightRef.current;
          mainNavBannerDistanceRef.current = mainNavTriggerStartRef.current;

          subNavTriggerStartRef.current =
            wh * 2 + mainNavTriggerEndRef.current - subNavHeightRef.current;
          subNavMainNavDistanceRef.current =
            subNavTriggerStartRef.current - mainNavTriggerEndRef.current;
          maxScrollUpDiff.current = wh / 3;

          if (bannerHeightRef.current)
            bannerAnimation.current = dismissBannerAnimation({
              $el,
              $bannerEl,
              bannerHeight: bannerHeightRef.current,
              onComplete: onBannerDismissed,
            });

          gsap.set($el.current, {
            '--banner-height': `${bannerHeightRef.current}px`,
          });

          setBannerHeight(bannerHeightRef.current);
        },

        true,
        true,
      );
    }, [
      windowWidth,
      windowHeight,
      bannerDismissed,
      onBannerDismissed,
      hideAnnouncementBanner,
      setNavigationHeight,
      setBannerHeight,
    ]);

    function isPastMainNav() {
      const scrollY = ScrollPosition.y || 0;
      return scrollY > mainNavTriggerEndRef.current;
    }

    function isPastSubNav() {
      const scrollY = ScrollPosition.y || 0;
      return scrollY > subNavTriggerStartRef.current + subNavHeightRef.current;
    }

    // subscribe to scroll to remove nav blur
    useEffect(() => {
      const unsubscribe = ScrollPosition.subscribe(() => {
        if (!hasBlur.current) return;
        if (whichSubNavShowing !== null && isPastSubNav()) {
          removeBlur();
        } else if (!whichSubNavShowing && isPastMainNav()) {
          removeBlur();
        }
      });

      return () => {
        unsubscribe();
      };
    }, [removeBlur, whichSubNavShowing]);

    useEffect(() => {
      let prevNavOffset = 0;
      let isFirstTick = true;
      let scrollDownChangeY = 0;
      let prevScrollDir = 0;
      let prevScrollY = 0;
      let animationDir = 0;

      const offsetValueUpdate = () => {
        if (
          typeof window === 'undefined' ||
          !breakpointRef.current ||
          !$el.current
        ) {
          return null;
        }

        const scrollY = ScrollPosition.y || 0;
        const scrollDir = ScrollPosition.direction || 0;

        if (scrollDir !== prevScrollDir) {
          prevScrollDir = scrollDir;
          prevScrollY = scrollY;
        }

        // start logging scroll after passing first trigger point
        const offsetScroll = Math.max(
          0,
          scrollY - bannerTriggerStartRef.current,
        );

        if (scrollDir == -1) {
          // on scroll up the nav will reappear, without banner, until the top is reached again
          const offset = offsetScroll > 0 ? -bannerHeightRef.current : 0;
          animationDir = 1;
          setNavOffset(offset);
        } else {
          // if scrolling past where main nav is no longer visible

          if (isPastSubNav()) {
            scrollDownChangeY = scrollY - prevScrollY;

            // if nav has reappeared (from scroll up), make it disappear again only after user has scrolled down a certain amount
            if (scrollDownChangeY > maxScrollUpDiff.current) {
              animationDir = -1;
              setNavOffset(
                -(
                  bannerHeightRef.current +
                  mainNavHeightRef.current +
                  subNavHeightRef.current
                ),
                20,
              );
            }
          } else {
            // max offset after passing the banner
            const offsetBanner =
              -1 * gsap.utils.clamp(0, bannerHeightRef.current, offsetScroll);

            animationDir = -1;
            let offset = offsetBanner;

            // how many pixels past the top nav trigger end
            const pixelsPastBanner = scrollY - bannerTriggerEndRef.current;
            const pixelsPastBannerTriggerEnd = Math.min(
              mainNavHeightRef.current,
              Math.max(0, pixelsPastBanner - mainNavBannerDistanceRef.current),
            );

            offset = offset - pixelsPastBannerTriggerEnd;

            const pixelsPastMainNavTriggerEnd =
              scrollY - mainNavTriggerEndRef.current;

            // how many pixels past the sub nav trigger start, cap at max subnav offset (subnav height)
            const pixelsPastSubNavTriggerEnd = Math.min(
              subNavHeightRef.current,
              Math.max(
                0,
                pixelsPastMainNavTriggerEnd - subNavMainNavDistanceRef.current,
              ),
            );

            offset = offset - pixelsPastSubNavTriggerEnd;

            setNavOffset(offset);
          }
        }

        if (navOffsetRef.current !== prevNavOffset || isFirstTick) {
          // since there is a change in nav offset, permit rendering this frame
          isFirstTick = false;
          prevNavOffset = navOffsetRef.current;
          return true;
        }

        return false;
      };

      const setNavOffset = (offset: number, ease = 10) => {
        const scrollDir = ScrollPosition.direction || 0;
        navOffsetRef.current =
          scrollDir === -1
            ? Math.min(offset, (navOffsetRef.current += ease))
            : Math.max(offset, (navOffsetRef.current -= ease));
      };

      const render = () => {
        if ($el.current) {
          if ($el.current) {
            if (animationDir === 1) {
              setIsDesktopNavigationShowing(true);
            } else if (animationDir === -1) {
              setIsDesktopNavigationShowing(false);
            }

            gsap.set($el.current, {
              y: navOffsetRef.current,
            });
          }
        }
      };

      const tick = () => {
        if (offsetValueUpdate()) render();
      };

      gsap.ticker.add(tick);

      return () => {
        gsap.ticker.remove(tick);
      };
    }, [setIsDesktopNavigationShowing]);

    const dismissBanner = () => {
      if ($bannerEl.current) bannerAnimation.current?.play();
    };

    if (isNavigationDisabled) return null;

    const renderNavItem = (button: CMSNavigationNavItemProps) => {
      const { _type, _key } = button;
      switch (_type) {
        case 'link':
          return (
            <li key={_key}>
              <NavItem
                link={button}
                onMouseOver={onMouseOverMainNavItem}
                onMouseOut={onMouseOutMainNavItem}
                onFocus={onMouseOverMainNavItem}
                onBlur={onMouseOutMainNavItem}
                disableActiveState={
                  disableMainNavItemActive || whichSubNavShowing !== null
                }
              />
            </li>
          );
        case 'button':
          const { to } = button;

          return (
            <li key={_key}>
              <Button
                to={to}
                className={styles.navigationButton}
                buttonVariant="pill"
                buttonColorScheme="solidLoud"
              >
                {to.label}
              </Button>
            </li>
          );
        case 'navGroup':
          return (
            <li key={_key}>
              <NavGroup
                id={_key || ''}
                navGroup={button}
                ref={$subNavEl}
                onMouseOver={(id) => showDesktopSubNav(id)}
                onMouseOut={() => hideDesktopSubNav()}
                onFocus={(id) => showDesktopSubNav(id)}
                onBlur={() => hideDesktopSubNav()}
                onMouseOverMainNavItem={onMouseOverMainNavItem}
                onMouseOutMainNavItem={onMouseOutMainNavItem}
                onFocusMainNavItem={onMouseOverMainNavItem}
                onBlurMainNavItem={onMouseOutMainNavItem}
                disableHeaderActiveState={disableMainNavItemActive}
              />
            </li>
          );
        default:
          return null;
      }
    };

    return (
      <header
        ref={$el}
        className={styles.navigation}
        aria-label={navigation.title}
      >
        {announcementBanner &&
          !isPortableTextContentEmpty(announcementBanner.content) &&
          !bannerDismissed &&
          !hideAnnouncementBanner && (
            <AnnouncementBanner
              ref={$bannerEl}
              content={announcementBanner?.content}
              onClose={dismissBanner}
              className={styles.navigationBannerEl}
            />
          )}
        <nav
          className={cn(
            styles.mainNavigation,
            whichSubNavShowing !== null && styles.subNavOpen,
            isNavigationOpaque && styles.isNavigationOpaque,
          )}
          ref={$mainNavEl}
        >
          <div className={styles.navigationInner}>
            <div className={styles.left}>
              <Link href="/">
                <a className={styles.logoLeft}>
                  <SvgFrameLogo title="Frame.io Logo" className={styles.logo} />
                </a>
              </Link>
            </div>

            <NavMobile bottomLinks={navigation.mobileNavBottomLinks}>
              {navigation.primaryLinks.map(renderNavItem)}
            </NavMobile>

            <ul className={styles.desktopNavigation}>
              {navigation.primaryLinks.map(renderNavItem)}
            </ul>

            <ul className={styles.right}>
              {navigation.rightLinks.map(renderNavItem)}
              <li>
                <AdobeLogo />
              </li>
            </ul>
          </div>
          <figure
            ref={$backdrop}
            className={cn(styles.backdrop, !hasBlur && styles.noBlur)}
          />
        </nav>
      </header>
    );
  }
  return null;
};

export default Navigation;
