import { CSSProperties, useRef } from 'react';

import Image from '~/components/atoms/image';
import Svg from '~/components/atoms/svg/Svg';
import Glow from '~/components/molecules/glow';
import { GlowPosition } from '~/components/molecules/glow/types.d';
import GlowMedia from '~/components/molecules/glowMedia';
import PortableText from '~/components/molecules/portableText';
import Shadow from '~/components/molecules/shadow';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { GradientOpacity, RaggingType } from '~/types/decorations.d';
import { cn, getSpacersStyles } from '~/utils';
import gradientClassesMap from '~/utils/maps/gradientClasses';

import styles from './styles.module.css';
import {
  CMSModuleSpeedBump,
  LayoutAlignment,
  LayoutVerticalAlignment,
} from './types.d';

// TODO: split speedbump into separate types: card, full-bleed, case study interstitial, case study teaser

const SpeedBump = ({
  background,
  className,
  content,
  gradient = GradientOpacity.HEAVY,
  gradientMobile = GradientOpacity.SUPER_HEAVY,
  hasSpacers,
  image,
  accentColor,
  garageDoorRevealParent,
  isAlternativeContainedLayout = false,
  isBackgroundFullBleed = false,
  isCardIndented = true,
  isCardOnMobile = false,
  isContentIndented = true,
  isMinimumPhotoHeight = false,
  isQuoted = false,
  layout = LayoutAlignment.LOGO_LEFT_TEXT_RIGHT,
  layoutFullBleedVerticalAlignment = LayoutVerticalAlignment.CENTER,
  logo,
  ragging = RaggingType.DEFAULT,
  slug,
}: CMSModuleSpeedBump) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  let renderClasses = cn(
    styles.speedbump,
    background && styles.hasBackground,
    hasSpacers?.before && decorationsStyles.hasSpacerBefore,
    hasSpacers?.after && decorationsStyles.hasSpacerAfter,
    garageDoorRevealParent && styles.isRevealedByGarageDoor,
    ragging === RaggingType.TIGHT && styles.ragTight,
    ragging === RaggingType.WIDE && styles.ragWide,
    className,
  );

  let backgroundContainerStyle = cn(
    styles.backgroundWrapper,
    gradient && styles[gradientClassesMap[gradient]],
    gradientMobile && styles[`${gradientClassesMap[gradientMobile]}Mobile`],
  );

  if (isBackgroundFullBleed) {
    // container classes which are only valid for full-bleed layout
    renderClasses = cn(
      renderClasses,
      isCardOnMobile && styles.isCardOnMobile,
      styles.isBackgroundFullBleed,
      styles[layout],
      styles[layoutFullBleedVerticalAlignment],
    );

    // image container styling
    backgroundContainerStyle = cn(
      backgroundContainerStyle,
      styles.backgroundImageFullBleed,
    );
  } else {
    // container classes which are only valid for card layout
    renderClasses = cn(
      renderClasses,
      styles[layout],
      styles.isBackgroundContained,
      isAlternativeContainedLayout && styles.isAlternativeContainedLayout,
      !isAlternativeContainedLayout && styles.isDefaultContainedLayout,
      isCardIndented && styles.isCardIndented,
      isContentIndented && styles.isContentIndented,
      isMinimumPhotoHeight && image && styles.isMinimumPhotoHeight,
    );

    // image container styling
    backgroundContainerStyle = cn(
      backgroundContainerStyle,
      styles.backgroundImageContained,
      isAlternativeContainedLayout && styles.isAlternativeContainedLayout,
      !isAlternativeContainedLayout && styles.isDefaultContainedLayout,
    );
  }

  if (isQuoted) {
    renderClasses = cn(renderClasses, styles.isQuoted);
  }

  if (logo?.markupString) {
    renderClasses = cn(renderClasses, styles.hasLogo);
  } else {
    renderClasses = cn(renderClasses, styles.noLogo);
  }

  if (image) {
    renderClasses = cn(renderClasses, styles.hasImage);
  } else {
    renderClasses = cn(renderClasses, styles.noImage);
  }

  const $element = useRef<HTMLElement>(null);

  const renderMedia = () => {
    return isBackgroundFullBleed ? (
      <Image
        source={image}
        quality={85}
        className={cn(backgroundContainerStyle, styles.cropBorders)}
      />
    ) : (
      <GlowMedia
        glowConfig={{
          intensity: 0.25,
          scale: 1.1,
          position: GlowPosition.BOTTOM_LEFT,
          type: 'IMAGE',
        }}
        sanityMedia={image}
        className={cn(styles.glowWrapper, styles.cropBorders)}
        elementClassName={backgroundContainerStyle}
      >
        <Shadow className={styles.dropShadow} />
      </GlowMedia>
    );
  };

  return (
    <section
      className={renderClasses}
      ref={$element}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <div className={styles.spacerWrapper}>
        <div className={styles.container}>
          <div className={styles.innerWrapper}>
            {logo?.markupString && (
              <div className={styles.logoWrapper}>
                <Svg {...logo} className={styles.logo} />
              </div>
            )}

            {/* TODO: this is bad, why isAutoGrid set to false??? */}

            <PortableText
              isAutoGrid={false}
              animate={false}
              content={content}
              className={styles.content}
              options={{
                block: {
                  title0: {
                    tagName: 'h1',
                  },
                  title1: {
                    tagName: 'h2',
                  },
                  title3: {
                    className: isAlternativeContainedLayout
                      ? styles.title3md2
                      : styles.title3,
                    tagName: 'h2',
                  },
                  title4: {
                    className: styles.title4,
                    tagName: 'h3',
                  },
                  title6: {
                    tagName: 'span',
                  },
                  titles: {
                    className: styles.quoteText,
                  },
                  body: {
                    className: styles.quoteText,
                  },
                  bodySmall: {
                    className: styles.attribution,
                  },
                  accents: {
                    className: styles.accents,
                  },
                  eyebrow: {
                    className: styles.eyebrow,
                  },
                  label: {
                    className: styles.attribution,
                  },
                },
                marks: {
                  em: {
                    className: styles.em,
                  },
                },
                types: {
                  'block.buttons': {
                    className: styles.buttons,
                  },
                },
              }}
            />
          </div>
        </div>

        {image ? (
          renderMedia()
        ) : (
          <>
            <Glow
              className={styles.glowWrapper}
              hasMedia={true}
              config={{
                type: 'COLOR',
                color: '#9947EB',
                intensity: 0.2,
                position: GlowPosition.BOTTOM_LEFT,
                scale: 1.1,
                dither: 0.8,
              }}
            />
            <Shadow className={backgroundContainerStyle}>
              <div className={cn(styles.blur, styles.cropBorders)} />
            </Shadow>
          </>
        )}
      </div>
    </section>
  );
};

export default SpeedBump;
