const link = `
	_type,
	type,
	label,
	type == 'internal' => {
		"url":"/" + coalesce(
			coalesce(
			*[_id match "drafts." + ^.reference._ref][0],
			reference->,
		).slug.current,
		''
		),
	},
	type == 'external' => {
		url,
	},
	type == 'download' => {
		"url": downloadurl,
	},
	type == 'phoneNumber' => {
		"url": phoneNumberUrl,
	},
	hash
`;

export default link;
