import { Fragment, RefObject, useRef } from 'react';

import { CMSAsymmetricalCardItem } from '../types';
import Row from './row';
import styles from './styles.module.css';

const CardRows = ({
  asymmetricalCardItems,
}: {
  asymmetricalCardItems: CMSAsymmetricalCardItem[];
  garageDoorRevealParent?: RefObject<HTMLElement>;
}) => {
  const half = Math.ceil(asymmetricalCardItems.length / 2);
  const rows = [
    asymmetricalCardItems.slice(0, half),
    asymmetricalCardItems.slice(half),
  ];

  const $rowsIds = useRef<string[]>(
    Array(rows.length)
      .fill(null)
      .map((item, index) => index.toString()),
  );

  const $wrapper = useRef<HTMLDivElement>(null);
  return (
    <div ref={$wrapper} className={styles.wrapper}>
      {rows.map((row: CMSAsymmetricalCardItem[], index: number) => (
        <Fragment key={$rowsIds.current[index]}>
          <Row
            rowIndex={index}
            cards={row}
            className={index === 0 ? styles.firstRow : ''}
          />
          <div className={styles.rowBreak} />
        </Fragment>
      ))}
    </div>
  );
};

export default CardRows;
