import { cn } from '~/utils';
import addToRefArray from '~/utils/addToRefArray';

import { BlockComponentProps, BlockElementProps } from '../types';
import getColumnClassesFromKey from '../utils/getColumnClassesFromKey';
import getElementPropsFromOptions from '../utils/getElementPropsFromOptions';

const Block = ({
  _key,
  blockName,
  blockGroupName,
  children,
  module,
  className = '',
  tagName = 'p',
}: BlockComponentProps) => {
  const externalOptionProps: BlockElementProps | null =
    getElementPropsFromOptions(module, 'block', blockName, blockGroupName);

  // default to props on <Block> element
  let renderClass: string = cn(externalOptionProps?.className, className);
  const renderTag: string = externalOptionProps?.tagName ?? tagName;

  // get css classes to split content into columns as designated by usage of block.column in sanity
  renderClass = cn(
    renderClass,
    getColumnClassesFromKey(
      module.content,
      _key,
      externalOptionProps.moduleStyles,
    ),
  );

  // TODO: attributes doesn't make sense but it's required by the type checker
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const attributes: any = {};
  const RenderTag = renderTag;
  const optionsRef =
    typeof externalOptionProps.ref !== 'undefined'
      ? externalOptionProps.ref
      : null;

  return (
    <RenderTag
      className={renderClass || null}
      ref={(element: HTMLDivElement) =>
        optionsRef && element && addToRefArray(element, optionsRef)
      }
      {...attributes}
    >
      {children}
    </RenderTag>
  );
};

export default Block;
