import { colors } from '@frameio-bs/tokens';
import { gsap } from 'gsap';
import { CSSProperties, useCallback, useRef, useState } from 'react';

import DeviceWrapper from '~/components/atoms/deviceWrapper';
import Observer from '~/components/atoms/observer';
import { VideoRef } from '~/components/atoms/video/types';
import Glow from '~/components/molecules/glow';
import { GlowPosition, GlowType } from '~/components/molecules/glow/types.d';
import Media from '~/components/molecules/media';
import PortableText from '~/components/molecules/portableText';
import { TextAnimations } from '~/components/molecules/portableText/types.d';
import Shadow from '~/components/molecules/shadow';
import useUIStore from '~/state/ui';
import {
  cn,
  RGBToHex,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';
import { EaseType } from '~/utils/singletons/Easing';

import { mediaSlideInAnimation, scaleDownAnimation } from './animations';
import styles from './styles.module.css';
import { CMSModuleHeroParallaxProps } from './types';

const HeroParallax = ({
  content,
  garageDoorRevealParent,
  media,
  uiMedia,
  background,
  moduleIndex,
  className,
  slug,
}: CMSModuleHeroParallaxProps) => {
  const $glowWrapper = useRef<HTMLDivElement>(null);
  const $element = useRef<HTMLDivElement>(null);
  const $media = useRef<VideoRef>(null);
  const $uiMedia = useRef<VideoRef>(null);
  const animation = useRef<GSAPTimeline>();
  const $mediaContainer = useRef<HTMLDivElement>(null);
  const $uiContainerInner = useRef<HTMLDivElement>(null);
  const $mediaGridWrapper = useRef<HTMLDivElement>(null);
  const progressSetter = useRef<(value: number) => void>();

  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  const initialMediaOpacity = useRef<string>(moduleIndex === 0 ? '0' : '');
  const windowWidth = useUIStore((state) => state.windowWidth);
  const bannerHeight = useUIStore((state) => state.bannerHeight);
  const bannerDismissed = useUIStore((state) => state.bannerDismissed);

  const glowType = GlowType.BAKED_CANVAS;

  // Grab dominant colors from the image or the video's thumbnail
  let startColor = RGBToHex(
    colors.find((color) => color.name === 'Grey 00')?.value || 'rgb(0,0,0)',
  );
  let stopColor = RGBToHex(
    colors.find((color) => color.name === 'Grey 00')?.value || 'rgb(0,0,0)',
  );
  if (
    media &&
    'thumbnail' in media?.sanityMedia &&
    media?.sanityMedia?.thumbnail?.asset.darkVibrantBackground &&
    media?.sanityMedia?.thumbnail?.asset.lightMutedBackground
  ) {
    startColor = media.sanityMedia.thumbnail.asset.darkVibrantBackground;
    stopColor = media.sanityMedia.thumbnail.asset.lightMutedBackground;
  }

  const onProgress = useCallback((progress: number) => {
    if (progressSetter.current)
      progressSetter.current(
        gsap.utils.clamp(0, 1, gsap.utils.normalize(0.1, 1, progress)),
      );
    if (progress > 0.26 && !$media.current?.$player?.paused) {
      // We pause the main video
      $media.current?.pause();
      // Scale down medias container (so we reveal the UI)
      animation.current?.play();
    }
  }, []);

  const onCompleteScaleDown = () => {
    // We play the UI video, if it exists
    $uiMedia.current?.play();
  };

  useScrollProgress($mediaContainer, onProgress, {
    $garageDoorElement: garageDoorRevealParent,
  });

  useLayoutEffect(() => {
    if (uiMedia?.sanityMedia?.mediaType && $uiContainerInner.current) {
      animation.current = scaleDownAnimation({
        $uiContainerInner,
        onCompleteScaleDown,
      });
    }
    if ($mediaGridWrapper.current) {
      mediaSlideInAnimation({
        $mediaGridWrapper,
      });
    }

    progressSetter.current = gsap.quickSetter(
      $element.current,
      '--transition-out-progress',
    ) as (value: number) => void;
  }, [uiMedia?.sanityMedia?.mediaType]);

  useLayoutEffect(() => {
    if ($glowWrapper.current && $mediaContainer.current) {
      gsap.ticker.add(
        () => {
          const rect = $mediaContainer.current?.getBoundingClientRect();

          gsap.set($glowWrapper.current, {
            '--glow-width': rect?.width + 'px',
            '--glow-height': rect?.height + 'px',
          });
        },
        true,
        true,
      );
    }
  }, [windowWidth]);

  useLayoutEffect(() => {
    gsap.to($element.current, {
      duration: bannerDismissed ? 0.5 : 0,
      '--banner-height': bannerHeight + 'px',
      ease: EaseType.BASIC_BUTTER,
    });
  }, [bannerHeight, bannerDismissed]);

  return (
    <section
      className={cn(
        styles.heroParallax,
        className,
        background && styles.hasBackground,
      )}
      ref={$element}
      style={
        {
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <div ref={$glowWrapper} className={styles.glowWrapper}>
        <Glow
          type={glowType}
          className={styles.glow}
          config={{
            type: 'GRADIENT',
            gradient: {
              startColor,
              stopColor,
              startPosition: 0.34,
              stopPosition: 1,
              orientation: 0,
            },
            intensity: 1.2,
            scale: 0.9,
            position: GlowPosition.TOP_RIGHT,
          }}
          style={
            {
              '--glow-transition-in-delay': '1s',
              '--glow-skew': '-12deg',
            } as CSSProperties
          }
        />
      </div>
      <PortableText
        content={content}
        className={styles.contentWrapper}
        alignment="center"
        isAutoRag={false}
        animate={TextAnimations.FULL}
        options={{
          block: {
            eyebrow: {
              className: styles.eyebrow,
            },
            accents: {
              className: styles.accents,
            },
            titles: {
              className: styles.titles,
            },
            title1: {
              className: styles.title1,
            },
            bodyLarge: {
              className: styles.bodyLarge,
            },
            bodies: {
              className: styles.bodies,
            },
          },
          types: {
            'block.buttons': {
              className: styles.buttons,
            },
            'block.form': {
              className: styles.forms,
            },
          },
        }}
      />

      <div
        className={cn(styles.mediaGridWrapper, styles.isMobileRightAligned)}
        ref={$mediaGridWrapper}
        style={
          initialMediaOpacity.current
            ? ({
                opacity: initialMediaOpacity.current,
              } as CSSProperties)
            : {}
        }
      >
        <Shadow className={cn(styles.mediaContainer)}>
          <Observer
            className={styles.mediaWrapper}
            ref={$mediaContainer}
            callback={updateIsInView}
            options={{ rootMargin: '200% 0%' }}
          >
            <div className={cn(styles.uiContainer)}>
              <div
                className={cn(
                  styles.uiContainerInner,
                  uiMedia?.sanityMedia?.mediaType && styles.hasUiMedia,
                )}
                ref={$uiContainerInner}
              >
                {uiMedia?.sanityMedia?.mediaType && (
                  <Media
                    className={styles.uiMedia}
                    sanityMedia={uiMedia.sanityMedia}
                    ref={$uiMedia}
                    canAutoplay={false}
                    isDisplayed={isInView !== false}
                  />
                )}
                <Media
                  className={cn(
                    styles.media,
                    media?.sanityMedia?.mediaType && styles.hasUiMedia,
                  )}
                  sanityMedia={media.sanityMedia}
                  ref={$media}
                  canAutoplay={true}
                  isDisplayed={isInView !== false}
                />
              </div>
            </div>
            <DeviceWrapper
              deviceType="studioDisplay"
              isInView={isInView !== false}
            />
          </Observer>
        </Shadow>
      </div>
    </section>
  );
};

export default HeroParallax;
