import { gsap } from 'gsap';
import { CSSProperties, useCallback, useRef } from 'react';

import Image from '~/components/atoms/image';
import PortableText from '~/components/molecules/portableText';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import {
  cn,
  getSpacersStyles,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';

import styles from './styles.module.css';
import { CMSModuleCapture } from './types';

const Capture = ({
  content,
  image,
  hasSpacers,
  accentColor,
  background,
  className,
  slug,
}: CMSModuleCapture) => {
  // eslint-disable-next-line @typescript-eslint/ban-types
  const scrollProgressQuickSet = useRef<Function>();

  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);

  const $capture = useRef<HTMLDivElement>(null);
  const onProgress = useCallback((progress: number) => {
    if (scrollProgressQuickSet.current) {
      scrollProgressQuickSet.current(`${(1 - progress) * -30}%`);
    }
  }, []);
  useScrollProgress($capture, onProgress);

  useLayoutEffect(() => {
    scrollProgressQuickSet.current = gsap.quickSetter(
      $capture.current,
      '--capture-parallax-y',
    );
  }, []);

  return (
    <section
      className={cn(
        styles.capture,
        background && styles.hasBackground,
        className,
      )}
      ref={$capture}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <div
        className={cn(
          styles.container,
          hasSpacers?.before && decorationsStyles.hasSpacerBefore,
          hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        )}
      >
        <PortableText
          content={content}
          className={styles.content}
          alignment="center"
          options={{
            block: {
              titles: {
                className: styles.name,
              },
              bodies: {
                className: styles.paragraph,
              },
            },
            types: {
              'block.buttons': {
                className: styles.buttons,
              },
              'block.form': {
                className: styles.forms,
              },
            },
          }}
        />
      </div>
      {image && (
        <Image source={image} className={styles.backgroundImageContainer} />
      )}
    </section>
  );
};

export default Capture;
