import { gsap } from 'gsap';
import { RefObject } from 'react';

export const testimonialCardsDragTimeline = ({
  $titleWrapper,
  $titles,
  $cards,
}: {
  $titleWrapper: RefObject<HTMLUListElement>;
  $titles: RefObject<HTMLLIElement[]>;
  $cards: RefObject<HTMLLIElement[]>;
}) => {
  const tl = gsap.timeline({ paused: true });

  let offset = 0;
  if (!$titles.current) return;
  $titles.current.forEach((title: HTMLLIElement, index: number) => {
    const { x } = title.getBoundingClientRect();
    // skip first item
    if (index === 0) {
      offset = x;
      return;
    }

    // TODO: had to put it here to avoid typescript naying / find why typescript is complaning if I put the fail-safe outside the loop
    if (!$cards.current) return;
    const prevCard = $cards.current[index - 1];
    const currentCard = $cards.current[index];
    const nextCard = $cards.current[index + 1];

    if (!prevCard || !currentCard) return;

    tl.to(prevCard, {
      rotate: '-40deg',
      x: '-200%',
      '--progress': 2.5,
      ease: 'linear',
      duration: 1,
    });
    tl.fromTo(
      currentCard,
      {
        y: -30,
        opacity: 0.4,
        scale: 0.9,
        '--progress': 0,
      },
      {
        scale: 1,
        y: 0,
        opacity: 1,
        '--progress': 1,
        ease: 'linear',
        duration: 1,
      },
      '<',
    );

    tl.to(
      $titleWrapper.current,
      {
        x: -(x - offset),
        ease: 'linear',
        duration: 1,
      },
      '<',
    );

    if (nextCard) {
      tl.fromTo(
        nextCard,
        {
          opacity: 0,
          scale: 0.9,
        },
        {
          y: -30,
          opacity: 0.4,
          scale: 0.9,
          ease: 'linear',
          duration: 1,
        },
        '<',
      );
    }
  });

  //  TODO: could probably be improved
  // set initial opacities for cards
  $cards.current?.forEach((card: HTMLLIElement, index: number) => {
    if (index < 2) return;
    gsap.set(card, {
      opacity: 0,
    });
  });

  return tl;
};
