import {
  PortableTextObjectRef,
  PortableTextProps,
  PropOptionObject,
} from '../types';

// retrieve ref from module options

const getRefFromOptions = (
  module: PortableTextProps,
  blockKey: string,
  elementKey: string,
  groupKey = '',
): PortableTextObjectRef | null => {
  if (module.options) {
    const blockValue = module.options[blockKey] ?? null;

    if (blockValue) {
      const groupBlockValue = (groupKey && blockValue[groupKey]) ?? null;
      const groupElementRef = (groupBlockValue && groupBlockValue.ref) || null;

      // if we're just querying for a group ref
      if (groupElementRef && !elementKey) {
        return groupElementRef as PortableTextObjectRef;
      }

      // if we're querying for an element ref
      const blockElementValue = blockValue as PropOptionObject;
      const elementValue = blockElementValue[elementKey] ?? null;
      return (elementValue?.ref as PortableTextObjectRef) || null;
    }
  }
  return null;
};

export default getRefFromOptions;
