import { useKeenSlider } from 'keen-slider/react';
import { useEffect, useState } from 'react';

import Card from '~/components/molecules/hoverCard';
import CarouselNavigation from '~/components/molecules/hoverCard/carouselNavigation';
import useUIStore from '~/state/ui';
import { cn, getSpacer, keenSliderConfig } from '~/utils';

import { CMSAsymmetricalCardItem } from '../types';
import styles from './styles.module.css';

const spacerValue = getSpacer(16);

const XsCards = ({
  asymmetricalCardItems,
}: {
  asymmetricalCardItems: CMSAsymmetricalCardItem[];
}) => {
  const [currentIndex, setCurrentIndex] = useState<number>(0);
  const isComputedStyleComplete = useUIStore(
    (state) => state.isComputedStyleComplete,
  );

  const [sliderRef, instanceRef] = useKeenSlider({
    ...keenSliderConfig.defaultConfig,
    selector: '.slideItem',
    slides: {
      origin: 'center',
      spacing: spacerValue,
      perView: 'auto',
      number: asymmetricalCardItems.length,
    },
    breakpoints: {
      [keenSliderConfig.breakpoints.sm.mediaQuery]: {
        disabled: true,
      },
    },
    slideChanged(slider) {
      setCurrentIndex(slider.track.details.rel);
    },
  });

  const moveToIndex = (index: number) => {
    instanceRef.current?.moveToIdx(index);
  };

  useEffect(() => {
    if (isComputedStyleComplete) {
      instanceRef.current?.update();
    }
  }, [isComputedStyleComplete, instanceRef]);

  return (
    <div className={styles.xsCards}>
      <div className={styles.cardsContainer} ref={sliderRef}>
        {asymmetricalCardItems.map((card: CMSAsymmetricalCardItem) => (
          <Card
            className={cn(styles.slideItem, 'slideItem')}
            key={card._key}
            _key={card._key}
            title={card.title}
            content={card.content}
            image={card.image}
          />
        ))}
      </div>
      <CarouselNavigation
        cards={asymmetricalCardItems}
        currentIndex={currentIndex}
        moveToIndex={moveToIndex}
      />
    </div>
  );
};

export default XsCards;
