import { useKeenSlider } from 'keen-slider/react';
import { useEffect, useState } from 'react';

import PaginationDots from '~/components/atoms/pagination/dots';
import HoverCard from '~/components/molecules/hoverCard';
import useUIStore from '~/state/ui';
import { cn, getSpacer, keenSliderConfig } from '~/utils';

import { CategoryProps } from '../types';
import styles from './styles.module.css';

const spacerValue = getSpacer(16);

const CategoryCards = ({
  gridCardItems,
  title,
  className,
  squaredAspectRatio = false,
  isInView,
}: CategoryProps) => {
  const [currentIndex, setCurrentIndex] = useState<number>(0);
  const isComputedStyleComplete = useUIStore(
    (state) => state.isComputedStyleComplete,
  );
  const [sliderRef, instanceRef] = useKeenSlider({
    ...keenSliderConfig.defaultConfig,
    selector: '.slideItem',
    slides: {
      origin: 'center',
      spacing: spacerValue,
      perView: 'auto',
      number: gridCardItems.length,
    },
    breakpoints: {
      [keenSliderConfig.breakpoints.sm.mediaQuery]: {
        disabled: true,
      },
    },
    slideChanged(slider) {
      setCurrentIndex(slider.track.details.rel);
    },
  });

  const moveToIndex = (index: number) => {
    instanceRef.current?.moveToIdx(index);
  };

  useEffect(() => {
    if (isComputedStyleComplete) {
      instanceRef.current?.update();
    }
  }, [isComputedStyleComplete, instanceRef]);

  return (
    <div className={cn(styles.category, className)}>
      {title && <h3 className={styles.categoryTitle}>{title}</h3>}
      <ul className={styles.items} ref={sliderRef}>
        {gridCardItems.map(
          ({ _key: cardKey, image, title, eyebrow, content, logo }) => (
            <li className={cn(styles.item, 'slideItem')} key={cardKey}>
              <HoverCard
                _key={cardKey}
                squaredAspectRatio={squaredAspectRatio}
                className={styles.card}
                content={content}
                image={image}
                title={title}
                eyebrow={eyebrow}
                logo={logo}
                isInView={isInView}
              />
            </li>
          ),
        )}
      </ul>
      <PaginationDots
        className={styles.gridCardsCarouselNavigation}
        total={gridCardItems.length}
        activeIndex={currentIndex}
        onClick={moveToIndex}
      />
    </div>
  );
};

export default CategoryCards;
