import { Children, ReactNode } from 'react';

import { CMSLogoTextItem } from '~/components/molecules/logoTextItem/types';

type ChildrenColumnProps = {
  children?: ReactNode;
  rows?: number;
};

export const mapChildrenToRows = ({
  children = [],
  rows = 1,
}: ChildrenColumnProps): ReactNode[] => {
  const nodes = [];

  if (rows === 1) {
    return Children.toArray(children);
  }

  const c = Children.map(children, (child) => child) || [];
  for (let i = 0; i < rows; i += 1) {
    nodes[i] = c.filter((_child: ReactNode, j: number) => j % rows === i);
  }

  return nodes;
};

/*
This function gets an array of logoTextItem objects and if the items arent enough we duplicate them
until we get to the min amount of logoTextItems.
*/
export const ensureEnoughItems = (items: CMSLogoTextItem[], min = 20) => {
  const data = [];
  if (items.length < min) {
    while (data.length < min) {
      // create a duplicate
      const duplicate = {
        ...items[data.length % items.length],
      } as CMSLogoTextItem;
      // modify sanity key to avoid react warnings on duplicate key.
      duplicate._key = `${duplicate._key}-d-${data.length}`;
      data.push(duplicate);
    }
  }

  return data;
};
