import { ForwardedRef, useRef } from 'react';

import SvgOrImage from '~/components/molecules/svgOrImage';
import { cn } from '~/utils';

import { ChicletConstellationLayout } from '../types';
import styles from './styles.module.css';
import { CMSLogoItem } from './types';

type LogoItemProps = {
  logo: CMSLogoItem;
  rowIndex: number;
  layout: ChicletConstellationLayout;
  className?: string;
  isFeatured?: boolean;
  isInView: boolean;
};

const LogoItem = ({
  logo,
  layout,
  className,
  isFeatured,
  isInView,
}: LogoItemProps) => {
  const $logoContainer = useRef<HTMLDivElement>(null);
  const $logo = useRef<SVGElement | HTMLDivElement>(null);

  return (
    <div ref={$logoContainer} className={cn(styles.logo, className)}>
      <SvgOrImage
        ref={$logo as ForwardedRef<HTMLDivElement>}
        {...logo.svgOrImage}
        alt={logo.name}
        className={cn(
          styles.icon,
          isFeatured && styles.featured,
          layout === ChicletConstellationLayout.CLOUD && styles.cloud,
        )}
        isInView={isInView}
      />
    </div>
  );
};

export default LogoItem;
