import {
  CSSProperties,
  MouseEvent,
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react';

import useUIStore from '~/state/ui';
import { cn } from '~/utils';

import { formatMMSS } from '../../utils/formatMMSS';
import styles from './styles.module.css';
import { TimeFormat, TrackProps } from './types';

const Track = ({
  className,
  duration,
  loadedSeconds,
  playedSeconds,
  seekTo,
}: TrackProps) => {
  const [isDragging, setIsDragging] = useState(false);
  const [timeFormat, setTimeFormat] = useState<TimeFormat>('remaining');
  const [barWidth, setBarWidth] = useState<number>(0);
  const $barContainer = useRef<HTMLDivElement>(null);
  const windowWidth = useUIStore((state) => state.windowWidth);

  const handleMouseDown = useCallback(() => {
    setIsDragging(true);
  }, []);

  const handleMouseMove = (event: MouseEvent<HTMLDivElement>) => {
    if (isDragging) seekTo((event.nativeEvent.offsetX / barWidth) * duration);
  };

  const handleMouseUp = (event: MouseEvent<HTMLDivElement>) => {
    if (isDragging) seekTo((event.nativeEvent.offsetX / barWidth) * duration);
    setIsDragging(false);
  };

  const toggleRemainingTime = useCallback(() => {
    if (timeFormat === 'remaining') setTimeFormat('elapsed');
    else setTimeFormat('remaining');
  }, [timeFormat]);

  useEffect(() => {
    if ($barContainer.current) {
      const rect = $barContainer.current.getBoundingClientRect();
      setBarWidth(rect.width);
    }
  }, [windowWidth]);

  return (
    <div
      className={cn(styles.track, className)}
      style={
        {
          '--buffer-progress': `${(1 - loadedSeconds / duration) * -100}%`,
          '--playing-progress': `${(1 - playedSeconds / duration) * -100}%`,
        } as CSSProperties
      }
    >
      <div
        className={styles.trackBars}
        onMouseDown={handleMouseDown}
        onMouseMove={handleMouseMove}
        onMouseUp={handleMouseUp}
        onMouseLeave={handleMouseUp}
        // hidden for screen rearders
        aria-hidden={true}
        ref={$barContainer}
      >
        <div className={styles.durationBar}>
          <div className={styles.trackBufferBar}></div>
          <div className={styles.trackProgressBar}></div>
          <div className={styles.trackPointer}></div>
        </div>
      </div>

      <button className={styles.remainingTime} onClick={toggleRemainingTime}>
        {timeFormat === 'remaining'
          ? `-${formatMMSS(duration - playedSeconds)}`
          : formatMMSS(playedSeconds)}
      </button>
    </div>
  );
};

export default Track;
