import Buttons from '../components/buttons';
import {
  PortableTextButtonsBlockProps,
  PortableTextButtonsProps,
} from '../components/buttons/types';
import Column from '../components/column';
import { PortableTextColumnProps } from '../components/column/types';
import DriftButton from '../components/DriftButton';
import {
  PortableTextDriftButtonBlockProps,
  PortableTextDriftButtonProps,
} from '../components/DriftButton/DriftButton.types.d';
import Form from '../components/form';
import {
  PortableTextFormBlockProps,
  PortableTextFormProps,
} from '../components/form/types';
import Spacer from '../components/spacer';
import {
  PortableTextSpacerBlockProps,
  PortableTextSpacerProps,
} from '../components/spacer/types';
import Stats from '../components/stats';
import {
  PortableTextStatsBlockProps,
  PortableTextStatsProps,
} from '../components/stats/types';
import SvgOrImage from '../components/svgOrImage/SvgOrImage';
import {
  PortableTextSvgOrImageBlockProps,
  PortableTextSvgOrImageProps,
} from '../components/svgOrImage/types';
import Touts from '../components/touts';
import {
  PortableTextToutsBlockProps,
  PortableTextToutsProps,
} from '../components/touts/types';
import { PortableTextProps, TypesRecord } from '../types';
import emptyRefArray from './emptyRefArray';

// types -
// NOTE: this does not currently have a config object because type components are so intertwined

// module - props passed in from portable text
// { value } - these are the content values being passed in from Sanity

const makeTypes = (module: PortableTextProps): TypesRecord => {
  return {
    'block.buttons': ({ value }: { value: PortableTextButtonsBlockProps }) => {
      const typeProps: PortableTextButtonsProps = {
        module,
        value,
        typeName: 'block.buttons',
        typeCategory: 'types',
      };
      emptyRefArray(module, typeProps.typeCategory, typeProps.typeName);
      return <Buttons {...typeProps} />;
    },

    'block.driftButton': ({
      value,
    }: {
      value: PortableTextDriftButtonBlockProps;
    }) => {
      const typeProps: PortableTextDriftButtonProps = {
        module,
        value,
        typeName: 'block.driftButton',
        typeCategory: 'types',
      };
      emptyRefArray(module, typeProps.typeCategory, typeProps.typeName);

      return <DriftButton {...typeProps} />;
    },

    'block.column': ({}: { value: PortableTextColumnProps }) => {
      return <Column />;
    },

    'block.form': ({ value }: { value: PortableTextFormBlockProps }) => {
      const typeProps: PortableTextFormProps = {
        module,
        value,
        typeName: 'block.form',
        typeCategory: 'types',
      };

      emptyRefArray(module, typeProps.typeCategory, typeProps.typeName);

      return <Form {...typeProps} />;
    },

    'block.spacer': ({ value }: { value: PortableTextSpacerBlockProps }) => {
      const typeProps: PortableTextSpacerProps = {
        module,
        value,
        typeName: 'block.spacer',
        typeCategory: 'types',
      };

      emptyRefArray(module, typeProps.typeCategory, typeProps.typeName);

      return <Spacer {...typeProps} />;
    },

    'block.stats': ({ value }: { value: PortableTextStatsBlockProps }) => {
      const typeProps: PortableTextStatsProps = {
        module,
        value,
        typeName: 'block.stats',
        typeCategory: 'types',
      };

      emptyRefArray(module, typeProps.typeCategory, typeProps.typeName);

      return <Stats {...typeProps} />;
    },

    'block.svgOrImage': ({
      value,
    }: {
      value: PortableTextSvgOrImageBlockProps;
    }) => {
      const typeProps: PortableTextSvgOrImageProps = {
        module,
        value,
        typeName: 'block.svgOrImage',
        typeCategory: 'types',
      };

      emptyRefArray(module, typeProps.typeCategory, typeProps.typeName);

      return <SvgOrImage {...typeProps} />;
    },

    'block.touts': ({ value }: { value: PortableTextToutsBlockProps }) => {
      const typeProps: PortableTextToutsProps = {
        module,
        value,
        typeName: 'block.touts',
        typeCategory: 'types',
      };

      emptyRefArray(module, typeProps.typeCategory, typeProps.typeName);

      return <Touts {...typeProps} />;
    },
  };
};

export default makeTypes;
