/**
 * Renders the icon for the default and Drift CTAs. Renders a download icon for download links, and arrow icon for internal and external links.
 * @param buttonVariant The variant of the parent cta, determines whether icon should render. Icon will only be paired with "textWithIcon" variant.
 * @param to The parent CTA's link
 * @example <ButtonIcon buttonVariant={buttonVariant} to-{to} />
 */
import { ForwardedRef, forwardRef } from 'react';

import SvgDownload from '~/assets/svg/download.svg';
import SvgPhone from '~/assets/svg/phone.svg';
import { cn } from '~/utils';

import AnimatedSvgArrow from '../../AnimatedSvgArrow';
import styles from './ButtonIcon.styles.module.css';
import { ButtonIconProps } from './ButtonIcon.types.d';

const ButtonIcon = (
  { className, buttonVariant, to }: ButtonIconProps,
  ref: ForwardedRef<HTMLElement>,
) => {
  if (buttonVariant !== 'textWithIcon' && to?.type !== 'phoneNumber')
    return null;

  switch (to?.type) {
    case 'phoneNumber':
      return (
        <span
          className={cn(
            styles.iconContainer,
            styles.phoneIconContainer,
            className,
          )}
        >
          <SvgPhone className={styles.phoneIcon} ref={ref} />
        </span>
      );

    case 'download':
      return (
        <span
          className={cn(
            styles.iconContainer,
            styles.downloadIconContainer,
            className,
          )}
          ref={ref}
        >
          <SvgDownload className={styles.icon} />
        </span>
      );
    default:
      return (
        <span className={cn(styles.arrowContainer, className)} ref={ref}>
          <AnimatedSvgArrow />
        </span>
      );
  }
};

export default forwardRef(ButtonIcon);
