import { cn } from '~/utils';

import styles from './styles.module.css';

const CarouselNavigation = ({
  cards,

  className,
  currentIndex,
  moveToIndex,
}: {
  cards: Array<{ _key: string }>;

  className?: string;
  currentIndex?: number;
  moveToIndex?: (value: number) => void;
}) => {
  return (
    <div className={cn(styles.navigation, className)}>
      {cards.map((card: { _key: string }, index: number) => (
        <button
          className={cn(styles.dot, index === currentIndex && styles.isCurrent)}
          key={`${card._key}-dot`}
          onClick={() => moveToIndex && moveToIndex(index)}
        >
          <span className="visuallyHidden">{`Go to slide ${index + 1}`}</span>
        </button>
      ))}
    </div>
  );
};

export default CarouselNavigation;
