import { ComponentType } from 'react';

import { cn } from '~/utils';

import Block from '../blocks/Block';
import { listConfig } from '../config';
import defaultStyles from '../styles.module.css';
import {
  BlockElementProps,
  BlockTemplateConfigProps,
  JsxRenderProps,
  ListRecord,
  PortableTextProps,
} from '../types';
import emptyRefArray from './emptyRefArray';
import getElementPropsFromOptions from './getElementPropsFromOptions';

// create block elements (typography - example: h1-h6, body sizes, label)

const makeList = (module: PortableTextProps): ListRecord => {
  const { isAutoStyles = true } = module;
  const config = listConfig;
  const listRecord: ListRecord = {};

  // clear stale refs
  for (const blockName in config) {
    emptyRefArray(module, 'list', blockName);
  }

  // create blocks
  for (const listTypeKey in config) {
    const listType = config[listTypeKey];
    if (typeof listType !== 'undefined' && module) {
      listRecord[listTypeKey] = (props: JsxRenderProps) => {
        let renderClass = cn(
          isAutoStyles && defaultStyles['list'],
          isAutoStyles && defaultStyles[listTypeKey],
          props.className,
        );

        const blockKey = props.value?._key || '';
        const blockProps: BlockTemplateConfigProps = {
          _key: blockKey,
          blockName: listTypeKey,
          module,
          className: renderClass,
          tagName: listType.tagName,
          render: listType.render,
        };

        if (blockProps?.render) {
          const CustomBlockRender =
            blockProps.render as ComponentType<JsxRenderProps>;
          const { blockName } = blockProps;

          const externalOptionProps: BlockElementProps | null =
            getElementPropsFromOptions(module, 'list', blockName);

          renderClass = cn(renderClass, externalOptionProps?.className);

          return (
            <CustomBlockRender className={renderClass} {...props}>
              {props.children}
            </CustomBlockRender>
          );
        }

        return <Block {...blockProps}>{props.children}</Block>;
      };
    }
  }

  return listRecord;
};

export default makeList;
