import linkFragment from '~/components/atoms/link/fragment';
import buttonFragment from '~/components/molecules/portableText/components/button/fragment';
import columnFragment from '~/components/molecules/portableText/components/column/fragment';
import driftButtonFragment from '~/components/molecules/portableText/components/DriftButton/DriftButton.fragment';
import formFragment from '~/components/molecules/portableText/components/form/fragment';
import statsFragment from '~/components/molecules/portableText/components/stats/fragment';
import svgOrImageFragment from '~/components/molecules/portableText/components/svgOrImage/fragment';
import toutsFragment from '~/components/molecules/portableText/components/touts/fragment';

import { blockConfig } from './config';

// automatically create a comma separated string of block types based on object keys from the config file
const blockKeys: string[] = [];
for (const blockTypeGroupsKey in blockConfig) {
  Object.keys(blockConfig[blockTypeGroupsKey]).map((value) =>
    blockKeys.push(`'${value}'`),
  );
}

//
export const requiredFields = ['_key', '_type'];

// portable text standard styles
const stylesFragment = `
  ${requiredFields},
  children,
  markDefs[]{
    ...,
    _type == "internalLink" => {
      ${linkFragment}
    }
  },
  style,
`;

const fragment = `
  _type == 'block.button' => {
    ${buttonFragment}
  },
  _type == 'block.driftButton' => {
    ${driftButtonFragment}
  },
  _type == 'block.column' => {
    ${columnFragment}
  },
  _type == 'block.form' => {
    ${formFragment}
  },
  _type == 'block.stats' => {
    ${statsFragment}
  },
  _type == 'block.svgOrImage' => {
    ${svgOrImageFragment}
  },
  _type == 'block.touts' => {
    ${toutsFragment}
  },
  _type == 'block' && style in [${blockKeys}] => {
    ${stylesFragment}
  }
`;

export default fragment;
