import { gsap } from 'gsap';
import { CSSProperties, useCallback, useMemo, useRef, useState } from 'react';

import DeviceWrapper from '~/components/atoms/deviceWrapper';
import { ImageProps } from '~/components/atoms/image/image.types.d';
import Link from '~/components/atoms/link';
import Observer from '~/components/atoms/observer';
import {
  GlowGradientSource,
  GlowPosition,
} from '~/components/molecules/glow/types.d';
import GlowMedia from '~/components/molecules/glowMedia';
import { GlowMediaRef } from '~/components/molecules/glowMedia/types';
import Logo from '~/components/molecules/logo';
import PortableText from '~/components/molecules/portableText';
import Shadow from '~/components/molecules/shadow';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import {
  cn,
  getBrightnessByColor,
  getSpacersStyles,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';

import styles from './styles.module.css';
import { CMSModuleSideBySide } from './types';

const MAX_PARALLAX_PERCENT = 16;
const SideBySide = ({
  content,
  layout,
  media,
  hasSpacers,
  accentColor,
  isImageFullBleed,
  background,
  logo,
  linkTo,
  wrapInDevice,
  isIndented,
  garageDoorRevealParent,
  className,
  slug,
}: CMSModuleSideBySide) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  const $element = useRef<HTMLDivElement>(null);

  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  const progressSetter = useRef<(value: number) => void>();
  const glowMedia = useRef<GlowMediaRef>(null);
  let mediaMayParallax = false;
  useLayoutEffect(() => {
    progressSetter.current = gsap.quickSetter(
      $element.current,
      '--side-by-side-progress',
    ) as (value: number) => void;
  }, []);

  const onProgress = useCallback(
    (progress: number, isInView: boolean) => {
      const $media = glowMedia.current?.$media;

      if (
        $media &&
        media.sanityMedia.mediaType === 'video' &&
        media.sanityMedia.willAutoplay
      ) {
        if (
          isInView &&
          progress > 0.3 &&
          progress < 0.5 &&
          $media.current &&
          $media.current.$player &&
          $media.current.$player.paused === true &&
          (!$media.current.$player.ended || media.sanityMedia.isLooping)
        ) {
          glowMedia.current?.$media.current.play();
        }
        // If we scrolled past it
        if (!isInView) {
          $media.current?.restart();
        }
      }
      if (isImageFullBleed || !$media) return;

      if (progressSetter.current) {
        progressSetter.current(progress);
      }
    },
    [isImageFullBleed, media],
  );

  useScrollProgress($element, onProgress, {
    $garageDoorElement: garageDoorRevealParent,
  });

  const mediaClass = styles.media;
  let renderClass = cn(
    styles.sideBySide,
    background && styles.hasBackground,
    className,
  );

  let containerClassName = styles.mediaContainerInGlow;

  if (isImageFullBleed) {
    renderClass = cn(renderClass, styles.isImageFullBleed);
  } else if (
    wrapInDevice !== 'studioDisplay' &&
    // We only parallax videos
    media.sanityMedia.mediaType === 'video'
  ) {
    mediaMayParallax = true;
    containerClassName = cn(styles.needsParallax, containerClassName);
  }
  let mediaAspectRatio =
    'asset' in media.sanityMedia
      ? media.sanityMedia.asset.aspectRatio
      : media.sanityMedia.aspectRatio;
  if (mediaAspectRatio) {
    mediaAspectRatio = mediaMayParallax
      ? mediaAspectRatio * (1 + MAX_PARALLAX_PERCENT / 100)
      : mediaAspectRatio;
  }

  if (isIndented) {
    renderClass = cn(renderClass, styles.isIndented);
  }

  if (layout === 'mediaLeftTextRight') {
    renderClass = cn(renderClass, styles.isTextRight);
  } else {
    renderClass = cn(renderClass, styles.isTextLeft);
  }

  renderClass = cn(
    renderClass,
    hasSpacers?.before && decorationsStyles.hasSpacerBefore,
    hasSpacers?.after && decorationsStyles.hasSpacerAfter,
    mediaMayParallax && styles.parallax,
    !!linkTo && styles.hasLink,
  );

  const mediaContainerClass = cn(
    styles.mediaContainer,
    styles.isMediaShadow,
    wrapInDevice === 'studioDisplay' && styles.wrapInStudioDisplay,
    'asset' in media.sanityMedia &&
      media.sanityMedia.asset.isOpaque === false &&
      styles.hasAlpha,
  );

  const mediaIntensityAndScale = useMemo(() => {
    if (!media.sanityMedia) {
      return {
        intensity: 0.8,
        scale: 1.1,
      };
    }

    const isImage = media.sanityMedia.mediaType === 'image';
    let mainColor: string | undefined;

    const brightness = (
      mainColor ? getBrightnessByColor(mainColor) : 255 / 2
    ) as number;

    let intensity = isImageFullBleed
      ? brightness < 128
        ? gsap.utils.mapRange(0, 128, 0.8, 0.4, brightness)
        : gsap.utils.mapRange(128, 255, 0.6, 0.5, brightness)
      : brightness < 128
      ? gsap.utils.mapRange(0, 128, 1, 0.8, brightness)
      : gsap.utils.mapRange(128, 255, 1, 0.8, brightness);

    let startColor = '#000000';
    let stopColor = '#000000';

    // Grab dominant colors from the image or the video's thumbnail

    if (isImage && 'asset' in media.sanityMedia) {
      mainColor = media.sanityMedia?.asset?.dominantBackground;
      startColor = media.sanityMedia?.asset?.darkMutedBackground || '#000000';
      stopColor = media.sanityMedia?.asset?.dominantBackground || '#000000';

      // If has transparency, bump down the intensity
      if (media.sanityMedia.asset.isOpaque === false) {
        intensity *= 0.9;
      }
    } else if ('thumbnail' in media.sanityMedia) {
      mainColor = media.sanityMedia?.thumbnail?.asset?.dominantBackground;
      startColor =
        media.sanityMedia?.thumbnail?.asset?.darkMutedBackground || '#000000';
      stopColor =
        media.sanityMedia?.thumbnail?.asset?.dominantBackground || '#000000';
    }

    const config = {
      intensity,
      scale:
        isImageFullBleed && wrapInDevice !== 'studioDisplay'
          ? gsap.utils.mapRange(0, 128, 1.8, 1.1, brightness)
          : gsap.utils.mapRange(0, 128, 1.15, 1, brightness),
      gradient: {
        startColor,
        stopColor,
        startPosition: 0.34,
        stopPosition: 0.85,
        orientation: 0,
      },
    };

    return config;
  }, [media, isImageFullBleed]);

  const Wrapper = linkTo ? Link : 'div';

  return (
    <section
      className={renderClass}
      ref={$element}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
          '--max-parallax-percent': MAX_PARALLAX_PERCENT - 2,
          '--media-aspect-ratio': mediaAspectRatio,
        } as CSSProperties
      }
      id={slug}
    >
      <Wrapper
        to={linkTo ? linkTo : undefined}
        className={cn(styles.container, !!linkTo && styles.withLink)}
      >
        <PortableText
          isAutoGrid={false}
          content={content}
          className={styles.content}
          options={{
            block: {
              accents: {
                className: styles.accents,
              },
              bodies: {
                className: styles.bodies,
              },
              titles: {
                className: styles.titles,
              },
              title3: {
                className: styles.title3,
              },
              title4: {
                className: styles.title4,
                tagName: 'h3',
              },
              title6: {
                tagName: 'h4',
              },
              bodySmall: {
                className: styles.bodySmall,
              },
            },
            types: {
              'block.buttons': {
                className: styles.buttons,
              },
              'block.stats': {
                className: styles.stats,
              },
              'block.touts': {
                className: styles.touts,
              },
            },
          }}
        />
        <Observer
          className={mediaContainerClass}
          callback={updateIsInView}
          options={{ rootMargin: '200% 0%' }}
        >
          <GlowMedia
            ref={glowMedia}
            glowBackgroundClassName={styles.glowBackground}
            glowConfig={{
              ...(mediaIntensityAndScale as GlowGradientSource),
              type: 'GRADIENT',
              position: isImageFullBleed
                ? GlowPosition.CENTER
                : GlowPosition.TOP_RIGHT,
            }}
            contain={true}
            sanityMedia={media.sanityMedia}
            containerClassName={containerClassName}
            className={mediaClass}
            elementClassName={styles.mediaElement}
            isDisplayed={isInView !== false}
            style={
              {
                '--glow-skew': '-10deg',
              } as CSSProperties
            }
          >
            <>
              {wrapInDevice === 'studioDisplay' ? (
                <>
                  <Shadow />
                  <DeviceWrapper
                    className={styles.studioDisplayWrapper}
                    deviceType="studioDisplay"
                    isInView={isInView !== false}
                  />
                </>
              ) : ('asset' in media.sanityMedia &&
                  media.sanityMedia.asset.isOpaque) ||
                media.sanityMedia.mediaType === 'video' ? (
                <Shadow />
              ) : null}
              {logo && (
                <Logo
                  className={styles.logo}
                  svg={logo.svg}
                  image={
                    {
                      ...logo.image,
                    } as ImageProps
                  }
                  isInView={isInView !== false}
                />
              )}
            </>
          </GlowMedia>
        </Observer>
      </Wrapper>
    </section>
  );
};

export default SideBySide;
