import mergeWith from 'lodash/mergeWith';

import { mergeWithClassNamesCustomizer } from '~/utils';

import {
  BlockElementProps,
  CMSPortableTextProps,
  PortableTextProps,
} from '../types';

// return all results from JSX options

const getElementPropsFromOptions = (
  module: PortableTextProps | CMSPortableTextProps,
  blockKey: string,
  elementKey: string,
  blockGroupName?: string,
): BlockElementProps => {
  if (module.options) {
    const blockValue = module.options[blockKey] ?? null;
    if (blockValue) {
      const elementValue = blockValue[elementKey] ?? null;
      const groupValue = (blockGroupName && blockValue[blockGroupName]) ?? null;
      // Merge both `groupValue` and `elementValue` and use mergeWithClassNamesCustomizer as a customizer to allow
      // to merge both objects with preserving all their attributes
      // We spread the "values" to prevent mutation
      const mergedValues = mergeWith(
        { ...groupValue },
        { ...elementValue },
        mergeWithClassNamesCustomizer,
      );

      return { ...mergedValues };
    }
  }
  return {};
};

export default getElementPropsFromOptions;
