//https://gist.github.com/w3core/e3d9b5b6d69a3ba8671cc84714cca8a4
/**
 * Calculate brightness value by RGB or HEX color.
 * @param color (String) The color value in RGB or HEX (for example: #000000 || #000 || rgb(0,0,0) || rgba(0,0,0,0))
 * @returns (Number) The brightness value (dark) 0 ... 255 (light)
 */
export default function brightnessByColor(colorString: string) {
  const color = '' + colorString,
    isHEX = color.indexOf('#') == 0,
    isRGB = color.indexOf('rgb') == 0;

  let r: number | undefined;
  let g: number | undefined;
  let b: number | undefined;

  if (isHEX) {
    const hasFullSpec = color.length == 7;
    const m = color.slice(1).match(hasFullSpec ? /(\S{2})/g : /(\S{1})/g);
    if (m) {
      r = parseInt(m[0] + (hasFullSpec ? '' : m[0]), 16);
      g = parseInt(m[1] + (hasFullSpec ? '' : m[1]), 16);
      b = parseInt(m[2] + (hasFullSpec ? '' : m[2]), 16);
    }
  }
  if (isRGB) {
    const m = color.match(/(\d+){3}/g);
    if (m) {
      r = parseInt(m[0], 16);
      g = parseInt(m[1], 16);
      b = parseInt(m[2], 16);
    }
  }

  if (
    typeof r != 'undefined' &&
    typeof g != 'undefined' &&
    typeof b != 'undefined'
  )
    return (r * 299 + g * 587 + b * 114) / 1000;
}
