import { CSSProperties, useState } from 'react';

import Observer from '~/components/atoms/observer';
import PortableText from '~/components/molecules/portableText';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { cn, getSpacersStyles } from '~/utils';

import CategoryCards from './CategoryCards';
import styles from './styles.module.css';
import { CMSModuleGridCardsProps } from './types';

const GridCards = ({
  accentColor,
  background,
  categories,
  className,
  hasSpacers,
  squaredAspectRatio,
  text,
  slug,
}: CMSModuleGridCardsProps) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  return (
    <section
      className={cn(
        styles.gridCards,
        background && styles.hasBackground,
        hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        className,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <PortableText
        content={text}
        className={styles.text}
        options={{
          block: {
            bodyLarge: {
              className: styles.gridCardParagraph,
            },
            eyebrow: {
              className: styles.gridCardeyebrow,
            },
            title2: {
              className: styles.gridCardtitle2,
            },
            bodies: {
              className: styles.bodies,
            },
          },
          types: {
            'block.buttons': {
              className: styles.gridCardCtaWrapper,
            },
          },
        }}
      />
      <Observer
        callback={updateIsInView}
        options={{ rootMargin: '200% 0%' }}
        className={styles.categoriesContainer}
      >
        {categories.map(({ title, _key, gridCardItems }) => (
          <CategoryCards
            key={_key}
            title={title}
            gridCardItems={gridCardItems}
            squaredAspectRatio={squaredAspectRatio}
            isInView={isInView !== false}
          />
        ))}
      </Observer>
    </section>
  );
};

export default GridCards;
