import { gsap } from 'gsap';
import { CSSProperties, useCallback, useEffect, useRef, useState } from 'react';

import SvgCircleCheckmark from '~/assets/svg/circle-checkmark.svg';
import SvgI from '~/assets/svg/i.svg';
import Button from '~/components/atoms/Buttons/Ctas/Button';
import PortableText from '~/components/molecules/portableText';
import GlobalData from '~/data/stores/GlobalData';
import usePricingStore from '~/state/pricing';
import useUIStore from '~/state/ui';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { cn, getSpacersStyles } from '~/utils';
import ScrollPosition from '~/utils/domEvents/ScrollPosition';
import getRootOffsetBounds from '~/utils/getRootOffsetBounds';

import { CMSCheckmarkFeature, CMSComparisonFeature } from '../global/types';
import styles from './styles.module.css';
import { CMSModulePricingTable } from './types';

const PricingTable = ({
  text,
  hasSpacers,
  accentColor,
  pricePeriod,
  background,
  className,
  slug,
}: CMSModulePricingTable) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  const globalPricePeriodValue = usePricingStore((state) => state.pricePeriod);

  const $refEl = useRef<HTMLElement>(null);
  const $refHeadingsRow = useRef<HTMLTableRowElement>(null);
  const $refTable = useRef<HTMLTableElement>(null);
  const $refTableBodyWrapper = useRef<HTMLDivElement>(null);
  const $refTableHeadersWrapper = useRef<HTMLDivElement>(null);
  const $refTableHeaders = useRef<HTMLTableElement>(null);

  const [tableBounds, setTableBounds] = useState(
    $refTable.current ? getRootOffsetBounds($refTable.current) : null,
  );

  const [headingsBounds, setHeadingsBounds] = useState(
    $refHeadingsRow.current
      ? getRootOffsetBounds($refHeadingsRow.current)
      : null,
  );

  const [isStuck, setIsStuck] = useState(false);

  // ui store values

  const navigationHeight = useUIStore((state) => state.navigationHeight || 80);
  const windowWidth = useUIStore((state) => state.windowWidth);
  const windowHeight = useUIStore((state) => state.windowHeight);

  const setIsDesktopNavigationOpaque = useUIStore(
    (state) => state.setIsDesktopNavigationOpaque,
  );

  const unsetIsDesktopNavigationOpaque = useUIStore(
    (state) => state.unsetIsDesktopNavigationOpaque,
  );

  const isDesktopNavigationShowing = useUIStore(
    (state) => state.isDesktopNavigationShowing,
  );

  // shortcuts to global pricing values

  const globalPricing = GlobalData.data.pricing;
  const products = globalPricing.products;

  // checkmark and feature table values

  const featureComparisons: CMSComparisonFeature[][] = products.map(
    (product) => product.featureComparisons,
  );

  const featureComparisonMaxLength = featureComparisons.reduce(
    (prevMax, currentValue) =>
      currentValue.length > prevMax.length ? currentValue : prevMax,
  ).length;

  const checkmarkComparisons: CMSCheckmarkFeature[][] = products.map(
    (product) => product.checkmarkComparisons,
  );

  const checkmarkComparisonMaxLength = checkmarkComparisons.reduce(
    (prevMax, currentValue) =>
      currentValue.length > prevMax.length ? currentValue : prevMax,
  ).length;

  // track whether this element has been registered in uiStore for isNavigationOpaque
  const isNavigationOpaque = useRef(false);

  // store last transform value to prevent unnecessary repeated value setting
  const lastHeaderWrapperTransform = useRef<string | null>(null);

  // TODO: split into measure and mutate?

  const transformHeaderWrapper = useCallback((value: string) => {
    if (
      value !== lastHeaderWrapperTransform.current ||
      lastHeaderWrapperTransform.current === null
    ) {
      if ($refTableHeadersWrapper.current) {
        $refTableHeadersWrapper.current.style.transform = value;
      }

      lastHeaderWrapperTransform.current = value;
    }
  }, []);

  const onTick = useCallback(() => {
    if (
      ScrollPosition.y !== null &&
      headingsBounds &&
      tableBounds &&
      windowHeight
    ) {
      const tableTopY = tableBounds.top - headingsBounds.height;
      const isInViewport =
        ScrollPosition.y + windowHeight >= tableTopY &&
        ScrollPosition.y <= tableBounds.bottom;

      if (isInViewport) {
        if (!isNavigationOpaque.current && $refEl.current) {
          isNavigationOpaque.current = true;
          setIsDesktopNavigationOpaque($refEl.current);
        }

        if (ScrollPosition.y >= tableTopY) {
          if (!isStuck) {
            setIsStuck(true);
          }
        }

        if (isDesktopNavigationShowing) {
          if (ScrollPosition.y < tableTopY - navigationHeight) {
            // unstick complete
            transformHeaderWrapper('');
            setIsStuck(false);
          } else {
            // animate back into place
            const navYOffset = Math.round(
              Math.max(
                Math.min(
                  ScrollPosition.y -
                    (tableTopY - navigationHeight) -
                    navigationHeight,
                  navigationHeight,
                ) - 1,
                0,
              ),
            );

            transformHeaderWrapper(`translateY(${navYOffset}px)`);
          }
        } else {
          // navigation not showing, remove shift
          transformHeaderWrapper('');
        }
      } else {
        if (isNavigationOpaque.current && $refEl.current) {
          isNavigationOpaque.current = false;
          unsetIsDesktopNavigationOpaque($refEl.current);
        }
      }

      if ($refTableHeadersWrapper.current && $refTableBodyWrapper.current) {
        $refTableHeadersWrapper.current.scrollLeft =
          $refTableBodyWrapper.current.scrollLeft;
      }
    }
  }, [
    headingsBounds,
    isDesktopNavigationShowing,
    isStuck,
    navigationHeight,
    setIsDesktopNavigationOpaque,
    tableBounds,
    transformHeaderWrapper,
    unsetIsDesktopNavigationOpaque,
    windowHeight,
  ]);

  useEffect(() => {
    const $el = $refEl.current;
    gsap.ticker.add(onTick);

    return () => {
      gsap.ticker.remove(onTick);
      if ($el) {
        unsetIsDesktopNavigationOpaque($el);
      }
    };
  }, [
    headingsBounds,
    isStuck,
    onTick,
    tableBounds,
    unsetIsDesktopNavigationOpaque,
  ]);

  // recalculate bounds when window resizes

  useEffect(() => {
    if ($refHeadingsRow.current && $refTable.current) {
      setHeadingsBounds(getRootOffsetBounds($refHeadingsRow.current));
      setTableBounds(getRootOffsetBounds($refTable.current));
    }
  }, [windowWidth]);

  // price period can be defined by default in Sanity, and it can also be toggled via radio buttons in PricingOverview
  const renderPricePeriodValue = globalPricePeriodValue || pricePeriod?.value;
  if (!renderPricePeriodValue) return null;

  return (
    <section
      className={cn(
        styles.pricingTable,
        background && styles.hasBackground,
        hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        className,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
      ref={$refEl}
    >
      <PortableText
        animate={false}
        content={text}
        className={styles.text}
        options={{
          block: {
            titles: {
              className: styles.titles,
            },
            bodies: {
              className: styles.bodies,
            },
          },
          types: {
            'block.buttons': {
              className: styles.buttons,
            },
            'block.form': {
              className: styles.forms,
            },
          },
        }}
      />

      <div
        ref={$refTableHeadersWrapper}
        className={cn(
          styles.tableHeadersWrapper,
          isDesktopNavigationShowing && styles.isDesktopNavigationShowing,
          isStuck && styles.isStuck,
        )}
      >
        <table
          className={styles.tableHeaders}
          ref={$refTableHeaders}
          style={{ '--product-count': products.length } as CSSProperties}
        >
          <thead>
            <tr className={styles.headingsRow} ref={$refHeadingsRow}>
              {products.map((product) => {
                const priceDetails =
                  product.pricesByPeriod?.filter(
                    (price) =>
                      price.pricePeriod.value === renderPricePeriodValue,
                  )[0] || null;

                // sign up button can be from current price period or fallback
                const signUpButton =
                  priceDetails?.signUpButton?.button ||
                  product.signUpButton.button;

                return (
                  <th
                    className={styles.pricingColumn}
                    key={`pricing_column_${product._key}`}
                  >
                    {/* top blue text */}
                    <div className={styles.cardTitle}>
                      {product.title || product.secondaryTitle}
                    </div>
                    <div className={styles.cardPrices}>
                      <div className={styles.cardPrice}>
                        {!isNaN(priceDetails?.price) &&
                          `$${priceDetails.price}/${priceDetails.pricePeriod.abbreviation}`}
                        {isNaN(priceDetails?.price) && 'Custom'}
                      </div>
                    </div>

                    {/* cta */}
                    {signUpButton && (
                      <Button
                        {...signUpButton}
                        className={styles.cardDetailButton}
                      >
                        {signUpButton.label}
                      </Button>
                    )}
                  </th>
                );
              })}
            </tr>
          </thead>
        </table>
      </div>

      <div className={styles.tableBodyWrapper} ref={$refTableBodyWrapper}>
        <table
          ref={$refTable}
          className={styles.tableBody}
          style={{ '--product-count': products.length } as CSSProperties}
        >
          <tbody>
            {/* feature comparison rows */}

            {[...Array(featureComparisonMaxLength)].map(
              (value, featureComparisonIndex) => (
                <tr
                  className={styles.featureComparisonRow}
                  key={`feature_comparison_${featureComparisonIndex}`}
                >
                  {products.map((product) => {
                    const featureComparisonItem =
                      product.featureComparisons[featureComparisonIndex];
                    return (
                      <td
                        className={styles.featureComparisonItem}
                        key={`${product._key}_${featureComparisonItem._key}`}
                      >
                        <span className={styles.featureComparisonTitle}>
                          {featureComparisonItem.title || '-'}
                        </span>
                        <span className={styles.featureComparisonSubtitle}>
                          {featureComparisonItem.subtitle}
                        </span>
                        {featureComparisonItem.description && (
                          <SvgI className={styles.featureComparisonI} />
                        )}
                      </td>
                    );
                  })}
                </tr>
              ),
            )}

            {/* checkmark comparison rows */}

            {[...Array(checkmarkComparisonMaxLength)].map(
              (value, checkmarkComparisonIndex) => (
                <tr
                  className={styles.checkmarkComparisonRow}
                  key={`checkmark_comparison_${checkmarkComparisonIndex}`}
                >
                  {products.map((product) => {
                    const checkmarkComparisonItem =
                      product.checkmarkComparisons[checkmarkComparisonIndex];
                    return (
                      <td
                        className={styles.checkmarkComparisonItem}
                        key={`${product._key}_${checkmarkComparisonItem._key}`}
                      >
                        {checkmarkComparisonItem?.isChecked && (
                          <SvgCircleCheckmark
                            className={styles.checkmarkComparisonCheck}
                          />
                        )}
                        {!checkmarkComparisonItem?.isChecked && (
                          <figure className={styles.checkmarkComparisonNoCheck}>
                            -
                          </figure>
                        )}
                        <span className={styles.checkmarkComparisonTitle}>
                          {checkmarkComparisonItem?.subscriptionFeature
                            ?.title || ''}
                        </span>
                      </td>
                    );
                  })}
                </tr>
              ),
            )}
          </tbody>
        </table>
      </div>
    </section>
  );
};

export default PricingTable;
