import { CSSProperties } from 'react';

import { cn, getSpacersStyles } from '~/utils';

import ImageSequence from '../../imageSequence';
import styles from './styles.module.css';
import { SimpleProps } from './types';

const Simple = ({
  sequence,
  top,
  garageDoorRevealParent,
  hasSpacers,
  background,
  className,
  slug,
}: SimpleProps) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  return (
    <section
      className={cn(className, background && styles.hasBackground)}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <ImageSequence
        canvasClassName={styles.canvas}
        sequence={sequence}
        top={top}
        garageDoorRevealParent={garageDoorRevealParent}
      />
    </section>
  );
};

export default Simple;
