import { gsap } from 'gsap';
import { CSSProperties, useCallback, useRef, useState } from 'react';

import Observer from '~/components/atoms/observer';
import PortableText from '~/components/molecules/portableText';
import {
  cn,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';

import Card from './cards';
import styles from './styles.module.css';
import { CMSCardItem, CMSModuleCloudCardsProps } from './types';

const CloudCards = ({
  text,
  cloudCardItems,
  accentColor,
  background,
  className,
  slug,
}: CMSModuleCloudCardsProps) => {
  const $wrapper = useRef<HTMLDivElement>(null);
  const progressSetter = useRef<(value: number) => void>();
  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  useLayoutEffect(() => {
    progressSetter.current = gsap.quickSetter(
      $wrapper.current,
      '--cloud-cards-progress',
    ) as (value: number) => void;
  }, []);

  const onProgress = useCallback((progress: number) => {
    if (progressSetter.current) {
      progressSetter.current((progress - 0.5) * 2);
    }
  }, []);

  useScrollProgress($wrapper, onProgress);

  return (
    <section
      ref={$wrapper}
      className={cn(
        styles.cloudCards,
        background && styles.hasBackground,
        className,
      )}
      style={
        {
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <PortableText content={text} className={styles.text} isAutoGrid={false} />
      <Observer
        callback={updateIsInView}
        options={{ rootMargin: '200% 0%' }}
        className={styles.cardsContainer}
      >
        {cloudCardItems.map((card: CMSCardItem) => (
          <Card
            key={card._key}
            _key={card._key}
            title={card.title}
            image={card.image}
            isInView={isInView !== false}
          />
        ))}
      </Observer>
    </section>
  );
};

export default CloudCards;
