import { useRouter } from 'next/router';
import { ReactNode, useEffect, useMemo, useState } from 'react';

import Link from '~/components/atoms/link';
import { CMSLinkProps } from '~/components/atoms/link/types';
import { cn } from '~/utils';

import useNavState from '../navState';
import styles from './styles.module.css';

export type NavItemProps = {
  link?: CMSLinkProps;
  label?: string;
  icon?: ReactNode;
  isGroupHeader?: boolean;
  isGroupPage?: boolean;
  className?: string;
  hidden?: boolean;
  disableActiveState?: boolean;
  onMouseOver?: () => void;
  onMouseOut?: () => void;
  onFocus?: () => void;
  onBlur?: () => void;
  onClick?: () => void;
};

const NavItem = ({
  link,
  icon,
  label,
  isGroupHeader = false,
  className,
  hidden,
  disableActiveState = false,
  onMouseOver,
  onMouseOut,
  onFocus,
  onBlur,
  onClick,
}: NavItemProps) => {
  const router = useRouter();
  const isCurrentPage = useMemo(
    () => link && router.asPath === link.url,
    [router.asPath, link],
  );

  const isMobileNav = useNavState((state) => state.isMobileNav);

  const [active, setActive] = useState<boolean | undefined>(false);

  useEffect(() => {
    if (link && !isGroupHeader) {
      setActive(isCurrentPage && !disableActiveState);
    } else {
      setActive(!disableActiveState);
    }
  }, [isCurrentPage, disableActiveState, link, isGroupHeader]);

  const innerContent = (
    <span className={styles.innerWrapper}>
      {link?.label ? link.label : label ? label : ''}
      {icon && icon}
    </span>
  );

  const renderLink = () => {
    if (!isGroupHeader) return link;

    // if item is a group header, it will be a clickable link on desktop and a button that expands the subnav on mobile
    if (isGroupHeader && !isMobileNav) return link;

    return undefined;
  };

  const RenderTag = () => {
    const link = renderLink();

    if (link) {
      return (
        // disabling eslint here because we need the anchor to have mouseover and mouseout functionality to trigger the submenu
        // eslint-disable-next-line jsx-a11y/anchor-is-valid
        <Link
          className={cn(styles.navItem, className, active && styles.active)}
          aria-current={isCurrentPage ? 'page' : false}
          onMouseOver={onMouseOver}
          onMouseOut={onMouseOut}
          onFocus={onFocus}
          onBlur={onBlur}
          tabIndex={hidden ? -1 : 0}
          onClick={onClick}
          to={link}
        >
          {innerContent}
        </Link>
      );
    } else {
      return (
        <button
          className={cn(styles.navItem, className, active && styles.active)}
          aria-current={isCurrentPage ? 'page' : false}
          onMouseOver={onMouseOver}
          onMouseOut={onMouseOut}
          onFocus={onFocus}
          onBlur={onBlur}
          tabIndex={hidden ? -1 : 0}
          onClick={onClick}
        >
          {innerContent}
        </button>
      );
    }
  };

  return <RenderTag />;
};

export default NavItem;
