import { CSSProperties, RefObject, useCallback, useRef } from 'react';

import Button from '~/components/atoms/Buttons/Ctas/Button';
import { VideoRef } from '~/components/atoms/video/types';
import Glow from '~/components/molecules/glow';
import Media from '~/components/molecules/media';
import ModalVideoButton from '~/components/molecules/Modal/ModalVideoButton';
import PortableText from '~/components/molecules/portableText';
import { TextAnimations } from '~/components/molecules/portableText/types.d';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { Alignment, GradientOpacity, RaggingType } from '~/types/decorations.d';
import {
  cn,
  getSpacersStyles,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';
import gradientClassesMap from '~/utils/maps/gradientClasses';

import { doorAnimation } from './animations';
import styles from './styles.module.css';
import { CMSModuleHero } from './types.d';

const Hero = ({
  moduleIndex,
  alignment = Alignment.LEFT,
  background,
  className,
  content,
  garageDoorRevealParent,
  gradient = GradientOpacity.HEAVY,
  gradientMobile = GradientOpacity.SUPER_HEAVY,
  hasSpacers,
  accentColor,
  media,
  garageDoorRevealType,
  isGarageDoor,
  isHighContrast = false,
  ragging = RaggingType.DEFAULT,
  valignment = Alignment.MIDDLE,
  video,
  videoButtonLabel = 'Watch the video',
  videoButtonType,
  secondaryCtas,
  slug,
}: CMSModuleHero) => {
  const $background = useRef<HTMLElement | VideoRef>(null);
  const $element = useRef<HTMLDivElement>(null);

  const glowColor =
    garageDoorRevealType !== 'module.customImageSequence' &&
    media &&
    media.sanityMedia
      ? media.sanityMedia.mediaType === 'image'
        ? media.sanityMedia.asset.dominantBackground
        : media.sanityMedia.thumbnail?.asset.dominantBackground
      : null;

  const backgroundClasses = cn(
    styles.backgroundWrapper,
    alignment === Alignment.LEFT && styles.alignLeft,
    alignment === Alignment.CENTER && styles.alignCenter,
    gradient && styles[gradientClassesMap[gradient]],
    gradientMobile && styles[`${gradientClassesMap[gradientMobile]}Mobile`],
  );

  const renderClasses = cn(
    styles.hero,
    background && styles.hasBackground,
    alignment === Alignment.LEFT && styles.alignLeft,
    alignment === Alignment.CENTER && styles.alignCenter,
    valignment === Alignment.BOTTOM && styles.valignBottom,
    valignment === Alignment.MIDDLE && styles.valignMiddle,
    ragging === RaggingType.TIGHT && styles.ragTight,
    ragging === RaggingType.WIDE && styles.ragWide,
    gradient && styles[gradientClassesMap[gradient]],
    gradientMobile && styles[`${gradientClassesMap[gradientMobile]}Mobile`],
    isGarageDoor && styles.isGarageDoor,
    isHighContrast && styles.isHighContrast,
    video && styles.hasVideo,
    className,
  );

  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);

  // for portable text refs, always use arrays of HTML elements
  const $buttons = useRef([] as HTMLElement[]);
  const $accents = useRef([] as HTMLElement[]);
  const $titles = useRef([] as HTMLElement[]);
  const $bodies = useRef([] as HTMLElement[]);

  // video button
  const $ctaContainer = useRef(null);

  // animation reference
  const animation = useRef<GSAPTimeline>();

  useLayoutEffect(() => {
    if ($background.current) {
      let foregroundElements = [] as HTMLElement[];

      if ($titles.current) {
        foregroundElements = [...foregroundElements, ...$titles.current];
      }

      if ($accents.current) {
        foregroundElements = [...foregroundElements, ...$accents.current];
      }

      if ($bodies.current) {
        foregroundElements = [...foregroundElements, ...$bodies.current];
      }

      if ($buttons.current) {
        foregroundElements = [...foregroundElements, ...$buttons.current];
      }

      if ($ctaContainer.current) {
        foregroundElements.push($ctaContainer.current);
      }

      const foregroundElementsAndBounds = [];
      for (const foregroundElement of foregroundElements) {
        foregroundElementsAndBounds.push({
          foregroundElement,
          bounds: foregroundElement.getBoundingClientRect(),
        });
      }

      const sortedForegroundElementsAndBounds =
        foregroundElementsAndBounds.sort((a, b) => b.bounds.top - a.bounds.top);

      const sortedForegroundElements = sortedForegroundElementsAndBounds.map(
        (value) => value.foregroundElement,
      );

      const videoRef = $background as RefObject<VideoRef>;

      animation.current = doorAnimation({
        $element,
        $background:
          // TODO: maybe better ways to do this
          media.sanityMedia?.mediaType === 'video'
            ? (videoRef.current?.$wrapper as RefObject<HTMLElement>)
            : ($background as RefObject<HTMLElement>),
        foregroundElements: sortedForegroundElements,
      });
    }
  }, []);

  const onProgress = useCallback((progress: number) => {
    animation.current?.progress(progress);
  }, []);

  useScrollProgress($element, onProgress, {
    $garageDoorElement: garageDoorRevealParent,
  });

  const textAnimationType =
    moduleIndex === 0 ? TextAnimations.FULL : TextAnimations.LIGHT;

  return (
    <>
      <section
        className={renderClasses}
        ref={$element}
        style={
          {
            ...(spacerValues as CSSProperties),
            ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
            ...(background ? { '--module-background': background } : {}),
          } as CSSProperties
        }
        id={slug}
      >
        <div
          className={cn(
            styles.container,
            hasSpacers?.before && decorationsStyles.hasSpacerBefore,
            hasSpacers?.after && decorationsStyles.hasSpacerAfter,
          )}
        >
          <PortableText
            content={content}
            className={styles.content}
            animate={textAnimationType}
            isAutoRag={false}
            options={{
              block: {
                title0: {
                  tagName: 'h1',
                },
                title6: {
                  tagName: 'span',
                },
                titles: {
                  className: styles.titles,
                  ref: $titles,
                },
                accents: {
                  className: styles.accents,
                  ref: $accents,
                },
                bodies: {
                  className: styles.bodies,
                  ref: $bodies,
                },
              },
              types: {
                'block.buttons': {
                  className: styles.buttons,
                  ref: $buttons,
                },
                'block.form': {
                  className: styles.forms,
                },
                'block.svgOrImage': {
                  className: styles.svgOrImage,
                },
              },
            }}
          />
          <div
            className={cn(
              styles.ctaContainer,
              alignment === Alignment.CENTER && styles.alignCenter,
            )}
            ref={$ctaContainer}
          >
            {video?.url && (
              // prevent null value from being passed in buttons with videoButtonType not selected

              <ModalVideoButton
                className={styles.videoButton}
                video={video}
                videoButtonLabel={videoButtonLabel}
                videoButtonType={videoButtonType ? videoButtonType : undefined}
              />
            )}
            {secondaryCtas &&
              secondaryCtas.map((item) => {
                const { _key, to, buttonVariant, label, customTrackingEvent } =
                  item.button;

                return (
                  <Button
                    to={to}
                    buttonVariant={buttonVariant}
                    key={_key}
                    className={styles.secondaryCta}
                    customTrackingEvent={customTrackingEvent}
                  >
                    {label}
                  </Button>
                );
              })}
          </div>
        </div>
        {media && (
          <div className={backgroundClasses}>
            <Media
              sanityMedia={media.sanityMedia}
              className={styles.backgroundImageContainer}
              cover={true}
              quality={90}
              preload={true}
              sizes={{
                base: { width: 600, height: Math.round(600 / 0.6) },
                xs: { width: 960, height: Math.round(960 / 0.75) },
                sm: { width: 1240, height: Math.round(1240 / 1.5) },
                md: { width: 1700, height: Math.round(1700 / 1.5) },
                lg: { width: 2000, height: Math.round(2000 / 1.5) },
                xl: { width: 3840, height: Math.round(3840 / 1.5) },
              }}
              aspectRatio={1.5}
              controls={false}
              canAutoplay={true}
              willAutoplay={true}
              isMuted={true}
              isLooping={true}
              ref={$background}
            />
          </div>
        )}
      </section>
      {glowColor && isGarageDoor && (
        <Glow
          className={styles.bottomGlow}
          config={{
            color: glowColor,
            intensity: 0.75,
            scale: 1.2,
            type: 'COLOR',
          }}
        />
      )}
    </>
  );
};

export default Hero;
