import blurFragment from './fragments/blur';
import colorFragment from './fragments/color';
import compositeFragment from './fragments/composite';
import gradientFragment from './fragments/gradient';
import imageFragment from './fragments/image';

const vertex = /* glsl */ `
attribute vec2 uv;
attribute vec2 position;

varying vec2 vUv;

void main() {
	vUv = uv;
	gl_Position = vec4(position, 0, 1);
}

`;

export {
  vertex,
  colorFragment,
  gradientFragment,
  imageFragment,
  //  shader for post processing
  blurFragment,
  compositeFragment,
};
