import { useKeenSlider } from 'keen-slider/react';
import { CSSProperties, useCallback, useEffect, useRef, useState } from 'react';

import ButtonCarouselArrow from '~/components/atoms/Buttons/UI/ButtonCarouselArrow';
import Observer from '~/components/atoms/observer';
import PaginationDots from '~/components/atoms/pagination/dots';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { GradientOpacity } from '~/types/decorations.d';
import { cn, getSpacer, getSpacersStyles, keenSliderConfig } from '~/utils';
import addToRefArray from '~/utils/addToRefArray';

import styles from './styles.module.css';
import TestimonialSlide from './TestimonialSlide';
import { ForwardedTestimonialSlideRef } from './TestimonialSlide/types';
import { CMSModuleCarouselTestimonialProps } from './types';

const spacerValue = getSpacer(64);
const CarouselTestimonials = ({
  slides,
  hasSpacers,
  accentColor,
  background,
  className,
  slug,
}: CMSModuleCarouselTestimonialProps) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  const [prevBtnEnabled, setPrevBtnEnabled] = useState(false);
  const [nextBtnEnabled, setNextBtnEnabled] = useState(false);
  const [activeIndex, setActiveIndex] = useState(0);
  const lastIndex = useRef<number>(-1);
  const refSlides = useRef<ForwardedTestimonialSlideRef[]>(
    Array(slides.length),
  );
  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  const renderClasses = cn(
    styles.carouselTestimonials,
    background && styles.hasBackground,
    hasSpacers?.before && decorationsStyles.hasSpacerBefore,
    hasSpacers?.after && decorationsStyles.hasSpacerAfter,
    className,
  );

  const [sliderRef, instanceRef] = useKeenSlider({
    ...keenSliderConfig.defaultConfig,
    selector: '.slide',
    slides: {
      origin: 'center',
      perView: 'auto',
      spacing: 0,
    },
    breakpoints: {
      [keenSliderConfig.breakpoints.xs.mediaQuery]: {
        detailsChanged: (slider) => {
          const progresses = slider.track.details.slides.map(
            (slide) => slide.portion,
          );
          progresses.forEach((progress: number, slideIndex: number) => {
            const slide = refSlides.current[slideIndex];
            if (slide) slide.setProgress(progress);
          });
        },
      },
      [keenSliderConfig.breakpoints.sm.mediaQuery]: {
        slides: {
          spacing: spacerValue,
          origin: 'center',
          perView: 'auto',
        },
      },
    },
    slideChanged: () => {
      // this doesn't get triggered until the slide is settled
      checkCurrentSlide.current();
    },
    animationStarted: (slider) => {
      // this gets triggered as soon as the seek animation begins
      const targetIdx = slider.animator.targetIdx ?? -1;
      if (targetIdx >= 0) {
        checkCurrentSlide.current(targetIdx);
      }
    },
  });

  const checkCurrentSlide = useRef((targetIdx = -1) => {
    const keenSlider = instanceRef.current;
    if (keenSlider && keenSlider.track) {
      const useTargetIdx = targetIdx >= 0;
      const currentIndex =
        (useTargetIdx ? targetIdx : keenSlider.track.details?.rel) || 0;

      if (currentIndex !== lastIndex.current) {
        setPrevBtnEnabled(currentIndex > 0);
        setNextBtnEnabled(currentIndex < keenSlider.slides.length - 1);

        for (let i = 0; i < refSlides.current.length; i++) {
          const slide = refSlides.current[i];
          if (slide) {
            if (currentIndex === i) {
              slide.setActive(
                true,
                Math.sign(lastIndex.current - currentIndex),
              );
            } else {
              slide.setActive(false);
            }
          }
        }

        // for pagination dots
        setActiveIndex(currentIndex);
        lastIndex.current = currentIndex;
      }
    }
  });

  const scrollPrev = useCallback(() => {
    instanceRef.current?.prev();
  }, [instanceRef]);

  const scrollNext = useCallback(() => {
    instanceRef.current?.next();
  }, [instanceRef]);

  const goToIndex = useCallback(
    (index: number) => {
      instanceRef.current?.moveToIdx(index);
    },
    [instanceRef],
  );

  useEffect(() => {
    checkCurrentSlide.current();
  }, []);

  return (
    <section
      className={renderClasses}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <Observer
        callback={updateIsInView}
        options={{ rootMargin: '200% 0%' }}
        className={styles.slidesViewport}
      >
        <div className={`${styles.slidesContainer}`} ref={sliderRef}>
          {slides.map(
            (
              {
                _key,
                gradient = GradientOpacity.HEAVY,
                image,
                isQuoted,
                logo,
                text,
              },
              slideIndex,
            ) => (
              <TestimonialSlide
                ref={(ref: ForwardedTestimonialSlideRef) => {
                  addToRefArray(ref, refSlides, slideIndex);
                }}
                className={cn(styles.slide, 'slide')}
                key={_key}
                _key={_key}
                slideIndex={slideIndex}
                gradient={gradient}
                image={image}
                isQuoted={isQuoted}
                logo={logo}
                text={text}
                onClick={goToIndex}
                isInView={isInView !== false}
              />
            ),
          )}
        </div>
        <PaginationDots
          className={styles.paginationDots}
          activeIndex={activeIndex}
          total={slides.length}
          onClick={goToIndex}
        />
      </Observer>
      <ButtonCarouselArrow
        className={styles.buttonPrev}
        iconDirection={'left'}
        onClick={scrollPrev}
        disabled={!prevBtnEnabled}
      />
      <ButtonCarouselArrow
        className={styles.buttonNext}
        iconDirection={'right'}
        onClick={scrollNext}
        disabled={!nextBtnEnabled}
      />
    </section>
  );
};

export default CarouselTestimonials;
