import { CMSImage } from '~/components/atoms/image/image.types.d';
import { SvgProps } from '~/components/atoms/svg/types';
import { CMSPortableTextCustomContent } from '~/components/molecules/portableText/types';
import { CMSModuleBase } from '~/components/organisms/types';
import { GradientOpacity, RaggingType } from '~/types/decorations.d';

export enum LayoutAlignment {
  LOGO_LEFT_TEXT_RIGHT = 'logoLeftTextRight',
  LOGO_TOP_TEXT_BOTTOM = 'logoTopTextBottom',
  LOGO_TOP_TEXT_BOTTOM_SPREAD = 'logoTopTextBottomSpread',
  LOGO_TOP_TEXT_BOTTOM_SPREAD_MOBILE = 'logoTopTextBottomSpreadMobile',
}

export enum LayoutVerticalAlignment {
  CENTER = 'verticalCenter',
  BOTTOM = 'verticalBottom',
}

export type CMSModuleSpeedBump = CMSModuleBase & {
  content: CMSPortableTextCustomContent;
  gradient: GradientOpacity;
  gradientMobile: GradientOpacity;
  image: CMSImage;
  isAlternativeContainedLayout: boolean;
  isBackgroundFullBleed: boolean;
  isCardIndented: boolean;
  isCardOnMobile: boolean;
  isContentIndented: boolean;
  isMinimumPhotoHeight: boolean;
  isQuoted: boolean;
  layout: LayoutAlignment;
  layoutFullBleedVerticalAlignment: LayoutVerticalAlignment;
  logo: SvgProps;
  ragging?: RaggingType;
  use?: RaggingType;
};
