import { gsap } from 'gsap';
import { KeyboardEvent, useCallback, useEffect, useRef, useState } from 'react';
import { FocusableElement, tabbable } from 'tabbable';

import SvgExpandIcon from '~/assets/svg/expand.svg';
import { cn } from '~/utils';

import MuteUnmuteButton from './buttons/muteUnmuteButton';
import PlayPauseButton from './buttons/playPauseButton';
import styles from './styles.module.css';
import Track from './track';
import { ControlPanelProps } from './types';

// seconds
const TIMEOUT_AMOUNT = 3;
const KEYBOARD_CONTROL_TIME_AMOUNT = 5;

const Controls = ({
  className,
  details,
  duration,
  isMuted,
  isPlaying,
  loadedSeconds,
  playedSeconds,
  showMute = true,
  showTrack = true,
  play,
  pause,
  seekTo,
  toggleMute,
  toggleFullScreen,
}: ControlPanelProps) => {
  const [isVisible, setIsVisible] = useState(true);
  const $container = useRef<HTMLDivElement>(null);
  const lastMoveTimeRef = useRef<number>(Date.now());

  const togglePlayPause = useCallback(() => {
    if (isPlaying) {
      pause();
    } else {
      play();
    }
  }, [isPlaying, pause, play]);

  const handleMouseMove = useCallback(() => {
    if (!isVisible) {
      setIsVisible(true);
    }

    lastMoveTimeRef.current = Date.now();
  }, [isVisible]);

  const handleMouseLeave = useCallback(() => {
    if (isVisible) {
      setIsVisible(false);
    }
  }, [isVisible]);

  const handleKeyDown = useCallback(
    (event: KeyboardEvent<HTMLDivElement>) => {
      // Play/Pause
      if (event.code === 'Space' || event.key === 'k') {
        togglePlayPause();
        event.preventDefault();
      }
      // Toggle Mute
      if (event.key === 'm') {
        toggleMute();
      }
      // Toggle Fullscreen
      if (event.key === 'f') {
        toggleFullScreen();
      }
      // Fast Backward
      if (event.code === 'ArrowLeft') {
        seekTo(playedSeconds - KEYBOARD_CONTROL_TIME_AMOUNT);
      }
      // Faster Backward
      if (event.key === 'j') {
        seekTo(playedSeconds - KEYBOARD_CONTROL_TIME_AMOUNT * 2);
      }
      // Fast Forward
      if (event.code === 'ArrowRight') {
        seekTo(playedSeconds + KEYBOARD_CONTROL_TIME_AMOUNT);
      }
      // Faster Forward
      if (event.key === 'l') {
        seekTo(playedSeconds + KEYBOARD_CONTROL_TIME_AMOUNT * 2);
      }

      // Focus
      if (event.code === 'Tab') {
        // determine the sequence of tabbable elements contained
        // within our parent
        const sequence = tabbable($container.current as Element);

        const currentlyFocused = sequence.some((element: FocusableElement) => {
          if (element === document.activeElement) {
            return true;
          }
          return false;
        });

        if (currentlyFocused) {
          setIsVisible(true);
          return;
        }
      }
    },
    [playedSeconds, seekTo, togglePlayPause, toggleFullScreen, toggleMute],
  );

  useEffect(() => {
    // Focus so that Keyboard events get triggered
    $container.current?.focus();
  }, []);

  useEffect(() => {
    const tick = () => {
      if (Date.now() - lastMoveTimeRef.current > TIMEOUT_AMOUNT * 1000) {
        setIsVisible(false);
      }
    };

    if (isVisible) {
      gsap.ticker.add(tick);
    }

    return () => {
      if (isVisible) {
        gsap.ticker.remove(tick);
      }
    };
  }, [isVisible]);

  return (
    <div
      ref={$container}
      className={cn(styles.controlPanel, className)}
      onMouseMove={handleMouseMove}
      onMouseLeave={handleMouseLeave}
      onKeyDown={handleKeyDown}
      role="toolbar"
      tabIndex={0}
    >
      <PlayPauseButton
        className={cn(
          styles.centerPlayPauseButton,
          (isVisible || !isPlaying) && styles.isVisible,
        )}
        isPlaying={isPlaying}
        isRound={true}
        onClick={togglePlayPause}
      ></PlayPauseButton>

      <div className={cn(styles.bottomPanel, isVisible && styles.isVisible)}>
        {details && details.title && (
          <div className={styles.bottomPanelDetails}>
            <p className={styles.detailsTitle}>{details.title}</p>
            <p className={styles.detaislSubtitle}>{details.subtitle}</p>
          </div>
        )}
        <div className={styles.bottomPanelControls}>
          <PlayPauseButton
            className={styles.bottomPanelPlayPauseButton}
            isPlaying={isPlaying}
            onClick={togglePlayPause}
          />
          {showMute && (
            <MuteUnmuteButton
              className={styles.bottomPanelMuteUnmuteButton}
              isMuted={isMuted}
              onClick={toggleMute}
            />
          )}
          {showTrack && (
            <Track
              className={cn(styles.track, showMute && styles.hasMuteButton)}
              playedSeconds={playedSeconds}
              loadedSeconds={loadedSeconds}
              duration={duration}
              seekTo={seekTo}
            />
          )}
          <button
            className={styles.bottomPanelExpandButton}
            onClick={toggleFullScreen}
          >
            {/* TODO: to replace with dictionary entry */}
            <span className="visuallyHidden">Toggle fullscreen</span>
            <SvgExpandIcon />
          </button>
        </div>
      </div>
    </div>
  );
};

export default Controls;
