import { CSSProperties } from 'react';

import Image from '~/components/atoms/image';
import Glow from '~/components/molecules/glow';
import { GlowType } from '~/components/molecules/glow/types.d';
import { TextAnimations } from '~/components/molecules/portableText/types.d';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { GradientOpacity, RaggingType } from '~/types/decorations.d';
import { cn, getSpacersStyles } from '~/utils';
import gradientClassesMap from '~/utils/maps/gradientClasses';

import ContentPortableText from './ContentPortableText';
import styles from './styles.module.css';
import { CMSModuleTextWithComponents, CustomGlowLayout } from './types.d';

const TextWithComponents = ({
  accentColor,
  alignment,
  background,
  className,
  content,
  customGlowLayout,
  gradient = GradientOpacity.SUPER_HEAVY,
  gradientMobile = GradientOpacity.SUPER_HEAVY,
  hasSpacers,
  image = null,
  moduleIndex,
  ragging = RaggingType.DEFAULT,
  slug,
  garageDoorRevealParent,
}: CMSModuleTextWithComponents) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);

  const renderClass = cn(
    alignment === ('center' || alignment === 'centerAll') && styles.alignCenter,
    alignment === 'centerAll' && styles.alignCenterAll,
    className,
    hasSpacers?.after && decorationsStyles.hasSpacerAfter,
    hasSpacers?.before && decorationsStyles.hasSpacerBefore,
    image?.asset && styles.hasBackgroundImage,
    garageDoorRevealParent && styles.isRevealedByGarageDoor,
    ragging === RaggingType.TIGHT && styles.ragTight,
    ragging === RaggingType.WIDE && styles.ragWide,
    styles.textWithComponents,
  );

  const backgroundImageClasses = cn(
    styles.backgroundWrapper,
    gradient && styles[gradientClassesMap[gradient]],
    gradientMobile && styles[`${gradientClassesMap[gradientMobile]}Mobile`],
  );

  const textAnimationType =
    moduleIndex === 0 ? TextAnimations.FULL : TextAnimations.LIGHT;

  const renderCustomGlow = () => {
    switch (customGlowLayout) {
      case CustomGlowLayout.BottomLeftPurple:
        return (
          <Glow
            className={styles.bottomLeftGlow}
            type={GlowType.BAKED_CANVAS}
            config={{
              intensity: 0.5,
              scale: 1.6,
              gradient: {
                startColor: '#FD856E',
                stopColor: '#9947EB',
                startPosition: 0.34,
                stopPosition: 0.85,
                orientation: 0,
              },
              type: 'GRADIENT',
            }}
          />
        );
      case CustomGlowLayout.TopRightPurple:
        return (
          <Glow
            className={styles.topRightGlow}
            type={GlowType.BAKED_CANVAS}
            config={{
              intensity: 0.4,
              scale: 1.6,
              gradient: {
                startColor: '#6052F9',
                stopColor: '#C34F7C',
                startPosition: 0.34,
                stopPosition: 0.85,
                orientation: 0.5,
              },
              type: 'GRADIENT',
            }}
          />
        );
      case CustomGlowLayout.CenterRightPurple:
        return (
          <Glow
            className={styles.centerRightGlow}
            type={GlowType.BAKED_CANVAS}
            config={{
              intensity: 0.4,
              scale: 1.5,
              color: '#9947EB',
              type: 'COLOR',
            }}
          />
        );
    }
    return null;
  };
  return (
    <section
      className={renderClass}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      {background && <div className={styles.backgroundColorWrapper}></div>}
      <div className={styles.container}>
        <ContentPortableText
          className={styles.content}
          alignment={alignment}
          content={content}
          ragging={ragging}
          textAnimationType={textAnimationType}
        />

        {image?.asset && (
          <Image source={image} className={backgroundImageClasses} />
        )}
      </div>
      {renderCustomGlow()}
    </section>
  );
};

export default TextWithComponents;
