import cloneDeep from 'lodash/cloneDeep';

import {
  CMSPortableTextCustomContent,
  CMSPortableTextProps,
} from '~/components/molecules/portableText/types';
import { ModuleProps } from '~/components/organisms/types';
import recursiveSearchAll from '~/utils/recursiveSearchAll';

const portableText = (module: ModuleProps) => {
  // search for portable text fields within each module
  const portableTextFields = recursiveSearchAll(
    module,
    '_type',
    'portableText',
  );

  for (const result of portableTextFields.results) {
    const portableTextFieldResult = result.node as CMSPortableTextProps;
    if (
      portableTextFieldResult?.content &&
      Array.isArray(portableTextFieldResult.content) &&
      portableTextFieldResult.content.length
    ) {
      let moduleContent =
        portableTextFieldResult.content as CMSPortableTextCustomContent[];

      // find consecutive buttons and render them in a button group
      const moduleContentClone = cloneDeep(moduleContent);

      const firstButton = moduleContentClone.find(
        (contentItem) => contentItem._type === 'block.button',
      );

      let index = firstButton ? moduleContentClone.indexOf(firstButton) : -1;

      if (index >= 0) {
        let lastIndex = index;

        moduleContent.forEach((contentItem, i) => {
          if (contentItem._type === 'block.button') {
            if (i === lastIndex + 1) {
              // add item to existing button group
              const moduleContentCloneItem = moduleContentClone[index] ?? null;
              if (Array.isArray(moduleContentCloneItem?.buttons)) {
                moduleContentCloneItem.buttons.push(contentItem.button);
                moduleContentClone.splice(i, 1);
              }
            } else {
              // create new button group
              const moduleContentCloneItem = moduleContentClone[i] ?? null;
              if (moduleContentCloneItem) {
                (moduleContentCloneItem._type = 'block.buttons'),
                  (moduleContentCloneItem.buttons = [contentItem.button]);
                delete moduleContentCloneItem.button;
                index = i;
              }
            }

            lastIndex = i;
          }
        });

        // filter out the original button blocks
        moduleContent = moduleContentClone.filter(
          (contentItem) => contentItem._type !== 'block.button',
        );
      }

      // portable text can come from sanity as either an array or single block object
      // if a single block, there's no need for columns

      const columns = moduleContent.filter(
        (contentItem) => contentItem._type === 'block.column',
      );

      if (columns.length) {
        // columns:
        // add column indices if specified in sanity
        for (const contentItem of moduleContent) {
          if (typeof contentItem.gridColumnIndex === 'undefined') {
            contentItem.gridColumnIndex = 0;
            contentItem.gridColumnChildIndex = 0;
            contentItem.gridColumnsTotal = columns.length + 1;
          }
        }

        // increment gridColumnIndex after each block.column
        if (columns.length === 1) {
          const columnPos = moduleContent.indexOf(columns[0]);
          let childIndex = 0;

          // items before first column
          for (let i = 0; i < columnPos; i++) {
            const contentItem = moduleContent[i];
            contentItem.gridColumnChildIndex = childIndex;
            childIndex++;
          }

          // item group after first column
          childIndex = 0;
          for (let i = columnPos + 1; i < moduleContent.length; i++) {
            const contentItem = moduleContent[i];
            if (
              contentItem &&
              typeof contentItem.gridColumnIndex !== 'undefined'
            ) {
              contentItem.gridColumnIndex++;
              contentItem.gridColumnChildIndex = childIndex;
              childIndex++;
            }
          }
        } else {
          let isFirstColumn = true;
          for (const column of columns) {
            const columnPos = moduleContent.indexOf(column);
            let childIndex = 0;

            // items before first column
            if (isFirstColumn) {
              for (let i = 0; i < columnPos; i++) {
                const contentItem = moduleContent[i];
                contentItem.gridColumnChildIndex = childIndex;
                childIndex++;
              }
              isFirstColumn = false;
            }

            // item groups after first column
            for (let i = columnPos + 1; i < moduleContent.length; i++) {
              const contentItem = moduleContent[i];
              if (
                contentItem &&
                typeof contentItem.gridColumnIndex !== 'undefined'
              ) {
                contentItem.gridColumnIndex++;
                contentItem.gridColumnChildIndex = childIndex;
                childIndex++;
              }
            }
          }
        }
      }

      // portable text data is nested an extra level to allow a _types prop.
      // for react modules with typescript, it's annoying and unnecessary.
      // here we move content up a level to avoid nested objects. (ex: content.content)

      if (result.parentKey) {
        const portableTextParent = result.parent as Record<string, object>;
        portableTextParent[result.parentKey] = moduleContent;
      }
    }
  }
};

export default portableText;
