const breakpoints = [
  {
    // Phone
    name: "base",
    min: 0,
  },
  {
    // Large phone
    name: "xs",
    min: 600,
  },
  {
    // Tablet
    name: "sm",
    min: 960,
  },
  {
    // Laptop
    name: "md",
    min: 1240,
  },
  {
    // Desktop
    name: "lg",
    min: 1440,
  },
  {
    // Desktop
    name: "xl",
    min: 1920,
  },
];

breakpoints.sort((a, b) =>
  a.min === null ? 1 : b === null ? -1 : a.min - b.min
);

module.exports = breakpoints;
