import { gsap } from 'gsap';

import { EaseType } from '~/utils/singletons/Easing';

export const animateIn = (
  $element: HTMLButtonElement | HTMLDivElement | null,
  config?: { delay?: number },
) => {
  if (!$element) return;
  const tl = gsap.timeline({ ...config });

  tl.fromTo(
    $element,
    { '--hover-progress': 0 },
    {
      '--hover-progress': 1,
      duration: 0.35,
      ease: EaseType.DEFAULT,
    },
  );
  tl.fromTo(
    $element,
    { '--scale-y': 0.1, '--inner-scale-y': 2 },
    {
      '--scale-y': 1,
      '--inner-scale-y': 1,
      duration: 0.3,
      ease: EaseType.DEFAULT,
    },
    '<',
  );

  return tl;
};
export const animateOut = (
  $element: HTMLButtonElement | HTMLDivElement | null,
  config?: { delay?: number; onComplete?: () => void },
) => {
  if (!$element) return;
  return gsap.to($element, {
    '--hover-progress': 0,
    '--scale-y': 0.5,
    '--inner-scale-y': 2,
    duration: 0.3,
    ease: EaseType.DEFAULT,
    overwrite: true,
    ...config,
  });
};
