import { gsap } from 'gsap';
import React, { CSSProperties, useCallback, useRef, useState } from 'react';

import Image from '~/components/atoms/image';
import { urlFor } from '~/components/atoms/image/utils';
import { VideoRef } from '~/components/atoms/video/types';
import Glow from '~/components/molecules/glow';
import {
  GlowAlignment,
  GlowPosition,
} from '~/components/molecules/glow/types.d';
import Media from '~/components/molecules/media';
import PortableText from '~/components/molecules/portableText';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import {
  cn,
  getSpacersStyles,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';

import styles from './styles.module.css';
import { AppleTVSection, CMSAppleTVProps } from './types';

const THRESHOLD = 0.3;

const enterProgressTransformer = gsap.utils.pipe(
  gsap.utils.normalize(0, THRESHOLD),
  gsap.utils.clamp(0, 1),
);
const leaveProgressTransformer = gsap.utils.pipe(
  gsap.utils.normalize(1 - THRESHOLD, 1),
  gsap.utils.clamp(0, 1),
);

export const AppleTV = ({
  backgroundImage,
  foregroundImage,
  hasSpacers,
  accentColor,
  video,
  background,
  sections,
  className,
  slug,
}: CMSAppleTVProps) => {
  const $wrapper = useRef<HTMLDivElement>(null);
  const $sections = useRef<HTMLDivElement[]>([]);
  const $media = useRef<VideoRef>(null);
  const enterProgressSetter = useRef<(value: number) => void>();
  const [isVisible, setIsVisible] = useState<boolean>(false);

  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);

  // Track visibility via useScrollProgress, to avoid re-render
  const onProgress = useCallback((progress: number, isInView: boolean) => {
    if (isInView && !isVisible) {
      setIsVisible(isInView);
    }
    if (enterProgressSetter.current)
      enterProgressSetter.current(
        Math.max(
          1 - enterProgressTransformer(progress),
          leaveProgressTransformer(progress),
        ),
      );

    if ($media && $media.current && $media.current.$player) {
      if (isInView && $media.current.$player.paused === true) {
        $media.current.play();
      } else if (!isInView && $media.current.$player.paused !== true) {
        $media.current.pause();
      }
    }
  }, []);

  useScrollProgress($wrapper, onProgress);

  useLayoutEffect(() => {
    enterProgressSetter.current = gsap.quickSetter(
      $wrapper.current,
      '--appletv-enter-progress',
    ) as (value: number) => void;
  }, []);

  const backgroundGlowUrl = urlFor(backgroundImage)
    .quality(90)
    .width(300)
    .url();

  return (
    <section
      ref={$wrapper}
      className={cn(
        styles.appleTv,
        background && styles.hasBackground,
        hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        className,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <div className={styles.background}>
        <div className={styles.backgroundWrapper}>
          <Glow
            className={styles.backgroundGlow}
            glowImageSrc={isVisible ? backgroundGlowUrl : undefined}
            config={{
              scale: 1.28,
              intensity: 0.9,
              circleSize: 0.43,
              position: GlowPosition.CENTER,
              align: GlowAlignment.CENTER,
              type: 'IMAGE',
            }}
          />
        </div>
      </div>
      <div className={styles.foreground}>
        <div className={styles.foregroundWrapper}>
          <div className={styles.videoWrapper}>
            <Image
              className={styles.foregroundImage}
              source={foregroundImage}
            />
            <Media
              className={styles.video}
              ref={$media}
              sanityMedia={video}
              isLooping={video.isLooping}
              isMuted={video.isMuted}
              canAutoplay={false}
              willAutoplay={false}
            />
          </div>
        </div>
      </div>
      <div className={cn(styles.container)}>
        {sections.map((section: AppleTVSection, index: number) => (
          <div
            ref={(node: HTMLDivElement) => ($sections.current[index] = node)}
            className={styles.section}
            key={section._key}
          >
            <PortableText
              isAutoGrid={false}
              content={section.text}
              options={{
                block: {
                  eyebrow: {
                    className: styles.eyebrow,
                  },
                  titles: {
                    className: styles.titles,
                  },
                  bodies: {
                    className: styles.bodies,
                  },
                },
                types: {
                  'block.touts': {
                    className: styles.touts,
                  },
                },
              }}
            />
          </div>
        ))}
      </div>
    </section>
  );
};
