import { useRouter } from 'next/router';
import {
  ForwardedRef,
  forwardRef,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import shallow from 'zustand/shallow';

import SvgCaret from '~/assets/svg/caret.svg';
import { CMSLinkProps } from '~/components/atoms/link/types';
import useUIStore from '~/state/ui';
import { cn, killTimeline } from '~/utils';
import addToRefArray from '~/utils/addToRefArray';
import isBreakpointOrGreater from '~/utils/isBreakpointOrGreater';

import {
  closeMobileNavSubNavAnimation,
  openMobileNavSubNavAnimation,
} from '../animations';
import NavItem from '../navItem';
import useNavState from '../navState';
import styles from './styles.module.css';
import { CMSNavGroupLink } from './types';

const RESET_ACTIVE_DELAY = 500;

type Props = {
  navGroup: CMSNavGroupLink;
  id: string;
  onMouseOver: (id: string) => void;
  onMouseOut: () => void;
  onFocus: (id: string) => void;
  onBlur: () => void;
  onMouseOverMainNavItem: () => void;
  onMouseOutMainNavItem: () => void;
  onFocusMainNavItem: () => void;
  onBlurMainNavItem: () => void;
  disableHeaderActiveState?: boolean;
};

const NavGroup = (
  {
    navGroup,
    id,
    onMouseOver,
    onMouseOut,
    onFocus,
    onBlur,
    onMouseOverMainNavItem,
    onMouseOutMainNavItem,
    onFocusMainNavItem,
    onBlurMainNavItem,
    disableHeaderActiveState,
  }: Props,
  ref: ForwardedRef<HTMLDivElement>,
) => {
  const animation = useRef<GSAPTimeline>();
  const $triggerEl = useRef<HTMLDivElement>(null);
  const $subNavWrapper = useRef<HTMLUListElement>(null);
  const $subNavLinks = useRef<HTMLLIElement[]>([]);

  const [disableSubNavItemActive, setDisableSubNavItemActive] = useState(false);

  const router = useRouter();
  const breakpoint = useUIStore((state) => state.breakpoint);

  const resetActiveItemHighlight = useRef<ReturnType<typeof setTimeout>>();

  const [
    whichSubNavShowing,
    setWhichSubNavShowing,
    currentGroup,
    mobileNavOpen,
    isMobileNav,
  ] = useNavState(
    (state) => [
      state.whichSubNavShowing,
      state.setWhichSubNavShowing,
      state.currentGroup,
      state.mobileNavOpen,
      state.isMobileNav,
    ],
    shallow,
  );

  const isGroupPage = currentGroup == id;

  useEffect(() => {
    // clear animation on breakpoint change
    resetAnimation();
  }, [breakpoint]);

  function resetAnimation() {
    if (animation.current) {
      killTimeline(animation.current);
    }
  }

  useEffect(() => {
    // clear time of resetting link active state highlight
    clearActiveStateDelay();
  }, [router.asPath]);

  function clearActiveStateDelay() {
    if (resetActiveItemHighlight.current) {
      clearTimeout(resetActiveItemHighlight.current);
    }
  }

  useEffect(() => {
    // on desktop subnav, we show the current group subnav expanded by default
    if (isGroupPage && !isMobileNav) {
      setWhichSubNavShowing(id);
    }
  }, [setWhichSubNavShowing, isGroupPage, id, isMobileNav]);

  // only mobile/tablet
  const animateIn = useCallback(() => {
    animation.current = openMobileNavSubNavAnimation({
      $subNavWrapper,
      $subNavLinks,
    });
    animation.current?.play();
  }, []);

  // only mobile/tablet
  const animateOut = useCallback(() => {
    animation.current = closeMobileNavSubNavAnimation({
      $subNavWrapper,
      $subNavLinks,
    });
    animation.current?.play();
    $triggerEl.current?.blur();
  }, []);

  useEffect(() => {
    if (isMobileNav) {
      // on mobile, only show one subnav at once, based on id
      if (mobileNavOpen) {
        if (whichSubNavShowing === id) {
          animateIn();
        } else {
          animateOut();
        }
      } else {
        setWhichSubNavShowing(null);
        resetAnimation();
      }
    } else {
      resetAnimation();
    }
  }, [
    mobileNavOpen,
    whichSubNavShowing,
    id,
    animateOut,
    animateIn,
    isMobileNav,
    setWhichSubNavShowing,
  ]);

  const onClickGroup = useCallback(() => {
    if (whichSubNavShowing === id) {
      // if clicking a subnav that's already open, toggle it closed
      if (isMobileNav) setWhichSubNavShowing(null);
    } else {
      setWhichSubNavShowing(id);
    }
  }, [id, setWhichSubNavShowing, whichSubNavShowing, isMobileNav]);

  const onMouseOverSubNavItem = () => {
    if (breakpoint && !isBreakpointOrGreater(breakpoint, 'md')) return;
    clearActiveStateDelay();
    setDisableSubNavItemActive(true);
  };

  const onMouseOutSubNavItem = () => {
    if (breakpoint && !isBreakpointOrGreater(breakpoint, 'md')) return;
    resetActiveItemHighlight.current = setTimeout(() => {
      setDisableSubNavItemActive(false);
    }, RESET_ACTIVE_DELAY);
  };

  const shouldDisableHeaderActiveState = useMemo(() => {
    if (isMobileNav) {
      return !(
        whichSubNavShowing === id ||
        (whichSubNavShowing === null && isGroupPage)
      );
    } else {
      return (
        whichSubNavShowing !== id || (isGroupPage && disableHeaderActiveState)
      );
    }
  }, [
    disableHeaderActiveState,
    id,
    isGroupPage,
    whichSubNavShowing,
    isMobileNav,
  ]);

  return (
    <div
      className={styles.navGroup}
      aria-label={`${navGroup.groupLink?.label} navigation group`}
    >
      <div
        onMouseOver={() => onMouseOver(id)}
        onMouseOut={onMouseOut}
        onFocus={() => onFocus(id)}
        onBlur={onBlur}
        className={styles.navGroupItem}
        ref={$triggerEl}
      >
        <NavItem
          link={navGroup.groupLink}
          label={navGroup.label}
          icon={<SvgCaret className={styles.navGroupCaret} />}
          isGroupHeader={true}
          onMouseOver={onMouseOverMainNavItem}
          onMouseOut={onMouseOutMainNavItem}
          onFocus={onFocusMainNavItem}
          onBlur={onBlurMainNavItem}
          disableActiveState={shouldDisableHeaderActiveState}
          onClick={onClickGroup}
        />

        {/* subnav */}
        <div
          ref={ref}
          className={cn(
            styles.navGroupSubnav,
            whichSubNavShowing === id && styles.active,
          )}
        >
          <ul className={styles.navGroupSubnavItems} ref={$subNavWrapper}>
            {navGroup.group?.map((navItem: CMSLinkProps, i) => {
              return (
                <li
                  className={styles.subNavItemWrapper}
                  key={`${navItem._key}`}
                  ref={(el) => addToRefArray(el, $subNavLinks, i)}
                >
                  <NavItem
                    link={navItem}
                    className={styles.subNavItem}
                    isGroupPage={isGroupPage}
                    hidden={whichSubNavShowing !== id}
                    onMouseOver={onMouseOverSubNavItem}
                    onMouseOut={onMouseOutSubNavItem}
                    onFocus={onMouseOverSubNavItem}
                    onBlur={onMouseOutSubNavItem}
                    disableActiveState={disableSubNavItemActive}
                  />
                </li>
              );
            })}
          </ul>
        </div>
      </div>
    </div>
  );
};

export default forwardRef<HTMLDivElement, Props>(NavGroup);
