import Button from '~/components/atoms/Buttons/Ctas/Button';
import { cn, getUniqueId } from '~/utils';
import addToRefArray from '~/utils/addToRefArray';

import { getElementAttributesFromOptions } from '../../utils';
import getBlockClassesFromContent from '../../utils/getBlockClassesFromContent';
import getRefFromOptions from '../../utils/getRefFromOptions';
import { PortableTextButtonProps } from '../button/types';
import styles from './styles.module.css';
import { PortableTextButtonsProps } from './types';

const Buttons = ({
  module,
  value,
  typeName,
  typeCategory,
}: PortableTextButtonsProps) => {
  const optionsProps = getElementAttributesFromOptions(
    module,
    typeCategory,
    typeName,
  );
  const ref = getRefFromOptions(module, typeCategory, typeName);
  const { className } = optionsProps;
  const { _key, buttons } = value;

  // TODO: ensure this doesn't break columns

  const renderClass = getBlockClassesFromContent(
    _key,
    value,
    cn(className, styles.buttons),
    styles,
  );

  return (
    <div
      className={renderClass}
      ref={(element: HTMLDivElement) => ref && addToRefArray(element, ref)}
    >
      {buttons &&
        buttons.flatMap((value: PortableTextButtonProps) => {
          const buttonClassName = cn(value.button?.className, styles.button);
          const renderValue = { ...value.button };
          delete renderValue.className;
          return value?.button
            ? [
                <Button
                  key={getUniqueId()}
                  {...renderValue}
                  className={buttonClassName}
                  asNonInteractive={value?.linkDeleted}
                >
                  {renderValue.label}
                </Button>,
              ]
            : [];
        })}
    </div>
  );
};

export default Buttons;
