import { gsap } from 'gsap';
import {
  CSSProperties,
  MouseEvent,
  TouchEvent,
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react';

import Image from '~/components/atoms/image';
import Observer from '~/components/atoms/observer';
import PortableText from '~/components/molecules/portableText';
import useUIStore from '~/state/ui';
import { cn } from '~/utils';

import { CMSFancyTestimonialsProps, FancyTestimonial } from '../types';
import { testimonialCardsDragTimeline } from './animation';
import styles from './styles.module.css';
import { TouchRef } from './types';
import {
  disableDocumentScroll,
  enableDocumentScroll,
  getPointerCoords,
} from './utils';

const WINDOW_WIDTH_FALLBACK = 320;

const XsTestimonials = ({
  title,
  testimonials,
  text,
  className,
}: CMSFancyTestimonialsProps) => {
  const [currentIndex, setCurrentIndex] = useState<number>(0);
  const $wrapper = useRef<HTMLDivElement>(null);
  const $titleWrapper = useRef<HTMLUListElement>(null);
  const $cards = useRef<HTMLLIElement[]>(Array(testimonials.length));
  const $titles = useRef<HTMLLIElement[]>(Array(testimonials.length));

  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  const animation = useRef<GSAPTimeline>();
  const windowWidth = useUIStore((state) => state.windowWidth);

  const touch = useRef<TouchRef>({
    isActive: false,
    x: 0,
    y: 0,
    dx: 0,
    dy: 0,
    startX: 0,
    startY: 0,
  });

  const setAnimationProgressToIndex = useCallback(
    (index = 0) => {
      if (!animation.current) return;

      const progress = (1 / (testimonials.length - 1)) * index;

      gsap.to(animation.current, {
        progress,
        ease: 'power3.out',
        duration: 0.7,
      });
    },
    [testimonials.length],
  );

  const handleTouchStart = useCallback(
    (event: TouchEvent<HTMLOListElement> | MouseEvent<HTMLOListElement>) => {
      /**
       * event.preventDefault() is not available within passive events, so
       * disable scrolling on the document
       */
      disableDocumentScroll();

      const { x, y } = getPointerCoords(event);

      touch.current.x = x;
      touch.current.y = y;
      touch.current.startX = x;
      touch.current.startY = y;
      touch.current.isActive = true;
    },
    [],
  );
  const handleTouchMove = useCallback(
    (event: TouchEvent<HTMLOListElement> | MouseEvent<HTMLOListElement>) => {
      if (!touch.current.isActive) return;

      const { x, y } = getPointerCoords(event);

      touch.current.x = x;
      touch.current.y = y;
      touch.current.dx = touch.current.startX - x;
      touch.current.dy = touch.current.startY - y;

      const normalizedProgress =
        touch.current.dx / (windowWidth || WINDOW_WIDTH_FALLBACK);
      animation.current?.progress(
        (1 / (testimonials.length - 1)) * currentIndex +
          (1 / (testimonials.length - 1)) * normalizedProgress,
      );
    },
    [currentIndex, windowWidth, testimonials.length],
  );

  const handleTouchEnd = useCallback(() => {
    /**
     * Re-enable document scrolling once touch interaction has completed
     */
    enableDocumentScroll();

    touch.current.isActive = false;
    const normalizedProgress =
      touch.current.dx / (windowWidth || WINDOW_WIDTH_FALLBACK);
    let indexToSet = currentIndex;

    if (Math.abs(normalizedProgress) > 0.2) {
      const newIndex = Math.max(
        0,
        Math.min(
          currentIndex + Math.sign(normalizedProgress),
          testimonials.length - 1,
        ),
      );

      indexToSet = newIndex;
      setCurrentIndex(newIndex);
    }

    setAnimationProgressToIndex(indexToSet);
  }, [
    currentIndex,
    setAnimationProgressToIndex,
    testimonials.length,
    windowWidth,
  ]);

  const handleTitleClick = useCallback(
    (index = 0) => {
      setCurrentIndex(index);
      setAnimationProgressToIndex(index);
    },
    [setAnimationProgressToIndex],
  );

  useEffect(() => {
    animation.current = testimonialCardsDragTimeline({
      $titleWrapper,
      $titles,
      $cards,
    });
  }, [windowWidth]);

  return (
    <div
      ref={$wrapper}
      className={cn(styles.xsTestimonials, className)}
      style={{ '--total': testimonials.length } as CSSProperties}
    >
      <div className={styles.header}>
        <h4 className={styles.title}>{title}</h4>
        <p className={styles.counter}>
          {currentIndex + 1} / {testimonials.length}
        </p>
      </div>
      <Observer
        callback={updateIsInView}
        options={{ rootMargin: '200% 0%' }}
        className={styles.cardsDeckWrapper}
      >
        {/**
         * We are intercepting touchmove and mousemove events here for a
         * carousel, which is not an established aria-role so we must attach to
         * a non-interactive element
         */}
        {/* eslint-disable-next-line jsx-a11y/no-noninteractive-element-interactions */}
        <ol
          aria-live="polite"
          onMouseDown={handleTouchStart}
          onMouseMove={handleTouchMove}
          onMouseUp={handleTouchEnd}
          onTouchStart={handleTouchStart}
          onTouchMove={handleTouchMove}
          onTouchEnd={handleTouchEnd}
          onTouchCancel={handleTouchEnd}
          className={styles.cardsDeck}
        >
          {testimonials.map((testimonial: FancyTestimonial, index: number) => (
            <li
              aria-hidden={currentIndex !== index}
              ref={(node: HTMLLIElement) => ($cards.current[index] = node)}
              className={styles.card}
              style={
                {
                  '--index-xs-testimonial': index,
                  '--main-card-color':
                    testimonial.image.asset.darkMutedBackground,
                } as CSSProperties
              }
              key={testimonial._key}
            >
              <Image
                className={styles.cardMedia}
                source={testimonial.image}
                isDisplayed={isInView !== false}
              />
              <PortableText
                content={testimonial.text}
                className={styles.cardContent}
                options={{
                  block: {
                    titles: {
                      className: styles.cardContentTitles,
                    },
                    bodies: {
                      className: styles.cardContentBody,
                    },
                    bodySmall: {
                      className: styles.cardContentBody,
                    },
                  },
                  marks: {
                    em: {
                      className: styles.cardContentEm,
                    },
                  },
                }}
              />
            </li>
          ))}
        </ol>
        <ul ref={$titleWrapper} className={styles.testimonialTitles}>
          {testimonials.map((testimonial: FancyTestimonial, index: number) => (
            <li
              ref={(node: HTMLLIElement) => ($titles.current[index] = node)}
              className={cn(
                styles.testimonialTitleItem,
                currentIndex === index && styles.active,
              )}
              key={testimonial._key}
            >
              <button
                className={styles.testimonialTitle}
                style={
                  {
                    '--gradient-color-1':
                      testimonial.image?.asset?.darkMutedBackground,
                    '--gradient-color-2':
                      testimonial.image?.asset?.lightMutedBackground,
                  } as CSSProperties
                }
                onClick={() => handleTitleClick(index)}
              >
                {testimonial.label}
              </button>
            </li>
          ))}
        </ul>
      </Observer>
      <PortableText
        content={text}
        isAutoGrid={false}
        className={styles.content}
        options={{
          block: {
            titles: {
              className: styles.contentTitles,
            },
          },
        }}
      />
    </div>
  );
};

export default XsTestimonials;
