import imageFragment from '~/components/atoms/image/fragment';
import moduleConfig from '~/components/organisms/config';
import moduleFragmentDefault from '~/data/fragments/module.default';

const moduleFragments = [];

for (const key in moduleConfig) {
  const element = moduleConfig[key];
  if (typeof element !== 'undefined' && element) {
    const fragment = element.fragment || moduleFragmentDefault;
    moduleFragments.push(`
    _type == '${key}' => {
      ${fragment},
    }
    `);
  }
}

export const pageBase = `
  _id,
  title,
  'accentColor': accentColor.title,
  footerBackgroundImage { ${imageFragment} },
  'slug': slug.current,
  ogTitle,
  ogImage { ${imageFragment} },
  hideAnnouncementBanner
`;

const page = `
  ${pageBase},
  "modules": modules[]-> {
    ...coalesce(
      *[(^._type in path("module.**")) && _id == "drafts." + ^._id][0],
      *[(^._type in path("module.**")) && _id == ^._id][0],
      ){
        'slug': slug.current,
        ${moduleFragments},
      }
    }
    `;

export default page;
