import { CMSPortableTextCustomContent } from '~/components/molecules/portableText/types';

const isContentEmpty = (content: CMSPortableTextCustomContent): boolean => {
  if (content && content?.children?.length) {
    for (const child of content.children) {
      if (child.text && String(child.text).trim()) {
        return false;
      }
    }
  }
  return true;
};

const areChildrenEmpty = (
  content: CMSPortableTextCustomContent | CMSPortableTextCustomContent[],
): boolean => {
  if (Array.isArray(content)) {
    for (const contentItem of content) {
      return isContentEmpty(contentItem);
    }
  } else {
    return isContentEmpty(content);
  }

  return true;
};

const isPortableTextContentEmpty = (
  content: CMSPortableTextCustomContent | CMSPortableTextCustomContent[],
): boolean => {
  if (content) {
    return areChildrenEmpty(content);
  }
  return true;
};

export default isPortableTextContentEmpty;
