import { gsap } from 'gsap';
import {
  CSSProperties,
  ForwardedRef,
  forwardRef,
  useImperativeHandle,
  useRef,
} from 'react';

import GlowMedia from '~/components/molecules/glowMedia';
import PortableText from '~/components/molecules/portableText';
import useUIStore from '~/state/ui';
import { cn, useIsomorphicLayoutEffect as useLayoutEffect } from '~/utils';
import isBreakpointOrGreater from '~/utils/isBreakpointOrGreater';

import { CardSectionProps, CardSectionRef } from '../types';
import styles from './styles.module.css';

// normalized progress of card entering the viewport
const enterProgressTransformer = gsap.utils.pipe(
  gsap.utils.normalize(0, 0.5),
  gsap.utils.clamp(0, 1),
);

// normalized progress of card starting to scale down + fade out
const leaveProgressTransformer = gsap.utils.pipe(
  gsap.utils.normalize(0.5, 0.75),
  gsap.utils.clamp(0, 1),
);
// normalized progress of next card entering the viewport
const nextProgressTransformer = gsap.utils.pipe(
  gsap.utils.normalize(0.5, 1),
  gsap.utils.clamp(0, 1),
);

const CardSection = (
  { index, content, total, media, className, active }: CardSectionProps,
  ref: ForwardedRef<CardSectionRef>,
) => {
  const $element = useRef<HTMLLIElement>(null);
  const enterProgressSetter = useRef<(value: number) => void>();
  const leaveProgressSetter = useRef<(value: number) => void>();
  const nextProgressSetter = useRef<(value: number) => void>();

  const animation = useRef<GSAPTimeline>();

  const windowWidth = useUIStore((state) => state.windowWidth);
  const windowHeight = useUIStore((state) => state.windowHeight);
  const breakpoint = useUIStore((state) => state.breakpoint);
  const isComputedStyleComplete = useUIStore(
    (state) => state.isComputedStyleComplete,
  );

  useImperativeHandle(
    ref,
    () => ({
      get $element() {
        return $element;
      },
      setProgress(progress: number) {
        if (enterProgressSetter.current)
          enterProgressSetter.current(enterProgressTransformer(progress));
        if (leaveProgressSetter.current)
          leaveProgressSetter.current(leaveProgressTransformer(progress));
        if (nextProgressSetter.current)
          nextProgressSetter.current(nextProgressTransformer(progress));
      },
      setCarouselProgress(progress: number) {
        animation.current?.progress(progress);
      },
      getRect() {
        return $element.current?.getBoundingClientRect();
      },
    }),
    [],
  );

  useLayoutEffect(() => {
    enterProgressSetter.current = gsap.quickSetter(
      $element.current,
      '--card-enter-progress',
    ) as (value: number) => void;
    leaveProgressSetter.current = gsap.quickSetter(
      $element.current,
      '--card-leave-progress',
    ) as (value: number) => void;
    nextProgressSetter.current = gsap.quickSetter(
      $element.current,
      '--card-next-progress',
    ) as (value: number) => void;
  }, []);

  useLayoutEffect(() => {
    const rect = $element.current?.getBoundingClientRect();
    if (rect)
      gsap.set($element.current, {
        '--top-offset': `${
          ((windowHeight || window.innerHeight) - rect?.height) / 2
        }px`,
      });
  }, [windowWidth, windowHeight]);

  useLayoutEffect(() => {
    if (
      isComputedStyleComplete &&
      breakpoint &&
      !isBreakpointOrGreater(breakpoint, 'md')
    ) {
      // TODO: un-comment for fancier mobile carousel
      // animation.current = mobileTimeline($element, index);
    } else if (animation.current) {
      animation.current.kill();
    }
  }, [breakpoint, isComputedStyleComplete, index]);

  return (
    <li
      ref={$element}
      className={cn(
        className,
        styles.section,
        active && styles.active,
        index === 0 && styles.firstItem,
      )}
      style={
        {
          '--reverse-index': total - index - 1,
        } as CSSProperties
      }
    >
      <div
        className={styles.mediaContainer}
        style={
          {
            '--media-aspect-ratio':
              'asset' in media.sanityMedia
                ? media.sanityMedia.asset.aspectRatio
                : media.sanityMedia.aspectRatio,
          } as CSSProperties
        }
      >
        <GlowMedia
          glowConfig={{
            type: 'IMAGE',
            scale: 1.3,
            intensity: 0.8,
          }}
          glowBackgroundClassName={styles.glowBackground}
          contain={true}
          sanityMedia={media.sanityMedia}
          elementClassName={styles.mediaElement}
          containerClassName={styles.mediaContainerInGlow}
          className={styles.media}
        />
      </div>
      <PortableText
        isAutoGrid={false}
        content={content}
        className={styles.content}
        options={{
          block: {
            accents: {
              className: styles.accents,
            },
            bodies: {
              className: styles.bodies,
            },
            titles: {
              className: styles.titles,
            },
            title3: {
              className: styles.title3,
            },
            title4: {
              className: styles.title4,
            },
            bodySmall: {
              className: styles.bodySmall,
            },
          },
          types: {
            'block.buttons': {
              className: styles.buttons,
            },
            'block.stats': {
              className: styles.stats,
            },
            'block.touts': {
              className: styles.touts,
            },
          },
        }}
      />
    </li>
  );
};

export default forwardRef(CardSection);
