import { breakpoints } from '@frameio-bs/tokens';
import { CSSProperties } from 'react';

import { SpacerSize } from '~/types/index.d';

export type SpacerProps = {
  _key: string;
  _id?: string;
  _type: string;
  sizes?: SpacerSize[];
};

const toRem = (pixels: number): string => `${pixels / 10}rem`;

const getStyles = (
  sizes: SpacerSize[],
  halfSpacers: boolean,
  position: string,
) => {
  const style: Record<string, string> = {};
  if (sizes && sizes.length) {
    const allBreakpoint = sizes.find((size) => size.breakpoint === 'all');
    let lastHeight = 0;
    for (const breakpoint of breakpoints) {
      const height = allBreakpoint
        ? allBreakpoint.height
        : sizes.find((size) => size.breakpoint === breakpoint.name)?.height ||
          lastHeight;
      lastHeight = height;
      style[`--sp-h-${position}-${breakpoint.name}`] = toRem(
        halfSpacers ? height * 0.5 : height,
      );
    }
  }
  return style;
};

const getSpacersStyles = (
  beforeSizes: SpacerSize[] = [],
  afterSizes: SpacerSize[] = [],
  halfSpacers = true,
) => {
  if (beforeSizes.length === 0 && afterSizes.length === 0)
    return {} as CSSProperties;

  // reducing the length of this style since it'll take quite some space in the DOM
  // --spacer-height-BREAKPOINT-before/after
  // -> --sp-h-BREAKPOINT-b/a
  const beforeStyles = getStyles(beforeSizes, halfSpacers, 'b');
  const afterStyles = getStyles(afterSizes, halfSpacers, 'a');

  return {
    ...beforeStyles,
    ...afterStyles,
  } as CSSProperties;
};

export default getSpacersStyles;
