import { gsap } from 'gsap';
import React, {
  CSSProperties,
  ForwardedRef,
  forwardRef,
  useCallback,
  useEffect,
  useId,
  useImperativeHandle,
  useRef,
  useState,
} from 'react';
import useStateRef from 'react-usestateref';

import { cn } from '~/utils';

import GlowWebglFactory from './helpers/GlowWebglFactory';
import styles from './styles.module.css';
import {
  GlowAlignment,
  GlowBaseConfig,
  GlowImageSource,
  GlowPosition,
  GlowProps,
  GlowRef,
  GlowType,
} from './types.d';

const DEFAULT_BLUR_AMOUNT = 120;

const DEFAULT_CONFIG: GlowBaseConfig = {
  blurAmount: DEFAULT_BLUR_AMOUNT,
  position: GlowPosition.CENTER,
  align: GlowAlignment.CENTER,
  circleSize: 0.35,
  imageScale: 1,
};

const Glow = (
  {
    className,
    backgroundClassName,
    glowImageSrc,
    children,
    hasMedia,
    style,
    type = GlowType.BAKED_CANVAS,
    config: configProps,
  }: GlowProps,
  ref: ForwardedRef<GlowRef>,
) => {
  const $wrapper = useRef<HTMLDivElement>(null);
  const $background = useRef<HTMLDivElement>(null);
  const $canvas = useRef<HTMLCanvasElement>(null);
  const $sourceElement = useRef<HTMLImageElement | HTMLVideoElement>();

  const id = useId();

  const [isReady, setIsReady] = useState<boolean>(false);
  const [hasRendered, setHasRendered, hasRenderedRef] =
    useStateRef<boolean>(false);
  const shouldRender = useRef(true);

  const setIsReadyIfDisplayed = useCallback((isReady: boolean) => {
    gsap.ticker.add(
      () => {
        if ($wrapper.current) {
          const isDisplayed =
            getComputedStyle($wrapper.current, null).display !== 'none';

          if (isDisplayed) {
            setIsReady(isReady);
          }
        }
      },
      true,
      true,
    );
  }, []);

  useImperativeHandle(
    ref,
    () => {
      return {
        $wrapper,
        startGlow: ($element: HTMLImageElement | HTMLVideoElement) => {
          // if a glow image Src was specified, then use it instead of the actual image (maybe a smaller image is required if the actual one is too bef)

          if (!glowImageSrc) {
            $sourceElement.current = $element;
          }
          setIsReadyIfDisplayed(true);
        },
        startGlowFromSrc: (src: string) => {
          const $image = new Image();
          $image.src = src;
          $image.crossOrigin = 'anonymous';
          $sourceElement.current = $image;

          $image.onload = () => {
            setIsReadyIfDisplayed(true);
          };
        },
      };
    },
    [glowImageSrc, setIsReadyIfDisplayed],
  );

  useEffect(() => {
    if (
      type === GlowType.BAKED_CANVAS &&
      isReady &&
      shouldRender.current &&
      !hasRendered
    ) {
      const config = {
        ...DEFAULT_CONFIG,
        ...configProps,
      };
      if (
        config.type === 'IMAGE' &&
        $sourceElement.current &&
        $sourceElement.current instanceof HTMLImageElement
      ) {
        (config as GlowImageSource).image = $sourceElement.current;
      }
      if (
        !$canvas.current ||
        (config.type === 'IMAGE' && typeof config.image === 'undefined')
      ) {
        return;
      }

      const context = $canvas.current.getContext('2d');

      if (context) {
        const source = { ...config };

        let rect: DOMRect | undefined;

        const measureGlowCanvas = () => {
          rect = $canvas.current?.getBoundingClientRect();
        };
        gsap.ticker.add(measureGlowCanvas, true, true);

        const requestGlowFromFactory = () => {
          if ($canvas.current && rect) {
            $canvas.current.width = rect.width;
            $canvas.current.height = rect.height;
            GlowWebglFactory.requestGlow({
              source,
              targetCanvasContext: context,
              config: {
                glowPosition: config.position,
                glowCircleSize: config.circleSize,
                glowImageScale: config.imageScale,
              },
            });
            shouldRender.current = false;
            setHasRendered(true);
          }
        };
        gsap.ticker.add(requestGlowFromFactory, true);
      }
    }

    return () => {
      shouldRender.current = true;
    };
  }, [hasRendered, isReady, type, configProps, setHasRendered]);

  useEffect(() => {
    if (configProps.type === 'COLOR' || configProps.type === 'GRADIENT') {
      // don't need to wait for image load since only need colors or gradient
      setIsReadyIfDisplayed(true);
    }
  }, [configProps, hasMedia, $sourceElement, setIsReadyIfDisplayed]);

  useEffect(() => {
    if (glowImageSrc) {
      const $image = new Image();
      $image.src = glowImageSrc;
      $image.crossOrigin = 'anonymous';
      $sourceElement.current = $image;

      $image.onload = () => {
        setIsReadyIfDisplayed(true);
      };
    }
  }, [glowImageSrc, setIsReadyIfDisplayed]);

  useEffect(() => {
    let unsubscribe: undefined | (() => void);
    async function initIntersectionObserver() {
      if ($background.current === null || $canvas.current === null) return;
      $background.current.setAttribute('data-id', id);
      const Observer = (
        await import('./helpers/glowIntersectionObserverSingleton')
      ).default;
      unsubscribe = Observer.subscribe($background.current, id, {
        isVisible: () => {
          if (hasRenderedRef.current) {
            gsap.set($canvas.current, { display: 'block' });
          }
        },
        isHidden: () => {
          if (hasRenderedRef.current) {
            gsap.set($canvas.current, { display: 'none' });
          }
        },
      });
    }
    initIntersectionObserver();

    return () => {
      if (unsubscribe) {
        unsubscribe();
      }
    };
    // We want to avoid any rerun for this effect
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div
      ref={$wrapper}
      className={cn(styles.glowWrapper, className)}
      style={style}
    >
      <div
        ref={$background}
        className={cn(
          styles.glowBackground,
          backgroundClassName,
          styles[configProps.position || GlowPosition.CENTER],
          hasRendered && styles.displayed,
          !children && styles.baked,
          styles[configProps.align || GlowAlignment.CENTER],
        )}
        style={
          {
            '--glow-scale': hasRendered ? configProps.scale || 1.5 : 1,
            '--glow-intensity': configProps.intensity,
          } as CSSProperties
        }
      >
        <canvas className={styles.glowCanvas} ref={$canvas}></canvas>
      </div>
      {children}
    </div>
  );
};

export default forwardRef<GlowRef, GlowProps>(Glow);
