import debounce from 'lodash/debounce';
import { useRouter } from 'next/router';
import { RefObject, useEffect, useRef, useState } from 'react';
import shallow from 'zustand/shallow';

import UIStore from '~/state/ui';
import ScrollPosition from '~/utils/domEvents/ScrollPosition';
import { ScrollPositionSubscriber } from '~/utils/domEvents/ScrollPosition.d';

import translateValues from '../getTranslateValues';
import { onProgressCallback } from './useScrollProgress.d';

// Defining multiples functions in the same file is not ideal but this is a very small and specific one
const isInView = (progress: number) => progress > 0 && progress < 1;

const useScrollProgress = (
  $element: RefObject<HTMLElement>,
  onProgress?: onProgressCallback,
  options?: {
    $garageDoorElement?: undefined | RefObject<HTMLElement>;
    startOnMiddleOfScreen?: boolean;
    finishOnMiddleOfScreen?: boolean;
  },
) => {
  const router = useRouter();
  const currentProgress = useRef<number | null>(null);
  const isScrollLocked = useRef(false);

  const [restartObservers, updateRestartObservers] = useState(Date.now());

  const { $garageDoorElement, startOnMiddleOfScreen, finishOnMiddleOfScreen } =
    {
      $garageDoorElement: undefined,
      startOnMiddleOfScreen: false,
      finishOnMiddleOfScreen: false,
      ...options,
    };

  useEffect(() => {
    let mounted = true;
    const unsubscribeScrollLock = UIStore.subscribe(
      (state) => state.isScrollLocked,
      (isLocked) => {
        if (isScrollLocked) {
          isScrollLocked.current = isLocked;
        }
      },
    );

    // In order to reset observers when the scroll jumps due to a word search
    // or an anchor change, we check the scroll position
    let prevScrollY = UIStore.getState().scrollY || 0;
    const debouncedUpdate = debounce(() => {
      if (mounted) {
        updateRestartObservers(Date.now());
      }
    }, 500);

    const handleScroll: ScrollPositionSubscriber = (position) => {
      // Arbitrary window height value times 1.2 to check if the delta is too big to be a regular scroll
      if (
        Math.abs((position.y || 0) - prevScrollY) >
        (UIStore.getState().windowHeight || 1) * 1.2
      ) {
        debouncedUpdate();
      }

      prevScrollY = position.y || 0;
    };

    const unsubscribeScrollPosition = ScrollPosition.subscribe(handleScroll);

    return () => {
      unsubscribeScrollLock();
      unsubscribeScrollPosition();
      mounted = false;
    };
  }, []);

  const returnFunction = () => {
    currentProgress.current = null;
  };

  useEffect(() => {
    if (isScrollLocked.current) {
      return returnFunction;
    }
    let total: number;
    if ($element.current || $garageDoorElement?.current) {
      let $usedElement = $element;
      if ($garageDoorElement && $garageDoorElement.current) {
        $usedElement = $garageDoorElement;
      }
      if ($usedElement.current) {
        let unsubscribe: (() => void) | null = null;
        let unsubscribeScroll: (() => void) | null = null;
        let boundingClientRect: DOMRectReadOnly | null = null;

        const observerCallback = (entries: IntersectionObserverEntry[]) => {
          const entry = entries[0];

          // That's the top of the element (because the observer is async, the top on the boundingrect
          // is not the pixel perfect value to we correct it with the scroll position)

          if (entry.isIntersecting) {
            boundingClientRect = entry.boundingClientRect;
            const windowHeight = UIStore.getState().windowHeight || 0;
            const top = (ScrollPosition.y as number) + boundingClientRect.top;
            total =
              boundingClientRect.height + Math.min(windowHeight as number, top);

            let startOffset = 0;

            if ($garageDoorElement) {
              // If the element is being revealed by a garage door, we start the progress later as
              // it will be hidden for as long as a window height first

              startOffset = Math.min(windowHeight, top);
              const isElementSmallerThanWindowHeight =
                boundingClientRect.height - windowHeight * 2 < 0;
              total = boundingClientRect.height;

              // TODO: Quick fix for having translate values messing up useScrollProgress start position
              if ($garageDoorElement.current) {
                const translate = translateValues($garageDoorElement.current);
                startOffset -=
                  $garageDoorElement.current && translate ? translate.y : 0;
              }
              if (isElementSmallerThanWindowHeight) {
                startOffset =
                  startOffset + (windowHeight - boundingClientRect.height);
                total = total + (windowHeight - boundingClientRect.height);
              }
            }

            if (startOnMiddleOfScreen) {
              const offset = Math.min(
                windowHeight,
                windowHeight / 2 + boundingClientRect.height / 2,
              );
              startOffset += offset;
              total -= offset;
            }

            if (finishOnMiddleOfScreen) {
              const offset = Math.min(
                windowHeight,
                windowHeight / 2 + boundingClientRect.height / 2,
              );
              total -= offset;
            }

            // Our callback to every scroll value change in the store
            const updateProgress = () => {
              if (isScrollLocked.current) {
                return returnFunction;
              }
              if (boundingClientRect) {
                const scrollY = ScrollPosition.y || 0;

                const progress =
                  ((scrollY as number) +
                    Math.min(windowHeight, top) -
                    top -
                    startOffset) /
                  total;

                currentProgress.current = Math.max(Math.min(progress, 1), 0);
                if (onProgress) {
                  onProgress(
                    currentProgress.current,
                    isInView(currentProgress.current) ||
                      (top < windowHeight && scrollY < windowHeight),
                    total,
                  );
                }
              }
            };
            unsubscribe = UIStore.subscribe(
              (state) => [
                state.windowHeight as number,
                state.windowWidth as number,
              ],
              updateProgress,
              { equalityFn: shallow },
            );

            unsubscribeScroll = ScrollPosition.subscribe(updateProgress, {
              fireImmediately: true,
            });
          } else {
            // If we're initializing the progress for the first time (e.g. page load)
            if (currentProgress.current === null) {
              // If the top if positive, the div is below so the progress is 0. Otherwise it's 1
              currentProgress.current =
                entry.boundingClientRect.top > 0 ? 0 : 1;
              if (onProgress) {
                onProgress(
                  currentProgress.current,
                  isInView(currentProgress.current),
                  total,
                );
              }
            }
            if (unsubscribe instanceof Function) {
              unsubscribe();
            }
            if (unsubscribeScroll instanceof Function) {
              unsubscribeScroll();
            }
            unsubscribe = null;
            unsubscribeScroll = null;
          }
        };

        const observer = new IntersectionObserver(observerCallback);
        observer.observe($usedElement.current);
        return () => {
          currentProgress.current = null;
          observer.disconnect();
          if (unsubscribe instanceof Function) {
            unsubscribe();
          }
          if (unsubscribeScroll instanceof Function) {
            unsubscribeScroll();
          }
          returnFunction();
        };
      }
    }
    return returnFunction;
  }, [
    router,
    $element,
    onProgress,
    $garageDoorElement,
    finishOnMiddleOfScreen,
    startOnMiddleOfScreen,
    isScrollLocked,
    restartObservers,
  ]);
};

export default useScrollProgress;
