import { ComponentType } from 'react';

import { cn } from '~/utils';

import Block from '../blocks/Block';
import { marksConfig } from '../config';
import defaultStyles from '../styles.module.css';
import {
  BlockElementProps,
  BlockTemplateConfigProps,
  JsxRenderProps,
  MarksRecord,
  PortableTextProps,
} from '../types';
import emptyRefArray from './emptyRefArray';
import getElementPropsFromOptions from './getElementPropsFromOptions';

// create block elements (typography - example: h1-h6, body sizes, label)

const makeMarks = (module: PortableTextProps): MarksRecord => {
  const { isAutoStyles = true } = module;
  const config = marksConfig;
  const marksRecord: MarksRecord = {};

  // clear stale refs
  for (const blockName in config) {
    emptyRefArray(module, 'marks', blockName);
  }

  // create blocks
  for (const marksTypeKey in config) {
    const marksType = config[marksTypeKey];
    if (typeof marksType !== 'undefined' && module) {
      marksRecord[marksTypeKey] = (props: JsxRenderProps) => {
        const blockKey = props.value?._key || '';
        const blockProps: BlockTemplateConfigProps = {
          _key: blockKey,
          blockName: marksTypeKey,
          module,
          className: isAutoStyles ? defaultStyles[marksTypeKey] : undefined,
          tagName: marksType.tagName,
          render: marksType.render,
        };

        if (blockProps?.render) {
          const CustomBlockRender =
            blockProps.render as ComponentType<JsxRenderProps>;
          const { blockName, className } = blockProps;

          const externalOptionProps: BlockElementProps | null =
            getElementPropsFromOptions(module, 'marks', blockName);

          const renderClass: string = cn(
            externalOptionProps?.className,
            props.className,
            className,
          );

          return (
            <CustomBlockRender className={renderClass} {...props}>
              {props.children}
            </CustomBlockRender>
          );
        }
        return <Block {...blockProps}>{props.children}</Block>;
      };
    }
  }

  return marksRecord;
};

export default makeMarks;
