import { useCallback, useEffect, useRef } from 'react';

import { VideoRef } from '~/components/atoms/video/types';
import Media from '~/components/molecules/media';

import styles from './styles.module.css';
import { CarouselSlideProps } from './types';

const CarouselSlide = ({
  media,
  isActive,
  index,
  onReady,
  isInView,
}: CarouselSlideProps) => {
  const ref = useRef<HTMLElement | VideoRef>(null);

  // We need to reset the video when the slide changes
  // so we can autoplay the video again from the start
  useEffect(() => {
    if (
      !isActive &&
      media.sanityMedia?.mediaType === 'video' &&
      ref.current &&
      'restart' in ref.current
    ) {
      ref.current.restart();
    }
  }, [isActive, media.sanityMedia?.mediaType]);

  const onMediaReady = useCallback(
    (element: HTMLImageElement | HTMLVideoElement) => {
      if (onReady) {
        onReady(element, index);
      }
    },
    [index, onReady],
  );

  return (
    <Media
      ref={ref}
      className={styles.media}
      sanityMedia={media.sanityMedia}
      canAutoplay={isActive}
      onReady={onMediaReady}
      isDisplayed={isInView}
    />
  );
};

export default CarouselSlide;
