import DriftButton from '~/components/atoms/Buttons/Ctas/DriftButton';
import { cn, getUniqueId } from '~/utils';
import addToRefArray from '~/utils/addToRefArray';

import { CMSPortableTextCustomContent } from '../../types';
import { getElementAttributesFromOptions } from '../../utils';
import getBlockClassesFromContent from '../../utils/getBlockClassesFromContent';
import getRefFromOptions from '../../utils/getRefFromOptions';
import { PortableTextDriftButtonProps } from './DriftButton.types.d';

const Buttons = ({
  module,
  value,
  typeName,
  typeCategory,
}: PortableTextDriftButtonProps) => {
  const optionsProps = getElementAttributesFromOptions(
    module,
    typeCategory,
    typeName,
  );
  const ref = getRefFromOptions(module, typeCategory, typeName);
  const { className } = optionsProps;
  const { _key, button } = value;
  const classQueryValue = {
    _key: value._key,
    button: value.button,
  } as CMSPortableTextCustomContent;

  const renderClass = getBlockClassesFromContent(
    _key,
    classQueryValue,
    cn(className),
  );

  const renderValue = { ...button.button };
  return (
    <div
      className={renderClass}
      ref={(element: HTMLDivElement) => ref && addToRefArray(element, ref)}
    >
      <DriftButton key={getUniqueId()} {...renderValue} />
    </div>
  );
};

export default Buttons;
