import { gsap } from 'gsap';
import { CSSProperties, useCallback, useRef } from 'react';

import { GlowPosition } from '~/components/molecules/glow/types.d';
import PortableText from '~/components/molecules/portableText';
import MediaWithTextContent from '~/components/organisms/mediaWithText/mediaWithTextContent';
import { MediaWithTextContentRef } from '~/components/organisms/mediaWithText/mediaWithTextContent/types';
import useUIStore from '~/state/ui';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import {
  cn,
  getSpacersStyles,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';
import isBreakpointOrGreater from '~/utils/isBreakpointOrGreater';

import ImageSequence from '../../imageSequence';
import getImageSequenceHeight from '../../imageSequence/helpers/getImageSequenceHeight';
import { fitsMap } from '../../imageSequence/types.d';
import styles from './styles.module.css';
import { LaptopCloseProps } from './types';

const LaptopClose = ({
  sequence,
  top,
  garageDoorRevealParent,
  aboveMediaWithText,
  hiddenText,
  hasSpacers,
  background,
  belowText,
  className,
  isReverseGarageDoor,
  accentColor,
  slug,
}: LaptopCloseProps) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);

  sequence.speed = 2;
  const elementHeight = getImageSequenceHeight({
    numberOfFrames: sequence.frames.length,
    speed: sequence.speed,
  });

  const mediaRef = useRef<MediaWithTextContentRef>(null);

  const $element = useRef<HTMLDivElement>(null);
  const $hiddenText = useRef<HTMLDivElement>(null);
  const $middleElement = useRef<HTMLDivElement>(null);
  const $mediaContainer = useRef<HTMLDivElement>(null);

  const middleTextTween = useRef<GSAPTween>();

  const breakpoint = useUIStore((state) => state.breakpoint);

  const progressSetter = useRef<(value: number) => void>();

  useLayoutEffect(() => {
    progressSetter.current = gsap.quickSetter(
      $element.current,
      '--media-with-text-progress',
    ) as (value: number) => void;
  }, []);

  const onMediaProgress = useCallback(
    (progress: number) => {
      if (progressSetter.current && !isReverseGarageDoor) {
        progressSetter.current(progress);
      }
    },
    [isReverseGarageDoor],
  );

  useLayoutEffect(() => {
    if (
      $hiddenText.current &&
      breakpoint &&
      isBreakpointOrGreater(breakpoint, 'sm')
    ) {
      gsap.set($hiddenText.current, { '--progress': 0 });

      middleTextTween.current = gsap.to($hiddenText.current, {
        '--progress': 1,
        paused: true,
      });
    } else {
      gsap.set($hiddenText.current, { clearProps: '--progress' });
    }
  }, [breakpoint]);

  const onProgress = useCallback(
    (progress: number) => {
      if (breakpoint && isBreakpointOrGreater(breakpoint, 'sm')) {
        const hiddenTextStart = 0.82;
        const hiddenTextEnd = 0.87;
        const normalizedTextProgress = Math.min(
          Math.max(
            (progress - hiddenTextStart) / (hiddenTextEnd - hiddenTextStart),
            0,
          ),
          1,
        );
        if (middleTextTween.current) {
          middleTextTween.current.progress(normalizedTextProgress);
        }
      }
    },
    [breakpoint],
  );

  useScrollProgress($mediaContainer, mediaRef.current?.onProgress, {
    $garageDoorElement: garageDoorRevealParent,
  });

  useScrollProgress($middleElement, onProgress, {
    $garageDoorElement: garageDoorRevealParent,
    finishOnMiddleOfScreen: true,
    startOnMiddleOfScreen: true,
  });

  return (
    <section
      className={cn(
        styles.laptopCloseImageSequence,
        background && styles.hasBackground,
        className,
        hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        hasSpacers?.after && decorationsStyles.hasSpacerAfter,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
          '--image-sequence-height': elementHeight,
          '--is-revealed':
            typeof garageDoorRevealParent !== 'undefined' ? 0 : 1,
        } as CSSProperties
      }
      id={slug}
      ref={$element}
    >
      {aboveMediaWithText && (
        <div ref={$mediaContainer} className={styles.mediaContainer}>
          <MediaWithTextContent
            onMediaProgress={onMediaProgress}
            {...aboveMediaWithText}
            textContentClassName={styles.mediaText}
            mediaWrapperClassName={styles.mediaWrapper}
            ref={mediaRef}
            glowConfig={{
              type: 'COLOR',
              color: '#B76A3F',
              intensity: 0.4,
              scale: 1.35,
              position: GlowPosition.LEFT_CENTER,
            }}
          />
        </div>
      )}
      <div className={styles.middleContent} ref={$middleElement}>
        <ImageSequence
          className={styles.sequence}
          canvasClassName={styles.canvas}
          sequence={sequence}
          top={top}
          garageDoorRevealParent={garageDoorRevealParent}
          fit={fitsMap.CONTAIN}
          // startOnMiddleOfScreen={true}
          finishOnMiddleOfScreen={true}
        />
        {hiddenText && (
          <div className={styles.hiddenTextWrapper}>
            <div className={styles.hiddenTextGridHolder}>
              <PortableText
                isAutoGrid={true}
                className={styles.hiddenText}
                content={hiddenText}
                ref={$hiddenText}
                animate={false}
                options={{
                  block: {
                    eyebrow: {
                      className: styles.hiddenTextEyebrow,
                    },
                    title2: {
                      className: styles.hiddenTextTitle2,
                    },
                  },
                  types: {
                    'block.stats': {
                      className: styles.stats,
                    },
                  },
                }}
              />
            </div>
          </div>
        )}
      </div>

      {belowText && (
        <PortableText
          content={belowText}
          className={styles.belowText}
          isAutoGrid={true}
        />
      )}
    </section>
  );
};

export default LaptopClose;
