import { cn } from '~/utils';
import addToRefArray from '~/utils/addToRefArray';

import {
  getBlockClassesFromContent,
  getElementAttributesFromOptions,
} from '../../utils';
import getRefFromOptions from '../../utils/getRefFromOptions';
import blockStyles from './styles.module.css';
import { PortableTextStatsProps } from './types';

const Stats = ({
  module,
  value,
  typeName,
  typeCategory,
}: PortableTextStatsProps) => {
  const optionsProps = getElementAttributesFromOptions(
    module,
    typeCategory,
    typeName,
  );
  const { className } = optionsProps;
  const { _key, stats, size, moduleStyles = null } = value;
  const ref = getRefFromOptions(module, typeCategory, typeName);

  const countClass = blockStyles[`with${stats.length}Stats`];
  const renderClass = getBlockClassesFromContent(
    _key,
    value,
    cn(
      className,
      countClass,
      blockStyles.stats,
      size === 'normal' && blockStyles.isNormal,
      size === 'large' && blockStyles.isLarge,
      size === 'largeMobile' && blockStyles.isLargeMobile,
    ),
    [blockStyles, moduleStyles],
  );

  return (
    <div
      className={renderClass}
      ref={(element: HTMLDivElement) => ref && addToRefArray(element, ref)}
    >
      {stats.map((stat) => {
        const { _key, value, valueSuffix, label } = stat;

        return (
          <p className={blockStyles.stat} key={_key}>
            <span className={blockStyles.valueAndSuffix}>
              <span
                className={cn(
                  size === 'large' && blockStyles.valueLarge,
                  size === 'largeMobile' && blockStyles.valueLargeMobile,
                  size === 'normal' && blockStyles.value,
                )}
              >
                {value}
              </span>
              <span
                className={cn(
                  size === 'large' && blockStyles.valueSuffixLarge,
                  size === 'largeMobile' && blockStyles.valueSuffixLargeMobile,
                  size === 'normal' && blockStyles.valueSuffix,
                )}
              >
                {valueSuffix}
              </span>
            </span>
            <span
              className={cn(
                size === 'large' && blockStyles.labelLarge,
                size === 'largeMobile' && blockStyles.labelLargeMobile,
                size === 'normal' && blockStyles.label,
              )}
            >
              {label}
            </span>
          </p>
        );
      })}
    </div>
  );
};

export default Stats;
