import { gsap } from 'gsap';
import {
  createRef,
  CSSProperties,
  KeyboardEvent,
  useCallback,
  useRef,
  useState,
} from 'react';

import Observer from '~/components/atoms/observer';
import Glow from '~/components/molecules/glow';
import { GlowType } from '~/components/molecules/glow/types.d';
// import SearchField from '~/components/atoms/searchField';
import LogoTextItem from '~/components/molecules/logoTextItem';
import { CMSLogoTextItem } from '~/components/molecules/logoTextItem/types.d';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import {
  cn,
  getSpacersStyles,
  titleToHash,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';

import IntegrationsModal from './integrationsModal';
import styles from './styles.module.css';
import { CMSIntegrationGroup, CMSModuleIntegrationsTable } from './types';
import { useHighlightReference } from './useHighlightReference';

const IntegrationsTable = ({
  title,
  groups,
  background,
  hasSpacers,
  accentColor,
  className,
  slug,
}: CMSModuleIntegrationsTable) => {
  // dynamically creates a reference for all "groups" coming from "module" props to be used to calculate which one is closest to the top
  const groupRefs = useRef(groups.map(() => createRef()));
  const $integrationTable = useRef<HTMLDivElement>(null);
  const $integrationTableInner = useRef<HTMLDivElement>(null);
  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  // hook that calculates which menu should be selected
  const selected = useHighlightReference(groupRefs.current, [
    $integrationTable,
    $integrationTableInner,
  ]);

  // TODO: requires clarification on questions on https://frame-io.atlassian.net/browse/FMWR-72
  // const handleSearch = (term: string) => {};

  const progressSetter = useRef<(value: string) => void>();
  const $keyline = useRef<HTMLDivElement>(null);
  const onProgress = useCallback((progress: number) => {
    if (progressSetter.current)
      progressSetter.current(`${Math.min(100, progress * 300)}%`);
  }, []);

  useLayoutEffect(() => {
    progressSetter.current = gsap.quickSetter(
      $keyline.current,
      '--keyline-scale',
    ) as (value: string) => void;
  }, []);

  useScrollProgress($keyline, onProgress);

  const [selectedIntegrationGroup, setSelectedIntegrationGroup] = useState<
    CMSLogoTextItem[] | null
  >(null);

  const [selectedIntegrationItemIndex, setSelectedIntegrationItemIndex] =
    useState<number | null>(null);

  const onClick = (groupIndex: number, itemIndex: number) => {
    onSelectItem(groupIndex, itemIndex);
  };

  const handleKeyDown = (
    event: KeyboardEvent<HTMLButtonElement>,
    groupIndex: number,
    itemIndex: number,
  ) => {
    if (event.code === 'Enter') {
      onSelectItem(groupIndex, itemIndex);
    }
  };

  const onSelectItem = (groupIndex: number, itemIndex: number) => {
    setSelectedIntegrationGroup(groups[groupIndex].logoTextItems);
    setSelectedIntegrationItemIndex(itemIndex);
  };

  const onClose = () => {
    setSelectedIntegrationGroup(null);
    setSelectedIntegrationItemIndex(null);
  };

  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);

  return (
    <section
      ref={$integrationTable}
      className={cn(
        styles.integrationTable,
        background && styles.hasBackground,
        className,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <Glow
        className={styles.topBackGlow}
        type={GlowType.BAKED_CANVAS}
        config={{
          color: '#3F404F',
          intensity: 0.2,
          type: 'COLOR',
          scale: 1,
        }}
      />
      <Glow
        className={styles.backGlow}
        type={GlowType.BAKED_CANVAS}
        config={{ color: '#ffffff', intensity: 0.1, type: 'COLOR', scale: 1 }}
      />
      <div
        ref={$integrationTableInner}
        className={cn(
          styles.integrationTableInner,
          hasSpacers?.before && decorationsStyles.hasSpacerBefore,
          hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        )}
      >
        <div className={styles.header}>
          <h3 className={styles.headerTitle}>{title}</h3>
          <div className={styles.headerSearch}>{/* <SearchField /> */}</div>
          <div ref={$keyline} className={styles.keyline}></div>
        </div>

        <aside className={styles.anchorNav}>
          <nav className={styles.anchorNavItems}>
            {groups.map((group: CMSIntegrationGroup, index: number) => (
              <a
                key={group._key}
                href={`#integration-${titleToHash(group.name)}`}
                className={cn(
                  styles.anchorNavItem,
                  selected === index && styles.active,
                )}
              >
                {group.name}
              </a>
            ))}
          </nav>
        </aside>

        <Observer
          callback={updateIsInView}
          options={{ rootMargin: '100% 0%' }}
          className={styles.groups}
        >
          {groups.map((group: CMSIntegrationGroup, groupIndex: number) => (
            <div
              ref={groupRefs.current[groupIndex]}
              id={`integration-${titleToHash(group.name)}`}
              key={group._key}
              className={styles.groupContainer}
            >
              <h4 className={styles.groupTitle}>{group.name}</h4>
              <div className={styles.groupItems}>
                {group.logoTextItems &&
                  group.logoTextItems.map((item, itemIndex) => (
                    <button
                      key={item._key}
                      className={styles.groupItem}
                      onClick={() => onClick(groupIndex, itemIndex)}
                      onKeyDown={(event) =>
                        handleKeyDown(event, groupIndex, itemIndex)
                      }
                    >
                      <LogoTextItem
                        {...item}
                        labelClassName={styles.label}
                        className={styles.integrationItem}
                        isInView={isInView !== false}
                      />
                    </button>
                  ))}
              </div>
            </div>
          ))}
        </Observer>
      </div>
      {selectedIntegrationGroup && selectedIntegrationItemIndex !== null && (
        <IntegrationsModal
          selectedIntegrationGroup={selectedIntegrationGroup}
          selectedIntegrationItemIndex={selectedIntegrationItemIndex}
          onClose={onClose}
        />
      )}
    </section>
  );
};

export default IntegrationsTable;
