import portableTextFragment from '~/components/molecules/portableText/fragment';
import baseFragment from '~/components/organisms/fragment.base';
import mediaWithTextFragment from '~/components/organisms/mediaWithText/fragment';

const fragment = `
  ${baseFragment},
  layout,
  "sequence": imageSequence {
    label,
    "frames": *[_type == 'sanity.imageAsset' && ^.label in opt.media.tags[]->name.current] | order(originalFilename asc) {
      _id
    },
    crop,
    hotspot,
    "speed": 15 - coalesce(defaultSpeed, 1),
    "dimensions": (*[_type == 'sanity.imageAsset' && ^.label in opt.media.tags[]->name.current] | order(originalFilename asc)[0]).metadata.dimensions
  },
  startOnMiddleOfScreen,
  "customContent": {
    defined(aboveMediaWithText) => {aboveMediaWithText {${mediaWithTextFragment}}},
    defined(aboveText) => {${portableTextFragment('aboveText')}},
    defined(middleText) => {${portableTextFragment('middleText')}},
    defined(belowText) => {${portableTextFragment('belowText')}}
  }
`;
export default fragment;
