import { PortableTextProps } from '../types';
import getRefFromOptions from './getRefFromOptions';

// retrieve ref from module options

const emptyRefArray = (
  module: PortableTextProps,
  blockKey: string,
  elementKey = '',
  groupKey = '',
): void => {
  const ref = getRefFromOptions(module, blockKey, elementKey, groupKey);

  if (ref && ref.current) {
    ref.current.splice(0);
  }
};

export default emptyRefArray;
