import { colors } from '@frameio-bs/tokens';
import { gsap } from 'gsap';
import { CSSProperties, useMemo } from 'react';

import { GlowPosition } from '~/components/molecules/glow/types.d';
import GlowMedia from '~/components/molecules/glowMedia';
import Shadow from '~/components/molecules/shadow';
import { getBrightnessByColor, RGBToHex } from '~/utils';

import styles from './styles.module.css';
import { CardProps } from './types';

const Card = ({ image, isInView }: CardProps) => {
  const glowConfig = useMemo(() => {
    const startColor =
      image?.asset?.darkMutedBackground ||
      RGBToHex(
        colors.find((color) => color.name === 'Grey 00')?.value || 'rgb(0,0,0)',
      );
    const stopColor =
      image?.asset?.lightMutedBackground ||
      RGBToHex(
        colors.find((color) => color.name === 'Grey 00')?.value || 'rgb(0,0,0)',
      );
    const mainColor = image?.asset?.dominantBackground;

    const brightness = (
      mainColor ? getBrightnessByColor(mainColor) : 255 / 2
    ) as number;

    const intensity =
      brightness < 128
        ? gsap.utils.mapRange(0, 128, 0.55, 0.4, brightness)
        : gsap.utils.mapRange(128, 255, 0.45, 0.35, brightness);

    const config = {
      gradient: {
        startColor,
        stopColor,
        startPosition: 0.34,
        stopPosition: 0.85,
        orientation: 0,
      },
      intensity,
    };

    return config;
  }, [image?.asset?.darkMutedBackground, image?.asset?.dominantBackground]);

  return (
    <div className={styles.card}>
      <GlowMedia
        className={styles.imageContainer}
        sanityMedia={image}
        elementClassName={styles.image}
        isDisplayed={isInView}
        glowConfig={{
          ...glowConfig,
          scale: 1.1,
          position: GlowPosition.CENTER,
          type: 'GRADIENT',
        }}
        style={
          {
            '--glow-skew': '-10deg',
          } as CSSProperties
        }
      >
        <Shadow />
      </GlowMedia>
    </div>
  );
};

export default Card;
