import { gsap } from 'gsap';
import { CSSProperties, useEffect, useRef } from 'react';

import Image from '~/components/atoms/image';
import ModalVideoButton from '~/components/molecules/Modal/ModalVideoButton';
import { GradientOpacity } from '~/types/decorations.d';
import { cn, useScrollProgress } from '~/utils';
import gradientClassesMap from '~/utils/maps/gradientClasses';

import styles from './styles.module.css';
import { CMSModuleFullscreenVideo } from './types';

const FullScreenVideo = ({
  buttonLabel,
  className,
  garageDoorRevealParent,
  gradient = GradientOpacity.HEAVY,
  posterImage,
  textOverlay,
  background,
  video,
  slug,
}: CMSModuleFullscreenVideo) => {
  const $element = useRef<HTMLDivElement>(null);
  const $textOverlay = useRef<HTMLDivElement>(null);
  const scrollProgressOpacitySetter = useRef<(value: number) => void>();
  const scrollProgressYSetter = useRef<(value: number) => void>();

  const $buttonWrapper = useRef<HTMLDivElement>(null);

  const backgroundClasses = cn(
    styles.backgroundWrapper,
    gradient && styles[gradientClassesMap[gradient]],
  );

  const onElementProgress = (progress: number) => {
    if (scrollProgressOpacitySetter.current) {
      const normalizedProgressOpacity = gsap.utils.clamp(
        0,
        1,
        gsap.utils.normalize(0.7, 1, progress),
      );
      scrollProgressOpacitySetter.current(1 - normalizedProgressOpacity);
    }
    if (scrollProgressYSetter.current) {
      const normalizedProgressY =
        gsap.utils.clamp(0, 1, gsap.utils.normalize(0.4, 1, progress)) - 0.2;
      scrollProgressYSetter.current(normalizedProgressY * 15);
    }
  };

  useEffect(() => {
    scrollProgressOpacitySetter.current = gsap.quickSetter(
      $element.current,
      '--scroll-progress-opacity',
    ) as (value: number) => void;
    scrollProgressYSetter.current = gsap.quickSetter(
      $element.current,
      '--scroll-progress-y',
      'vh',
    ) as (value: number) => void;
  }, []);

  useScrollProgress($element, onElementProgress, {
    $garageDoorElement: garageDoorRevealParent,
  });

  return (
    <section
      className={cn(
        styles.fullscreenVideo,
        background && styles.hasBackground,
        className,
      )}
      ref={$element}
      style={
        {
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <div className={backgroundClasses}>
        <Image
          source={posterImage}
          className={styles.posterImageContainer}
          quality={70}
        />
      </div>

      <div className={styles.contentWrapper}>
        <p ref={$textOverlay} className={styles.textOverlay}>
          {textOverlay}
        </p>
        <div className={styles.buttonWrapper} ref={$buttonWrapper}>
          <ModalVideoButton
            className={styles.button}
            video={video}
            videoButtonLabel={buttonLabel}
          />
        </div>
      </div>
    </section>
  );
};

export default FullScreenVideo;
