import { gsap } from 'gsap';
import { useKeenSlider } from 'keen-slider/react';
import { CSSProperties, useCallback, useRef, useState } from 'react';

import PaginationDots from '~/components/atoms/pagination/dots';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import {
  cn,
  getSpacersStyles,
  keenSliderConfig,
  useScrollProgress,
} from '~/utils';
import addToRefArray from '~/utils/addToRefArray';

import ContentPortableText from '../textWithComponents/ContentPortableText';
import CardSection from './CardSection';
import styles from './styles.module.css';
import {
  CardSectionRef,
  CMSScrollingCardStackProps,
  ScrollingCardStackSection,
} from './types';

const ScrollingCardStack = ({
  className,
  content,
  alignment,
  ragging,
  sections,
  hasSpacers,
  garageDoorRevealParent,
  slug,
}: CMSScrollingCardStackProps) => {
  const $wrapper = useRef<HTMLUListElement>(null);
  const sectionRefs = useRef<CardSectionRef[]>(Array(sections.length));
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  const [activeSectionIndex, setActiveSectionIndex] = useState<number>(0);

  // Keen slider (mobile only)
  const [sliderRef, instanceRef] = useKeenSlider({
    ...keenSliderConfig.defaultConfig,
    loop: false,
    selector: '.slideItem',
    slides: {
      origin: 'center',
      perView: 1,
      number: sections.length,
    },
    // TODO: un-comment when fancier mobile version
    // renderMode: 'custom',
    detailsChanged: (slider) => {
      const progresses = slider.track.details.slides.map(
        (slide) => slide.portion,
      );
      progresses.forEach((progress: number, slideIndex: number) => {
        const slide = sectionRefs.current[slideIndex];
        if (slide) slide.setCarouselProgress(progress);
      });
    },
    slideChanged(slider) {
      setActiveSectionIndex(slider.track.details.rel);
    },
    breakpoints: {
      [keenSliderConfig.breakpoints.sm.mediaQuery]: {
        disabled: true,
      },
    },
  });

  const moveToIndex = (index: number) => {
    instanceRef.current?.moveToIdx(index);
  };

  const transformers = useRef<{ (input: number): number }[]>(
    Array(sections.length)
      .fill(0)
      .map((value: number, index: number) => {
        return gsap.utils.pipe(
          gsap.utils.normalize(index, index + 2),
          gsap.utils.clamp(0, 1),
        );
      }),
  );

  // Scroll card stack
  const onProgress = useCallback(
    (progress: number) => {
      if (!instanceRef?.current?.options.disabled) return;
      // progress from 0 to sections array max index
      const normalizedProgress = progress / (1 / sections.length);

      const index = Math.max(1, Math.floor(normalizedProgress));

      if (index !== activeSectionIndex) setActiveSectionIndex(index);
      sectionRefs.current?.forEach(
        (ref: CardSectionRef, sectionIndex: number) => {
          ref.setProgress(
            transformers.current[sectionIndex](normalizedProgress),
          );
        },
      );
    },
    [activeSectionIndex, sections.length, instanceRef],
  );

  useScrollProgress($wrapper, onProgress, {
    $garageDoorElement: garageDoorRevealParent,
    startOnMiddleOfScreen: true,
    finishOnMiddleOfScreen: true,
  });

  return (
    <section
      className={cn(
        styles.root,
        hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        className,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          '--total-sections': sections.length,
        } as CSSProperties
      }
      id={slug}
    >
      {content && (
        <div className={styles.contentWrapper}>
          <ContentPortableText
            className={styles.content}
            alignment={alignment}
            content={content}
            ragging={ragging}
          />
        </div>
      )}
      {/* Had to add a wrapping div, because `mergeRefs` was messing with keen slider */}
      <div ref={sliderRef}>
        <ul ref={$wrapper} className={cn(styles.wrapper)}>
          {sections.map(
            (
              { content, media, _key }: ScrollingCardStackSection,
              index: number,
            ) => {
              return (
                <CardSection
                  ref={(section: CardSectionRef) =>
                    addToRefArray(section, sectionRefs, index)
                  }
                  key={_key}
                  index={index}
                  className={cn('slideItem', styles.section)}
                  content={content}
                  media={media}
                  total={sections.length}
                  active={index === activeSectionIndex}
                />
              );
            },
          )}
        </ul>
      </div>
      <PaginationDots
        className={styles.paginationDots}
        activeIndex={activeSectionIndex}
        total={sections.length}
        onClick={moveToIndex}
      />
    </section>
  );
};

export default ScrollingCardStack;
