import { CSSProperties, useState } from 'react';

import Observer from '~/components/atoms/observer';
import Logo from '~/components/molecules/logo';
import { CMSLogoProps } from '~/components/molecules/logo/types';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { cn, getSpacersStyles } from '~/utils';

import styles from './styles.module.css';
import { CMSModuleLogoWall } from './types';

const LogoWall = ({
  logos,
  className,
  hasSpacers,
  accentColor,
  garageDoorRevealParent,
  background,
  slug,
}: CMSModuleLogoWall) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);

  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  return (
    <section
      className={cn(
        styles.container,
        garageDoorRevealParent && styles.garageDoorExtraSpace,
        background && styles.hasBackground,
        hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        className,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <Observer
        callback={updateIsInView}
        options={{ rootMargin: '100% 0%' }}
        className={styles.content}
      >
        {logos.map((logo: CMSLogoProps) => {
          const className = styles.icon;

          return (
            <Logo
              key={logo._id}
              className={className}
              {...(logo.image && {
                image: {
                  source: logo.image,
                },
              })}
              name={logo.name}
              svg={logo.svg}
              isInView={isInView !== false}
            />
          );
        })}
      </Observer>
    </section>
  );
};

export default LogoWall;
