// retrieve module configuration information by matching block key to module object
// we can manipulate the module configuration when reading in sanity data via getStaticProps
// portableText config is different - it's generic per block type rather than individual instances

import { cn } from '~/utils';

import defaultStyles from '../styles.module.css';
import { CMSPortableTextCustomContent, CssStyles } from '../types';

function applyClassNameToStyles(
  className: string,
  styles?: CssStyles | CssStyles[],
): string {
  let returnClasses = '';
  if (Array.isArray(styles)) {
    for (const stylesItem of styles) {
      if (stylesItem) {
        returnClasses = cn(returnClasses, stylesItem[className]);
      }
    }
  } else if (styles) {
    returnClasses = cn(returnClasses, styles[className]);
  }

  return returnClasses;
}

// moduleStyles are typically passed in via a portable text component
// the reason to pass in styles is to connect state styles, such as gridColumns
// to the component stylesheet

function getColumnClassesFromKey(
  content: CMSPortableTextCustomContent | CMSPortableTextCustomContent[],
  key: string,
  moduleStyles?: CssStyles | CssStyles[],
): string {
  if (key) {
    let matchingConfig: CMSPortableTextCustomContent | null = null;
    if (Array.isArray(content)) {
      const matchingContentResults = content.filter(
        (contentItem) => contentItem._key === key,
      );
      if (matchingContentResults.length) {
        matchingConfig = matchingContentResults[0];
      }
    } else if (content._key === key) {
      matchingConfig = content;
    }

    if (matchingConfig) {
      const { gridColumnIndex, gridColumnChildIndex, gridColumnsTotal } =
        matchingConfig;
      let gridClasses = '';
      if (typeof gridColumnIndex !== 'undefined') {
        gridClasses = cn(
          gridClasses,
          defaultStyles[`gridColumnIndex${gridColumnIndex}`],
          applyClassNameToStyles(
            `gridColumnIndex${gridColumnIndex}`,
            moduleStyles,
          ),
        );
      }
      if (typeof gridColumnChildIndex !== 'undefined') {
        gridClasses = cn(
          gridClasses,
          defaultStyles[`gridColumnChildIndex${gridColumnChildIndex}`],
          applyClassNameToStyles(
            `gridColumnChildIndex${gridColumnChildIndex}`,
            moduleStyles,
          ),
        );
      }
      if (typeof gridColumnsTotal !== 'undefined') {
        gridClasses = cn(
          gridClasses,
          defaultStyles[`gridColumnsTotal${gridColumnsTotal}`],
          applyClassNameToStyles(
            `gridColumnsTotal${gridColumnsTotal}`,
            moduleStyles,
          ),
        );
      }
      return gridClasses;
    }
  }
  return '';
}

export default getColumnClassesFromKey;
