const getImageSequenceHeight = ({
  numberOfFrames,
  speed = 1,
}: {
  numberOfFrames: number;
  speed?: number;
  // Speed is reversed (in the fragment we do 10 (arbitrary value) - speed (from the CMS)). So 1 in the CMS
  // will have an actual value of 10 - 1 = 9 here. Which means, 1 frame will take up 9px of scroll
}) => numberOfFrames * speed;

export default getImageSequenceHeight;
