/**
 * A button with play icon that opens a modal containing a single video
 * @param video A single data object containing the video properties
 * @param videoButtonLabel A string containing the button's label, only shown when the videoButtonType is "icon" - defaults to "Watch the Video"
 * @param videoButtonType The button's variant - defaults to "icon"
 * @param className
 * @example <ModalVideoButton className={styles.button} video={video} videoButtonLabel={"What the Video"}/>
 */
import { ForwardedRef, forwardRef, useRef, useState } from 'react';

import SvgPlayIcon from '~/assets/svg/play-icon.svg';
import buttonStyles from '~/components/atoms/Buttons/Ctas/ButtonBase.styles.module.css';
import ButtonPlay from '~/components/atoms/Buttons/UI/ButtonPlay';
import { VideoRef } from '~/components/atoms/video/types';
import { cn } from '~/utils';

import ModalVideo from '../ModalVideo';
import styles from './ModalVideoButton.styles.module.css';
import { ModalVideoButtonProps } from './ModalVideoButton.types.d';

const ModalVideoButton = (
  {
    className,
    video,
    videoButtonLabel = 'Watch the Video',
    videoButtonType = 'icon',
  }: ModalVideoButtonProps,
  ref: ForwardedRef<HTMLDivElement>,
) => {
  const $button = useRef<HTMLButtonElement>(null);
  const $modalVideo = useRef<VideoRef>(null);

  const [showVideoModal, setShowVideoModal] = useState(false);

  const onModalLeave = () => {
    $modalVideo.current?.muteSmoothly();

    // return focus to play button
    if ($button.current) $button.current.focus();
  };

  return (
    <div className={className} ref={ref}>
      {videoButtonType == 'icon' ? (
        <button
          className={styles.button}
          ref={$button}
          onClick={() => setShowVideoModal(true)}
        >
          <ButtonPlay tag="span" buttonColorScheme="solid" />
          <span className={styles.buttonLabel}>{videoButtonLabel}</span>
        </button>
      ) : (
        <button
          className={cn(styles.pillButton, buttonStyles.solidLight)}
          ref={$button}
          onClick={() => setShowVideoModal(true)}
        >
          <span className={styles.iconContainer}>
            <SvgPlayIcon className={styles.icon} />
          </span>
          {videoButtonLabel}
        </button>
      )}

      <ModalVideo
        video={video}
        modalTriggeredByClick={showVideoModal}
        onModalLeave={onModalLeave}
        onModalClose={() => setShowVideoModal(false)}
      />
    </div>
  );
};

export default forwardRef(ModalVideoButton);
