import { CSSProperties } from 'react';

import SvgCheckmarkIcon from '~/assets/svg/circle-checkmark.svg';
import Glow from '~/components/molecules/glow';
import { GlowType } from '~/components/molecules/glow/types.d';
import PortableText from '~/components/molecules/portableText';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { cn, getSpacersStyles } from '~/utils';

import styles from './styles.module.css';
import { CMSItemList, CMSModuleItemsList } from './types';

const ItemsList = ({
  alignment = 'left',
  content,
  hasSpacers,
  accentColor,
  lists,
  background,
  className,
  slug,
}: CMSModuleItemsList) => {
  const countClass = styles[`with${lists.length}Lists`];

  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);

  return (
    <section
      className={cn(
        styles.itemList,
        background && styles.hasBackground,
        alignment === 'center' && styles.alignCenter,
        hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        className,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <PortableText
        content={content}
        className={styles.content}
        options={{
          block: {
            eyebrow: {
              className: styles.eyebrow,
            },
            titles: {
              className: styles.titles,
            },
            bodies: {
              className: styles.bodies,
            },
          },
          types: {
            'block.buttons': {
              className: styles.buttons,
            },
          },
        }}
      />

      <div className={cn(styles.lists, countClass)}>
        {lists.map((list: CMSItemList) => {
          return (
            <div key={list._key} className={styles.list}>
              <h4 className={styles.listLabel}>{list.label}</h4>
              {list.items.map((label: string) => {
                return (
                  <div key={label} className={styles.listItem}>
                    <SvgCheckmarkIcon className={styles.listItemIcon} />
                    <p className={styles.listItemText}>{label}</p>
                  </div>
                );
              })}
            </div>
          );
        })}
      </div>
      <Glow
        className={styles.leftGlow}
        type={GlowType.BAKED_CANVAS}
        config={{
          intensity: 0.5,
          scale: 1.3,
          gradient: {
            startColor: '#FD856E',
            stopColor: '#9947EB',
            startPosition: 0.14,
            stopPosition: 0.65,
            orientation: Math.PI / 2,
          },
          type: 'GRADIENT',
        }}
      />
    </section>
  );
};

export default ItemsList;
