import {
  SanityImageCrop,
  SanityImageHotspot,
  SanityReference,
} from '@sanity/asset-utils';
import { ReactNode, RefObject } from 'react';

type ImageSequenceFrame = {
  asset: SanityReference;
  crop: SanityImageCrop;
  hotspot: SanityImageHotspot;
  index: number;
};

export enum fitsMap {
  COVER = 'COVER',
  CONTAIN = 'CONTAIN',
}

export type ImageSequence = {
  frames: ImageSequenceFrame[];
  speed: number;
  framesLoadOrder: number[];
  aspectRatio: number;
};

export type ImageSequenceProps = {
  sequence: ImageSequence;
  className?: string;
  canvasClassName?: string;
  top?: number;
  garageDoorRevealParent?: RefObject<HTMLElement>;
  fit?: fitsMap;
  children?: ReactNode;
  finishOnMiddleOfScreen?: boolean;
  startOnMiddleOfScreen?: boolean;
};
