import { gsap } from 'gsap';
import throttle from 'lodash/throttle';

type width = number | null;
type height = number | null;

type size = {
  width: width;
  height: height;
};

type subscriber = (size: size) => void;

class WindowSize {
  width: width | null;
  height: height | null;
  subscribers: subscriber[];
  boundUpdateSize: () => void;
  isActive: boolean;

  constructor() {
    // We're using clientWidth on body to exclude the scrollbar width
    this.width =
      typeof window !== 'undefined' && window.document
        ? document.body.clientWidth
        : null;

    // The client width is usually not yet computed on load,
    // so we're polling until it's a valid value
    if (this.width === 0) {
      const update = () => {
        this.updateSize();
        if (this.width === 0) {
          requestAnimationFrame(update);
        }
      };
      requestAnimationFrame(update);
    }

    this.height =
      typeof window !== 'undefined' && window.document
        ? window.innerHeight
        : null;
    this.subscribers = [];
    this.boundUpdateSize = throttle(this.updateSize.bind(this), 300);
    this.isActive = false;
    if (typeof window !== 'undefined' && window.document) {
      this.addEventListeners();
    }
  }

  addEventListeners() {
    this.isActive = true;
    window.addEventListener('resize', this.boundUpdateSize);
  }

  removeEventListeners() {
    this.isActive = false;
    window.removeEventListener('resize', this.boundUpdateSize);
  }

  updateSize() {
    if (this.isActive) {
      const windowSizeUpdateInTicker = () => {
        const width = document.body.clientWidth;
        const height = window.innerHeight;
        if (this.width !== width || this.height !== height) {
          this.setSize(width, height);
          for (const subscriber of this.subscribers) {
            subscriber({ width, height });
          }
        }
      };

      gsap.ticker.add(windowSizeUpdateInTicker, true, true);
    }
  }

  subscribe(callback: subscriber) {
    this.subscribers.push(callback);
    return () => {
      this.subscribers = this.subscribers.filter((cb) => cb !== callback);
    };
  }

  getSize() {
    return {
      width: this.width,
      height: this.height,
    };
  }

  setSize(width: width, height: height) {
    this.width = width;
    this.height = height;
  }
}

export default new WindowSize();
