import cloneDeep from 'lodash/cloneDeep';
import { ForwardedRef, forwardRef } from 'react';

import Image from '~/components/atoms/image';
import Svg from '~/components/atoms/svg';

import { LogoProps } from './types';

const Logo = (
  { svg, className, image, name, isInView }: LogoProps,
  ref: ForwardedRef<HTMLElement | SVGElement>,
) => {
  if (svg) {
    // replace asset name/alt with logo `name` prop.
    const svgProps = cloneDeep(svg);
    if (name) svgProps.name = name;

    return (
      <Svg
        {...svgProps}
        className={className}
        ref={ref as ForwardedRef<SVGElement>}
      />
    );
  } else if (image?.source) {
    const imageProps = { ...image };
    imageProps.contain = true;
    imageProps.source.alt = imageProps.source.alt || name || '';

    return (
      <Image
        {...imageProps}
        className={className}
        ref={ref as ForwardedRef<HTMLElement>}
        isDisplayed={isInView}
        preload={undefined}
        sizes={undefined}
      />
    );
  }

  return null;
};

export default forwardRef(Logo);
