import { ComponentType } from 'react';

import { cn, getUniqueId } from '~/utils';

import Block from '../blocks/Block';
import { blockConfig } from '../config';
import defaultStyles from '../styles.module.css';
import {
  BlockElementProps,
  BlockRecord,
  BlockTemplateConfigProps,
  JsxRenderProps,
  PortableTextProps,
} from '../types';
import emptyRefArray from './emptyRefArray';
import getElementPropsFromOptions from './getElementPropsFromOptions';

// create block elements (typography - example: h1-h6, body sizes, label)

const makeBlock = (module: PortableTextProps): BlockRecord => {
  const { isAutoStyles = true } = module;
  const config = blockConfig;
  const blockRecord: BlockRecord = {};

  // clear stale refs
  for (const blockGroupName in config) {
    const blockGroup = config[blockGroupName];

    // empty group refs
    emptyRefArray(module, 'block', '', blockGroupName);

    // empty element refs
    for (const blockName in blockGroup) {
      emptyRefArray(module, 'block', blockName, blockGroupName);
    }
  }

  // create blocks
  for (const blockTypeGroupsKey in config) {
    const blockGroup = config[blockTypeGroupsKey];
    for (const blockTypeKey in blockGroup) {
      const blockType = blockGroup[blockTypeKey];

      if (typeof blockType !== 'undefined' && module) {
        const renderClass = cn(
          isAutoStyles && defaultStyles[blockTypeGroupsKey],
          isAutoStyles && defaultStyles[blockTypeKey],
        );

        blockRecord[blockTypeKey] = (props: JsxRenderProps) => {
          const blockKey = props.value?._key || getUniqueId('block_');

          const blockProps: BlockTemplateConfigProps = {
            _key: blockKey,
            blockGroupName: blockTypeGroupsKey,
            blockName: blockTypeKey,
            module: module,
            className: renderClass,
            tagName: blockType.tagName,
            render: blockType.render,
          };

          if (blockProps?.render) {
            const CustomBlockRender =
              blockProps.render as ComponentType<JsxRenderProps>;
            const { blockName, blockGroupName } = blockProps;

            const externalOptionProps: BlockElementProps | null =
              getElementPropsFromOptions(
                module,
                'block',
                blockName,
                blockGroupName,
              );

            let renderClass: string = cn(
              externalOptionProps?.className,
              props.className,
              isAutoStyles && defaultStyles[blockTypeGroupsKey],
              isAutoStyles && defaultStyles[blockTypeKey],
            );

            // module styles can be passed in via a portable text component
            // the reason to pass in styles is to connect state styles, such as grid columns
            // to the component stylesheet
            const moduleStyles = externalOptionProps.moduleStyles;
            if (moduleStyles) {
              renderClass = cn(
                renderClass,
                moduleStyles[blockTypeGroupsKey],
                moduleStyles[blockTypeKey],
              );
            }

            return (
              <CustomBlockRender className={renderClass} {...props}>
                {props.children}
              </CustomBlockRender>
            );
          }
          return <Block {...blockProps}>{props.children}</Block>;
        };
      }
    }
  }
  return blockRecord;
};

export default makeBlock;
