import { ReactElement } from 'react';

import { cn } from '~/utils';

import EndsWithText from './layouts/endsWithText/EndsWithText';
import LaptopClose from './layouts/laptopClose';
import Simple from './layouts/simple';
import { CustomImageSequenceLayoutsProps } from './layouts/types';
import { CMSCustomImageSequence } from './types';

const layouts: {
  [key: string]: (props: CustomImageSequenceLayoutsProps) => ReactElement;
} = {
  laptopClose: LaptopClose,
  simple: Simple,
  endsWithText: EndsWithText,
};

const CustomImageSequence = ({
  layout,
  customContent,
  sequence,
  hasSpacers,
  accentColor,
  startOnMiddleOfScreen,
  background,
  top,
  garageDoorRevealParent,
  isReverseGarageDoor,
  className,
  slug,
}: CMSCustomImageSequence) => {
  const Layout = layouts[layout];

  if (typeof Layout === 'undefined') {
    console.warn('Custom image sequence: layout not defined');
    return null;
  }

  return (
    <Layout
      hasSpacers={hasSpacers}
      sequence={sequence}
      accentColor={accentColor}
      top={top}
      background={background}
      garageDoorRevealParent={garageDoorRevealParent}
      startOnMiddleOfScreen={startOnMiddleOfScreen}
      aboveMediaWithText={customContent?.aboveMediaWithText}
      aboveText={customContent?.aboveText}
      hiddenText={customContent?.middleText}
      belowText={customContent?.belowText}
      className={cn(className)}
      isReverseGarageDoor={isReverseGarageDoor}
      slug={slug}
    />
  );
};

export default CustomImageSequence;
