import { CMSButtonProps } from '~/components/atoms/Buttons/Ctas/ButtonBase.types.d';

export enum PricePeriodValue {
  MONTH = 'month',
  MONTH_ANNUAL_PREPAID = 'monthly-annual-prepaid',
  YEAR = 'year',
}

export type CMSPricePeriod = {
  title: string;
  abbreviation: string;
  value: PricePeriodValue;
};

export type CMSSubscriptionFeature = {
  title: string;
  description?: string;
};

export type CMSBullet = {
  _key: string;
  title: string;
  description?: string;
};

export type CMSComparisonFeature = {
  _key: string;
  title: string;
  subtitle: string;
  description?: string;
};

export type CMSCheckmarkFeature = {
  _key: string;
  subscriptionFeature: CMSSubscriptionFeature;
  isChecked?: boolean;
};

export type CMSPriceDetails = {
  _key: string;
  price: number;
  pricePeriod: CMSPricePeriod;
  contextLine1: string;
  contextLine2: string;
  signUpButton: {
    button: CMSButtonProps;
  };
};

export type CMSPricingProduct = {
  _key: string;
  title: string;
  pricesByPeriod: CMSPriceDetails[];
  secondaryTitle?: string;
  description: string;
  topBullets?: CMSBullet[];
  topDescription?: string;
  signUpButton: CMSButtonProps;
  secondaryButton: CMSButtonProps;
  detailsTitle: string;
  detailsBullets: CMSBullet[];
  featureComparisons: CMSComparisonFeature[];
  checkmarkComparisons: CMSCheckmarkFeature[];
};

export type CMSPricing = {
  products: CMSPricingProduct[];
};
