import { gsap } from 'gsap';
import { useKeenSlider } from 'keen-slider/react';
import { useEffect, useRef } from 'react';

import { cn, keenSliderConfig } from '~/utils';
import addToRefArray from '~/utils/addToRefArray';

import styles from './styles.module.css';
import { PaginationDotsProps } from './types';

// pagination dots that mimic instagram carousel pagination, with dots farther away from the current index scaled to be smaller
const MAX_DOTS = 6;
const NUM_FULL_SIZED = MAX_DOTS / 2;
const SCALE_INTERVAL = 1 / NUM_FULL_SIZED;

const PaginationDots = ({
  className,
  activeIndex = 0,
  total,
  onClick,
}: PaginationDotsProps) => {
  const refDots = useRef<HTMLButtonElement[]>(Array(total));

  // when there are too many dots to fit into the container space, we use a slider
  const [sliderRef, sliderInstanceRef] = useKeenSlider({
    ...keenSliderConfig.defaultConfig,
    selector: '.dotItem',
    mode: 'snap',
    drag: false,
    initial: activeIndex,
    slides: {
      perView: MAX_DOTS,
    },
  });

  useEffect(() => {
    if (total <= MAX_DOTS - 1) return;
    const offsetActiveIndex = Math.floor(
      gsap.utils.mapRange(0, total, 1, total - 1, activeIndex),
    );
    sliderInstanceRef.current?.track.details.slides.map((slide, i) => {
      // map the active index to a range between 1 and total - 1
      const scale = gsap.utils.clamp(
        0,
        1,
        1 - Math.max(0, Math.abs(offsetActiveIndex - i) - 1) * SCALE_INTERVAL,
      );
      gsap.set(refDots.current[i], { scale: scale });
    });

    sliderInstanceRef.current?.moveToIdx(
      Math.max(0, activeIndex - (NUM_FULL_SIZED - 1)),
    );
  }, [activeIndex, sliderInstanceRef, total]);

  const onDotClick = (index: number) => {
    if (onClick && typeof onClick === 'function') {
      // this callback should update `activeIndex` prop
      onClick(index);
    }
  };

  if (total < 2) {
    return null;
  }

  return (
    <div
      className={cn(styles.paginationDotsContainer, className)}
      ref={sliderRef}
    >
      <ul
        className={cn(
          styles.paginationDotsList,
          total < MAX_DOTS && styles.center,
        )}
      >
        {[...Array(total)].map((x, i) => (
          <li
            className={cn(
              styles.dotItem,
              activeIndex === i && styles.active,
              'dotItem',
            )}
            key={i}
          >
            <button className={styles.dotButton} onClick={() => onDotClick(i)}>
              <figure
                className={styles.dotFigure}
                ref={(ref: HTMLButtonElement) => {
                  addToRefArray(ref, refDots, i);
                }}
              />
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default PaginationDots;
