import { CSSProperties } from 'react';

import decorationsStyles from '~/styles/theme/decorations.module.css';
import { cn, getSpacersStyles } from '~/utils';

import FancyHoverTestimonials from './fancyHoverTestimonials';
import styles from './styles.module.css';
import { CMSFancyTestimonialsProps } from './types';
import XsFancyTestimonials from './xsFancyTestimonials';

const FancyTestimonials = (props: CMSFancyTestimonialsProps) => {
  const spacerValues = getSpacersStyles(
    props.hasSpacers?.before,
    props.hasSpacers?.after,
  );

  const { slug } = props;

  return (
    <section
      className={cn(
        styles.fancyTestimonials,
        props.background && styles.hasBackground,
        props.hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        props.hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        props.className,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          '--color-accent': props.accentColor || '',
          '--module-background': props.background,
        } as CSSProperties
      }
      id={slug}
    >
      <XsFancyTestimonials
        {...props}
        className={styles.fancyTestimonialsInner}
      />
      <FancyHoverTestimonials
        {...props}
        className={styles.fancyTestimonialsInner}
      />
    </section>
  );
};

export default FancyTestimonials;
