import { gsap } from 'gsap';
import { CSSProperties, useCallback, useEffect, useRef, useState } from 'react';

import Observer from '~/components/atoms/observer';
import Glow from '~/components/molecules/glow';
import {
  GlowAlignment,
  GlowPosition,
  GlowProps,
  GlowType,
} from '~/components/molecules/glow/types.d';
import PortableText from '~/components/molecules/portableText';
import Shadow from '~/components/molecules/shadow';
import UIStore from '~/state/ui';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import {
  cn,
  getSpacersStyles,
  killTimeline,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';
import addToRefArray from '~/utils/addToRefArray';
import isBreakpointOrGreater from '~/utils/isBreakpointOrGreater';

import { animation } from './animations';
import BentoCard from './cards/BentoCard';
import { BentoCardProps } from './cards/types';
import styles from './styles.module.css';
import { CMSModuleBento } from './types';

const useUIStore = UIStore;

const glowConfig: GlowProps[] = [
  {
    config: {
      scale: 1.5,
      position: GlowPosition.TOP_LEFT,
      color: '#144353',
      intensity: 0.8,
      type: 'COLOR',
    },
  },
  {
    config: {
      scale: 1.5,
      position: GlowPosition.TOP_CENTER,
      align: GlowAlignment.RIGHT,
      color: '#144353',
      intensity: 0.7,
      type: 'COLOR',
    },
  },
  {
    config: {
      scale: 1.4,
      position: GlowPosition.BOTTOM_LEFT,
      color: '#144353',
      intensity: 0.8,
      type: 'COLOR',
    },
  },
  {
    config: {
      scale: 2,
      position: GlowPosition.BOTTOM_RIGHT,
      color: '#144353',
      intensity: 0.7,
      type: 'COLOR',
    },
  },
];

const ONE_COL_WINDOW_WIDTH = parseInt(styles.bentoSmallBreakpoint);

export const gradientClassesMap = {
  landing: 'hasLandingGradient',
  subpage: 'hasSubpageGradient',
};

const Bento = ({
  accentColor,
  background,
  cards,
  className,
  content,
  garageDoorRevealParent,
  gradient,
  hasSpacers,
  slug,
}: CMSModuleBento) => {
  const $element = useRef<HTMLDivElement>(null);
  const $cardsWrapper = useRef<HTMLDivElement>(null);
  const $cards = useRef([] as HTMLDivElement[]);
  const $text = useRef<HTMLDivElement>(null);
  const timeline = useRef<GSAPTimeline>();
  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);

  const windowWidth = useUIStore((state) => state.windowWidth);
  const breakpoint = useUIStore((state) => state.breakpoint);

  const windowWidthLast = useRef(windowWidth || 0);

  // GSAP thought it was fine to use this type 
  // eslint-disable-next-line @typescript-eslint/ban-types
  const scrollProgressQuickSet = useRef<Function>();

  useLayoutEffect(() => {
    scrollProgressQuickSet.current = gsap.quickSetter(
      $element.current,
      '--bento-scroll-progress',
    );
  }, []);

  // In this one we have animations based on the actual height of the module and the breakpoint,
  // so we'll have to initialize the timeline when we know that height, i.e. when it's in the view.
  // Then, we only want to re-init the timeline if the total height or the breakpoint has changed

  const MIN_ANIMATION_BREAKPOINT = 'sm';

  const killAnimationByBreakpoint = useCallback(
    (force = false) => {
      if (
        breakpoint &&
        timeline.current &&
        (!isBreakpointOrGreater(breakpoint, MIN_ANIMATION_BREAKPOINT) || force)
      ) {
        killTimeline(timeline.current);
        timeline.current = undefined;
      }
    },
    [breakpoint],
  );

  const killAnimation = useCallback(() => {
    if (timeline.current) {
      killTimeline(timeline.current);
      timeline.current = undefined;
    }
  }, []);

  const onProgress = useCallback(
    (progress: number, isInView: boolean, totalHeight: number | null) => {
      const breakpoint = UIStore.getState().breakpoint;
      if (
        isInView &&
        breakpoint &&
        isBreakpointOrGreater(breakpoint, MIN_ANIMATION_BREAKPOINT)
      ) {
        // if window size changed or timeline hasn't been created yet
        if (
          totalHeight &&
          windowWidth &&
          (!timeline.current || windowWidth !== windowWidthLast.current)
        ) {
          windowWidthLast.current = windowWidth;

          killAnimation();

          // create new timeline
          // TODO: debounce creating new timeline, this may be causing inconsistent behavior

          timeline.current = animation(
            {
              // disable text parallax if there's no garage door effect
              $text: garageDoorRevealParent ? $text : undefined,
              $cardsWrapper,
              $cards: {
                current: $cards.current.slice(Math.ceil(cards.length / 2)),
              },
            },
            totalHeight,
            windowWidth ? windowWidth < ONE_COL_WINDOW_WIDTH : false,
          );
        }
      }

      if (scrollProgressQuickSet.current) {
        scrollProgressQuickSet.current(Math.min(1, (progress - 0.5) / 0.5));
      }

      if (timeline.current) {
        timeline.current?.progress(progress);
      }
    },
    [windowWidth, killAnimation, garageDoorRevealParent, cards.length],
  );

  // TODO: i don't like that windowWidth is a state - we shouldn't be forcing a re-render every time the window resizes

  useEffect(() => {
    killAnimationByBreakpoint();
  }, [breakpoint, killAnimationByBreakpoint]);

  useScrollProgress($element, onProgress, {
    $garageDoorElement: garageDoorRevealParent,
  });

  // reset to remove stale refs
  $cards.current = [];

  return (
    <section
      className={cn(
        styles.bento,
        background && styles.hasBackground,
        className,
      )}
      ref={$element}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <Glow
        className={styles.topGlow}
        type={GlowType.BAKED_CANVAS}
        config={{
          color: '#144353',
          intensity: 0.65,
          scale: 1.1,
          type: 'COLOR',
        }}
      />
      <div
        className={cn(
          styles.container,
          hasSpacers?.before && decorationsStyles.hasSpacerBefore,
          hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        )}
      >
        <PortableText
          alignment="center"
          content={content}
          className={styles.content}
          ref={$text}
          options={{
            block: {
              titles: {
                className: styles.titles,
              },
              bodies: {
                className: styles.bodies,
              },
              eyebrow: {
                className: styles.eyebrow,
              },
              label: {
                className: styles.label,
              },
            },
            types: {
              'block.buttons': {
                className: styles.buttons,
              },
            },
          }}
        />
        <Observer
          className={styles.cardsWrapper}
          ref={$cardsWrapper}
          options={{ rootMargin: '200% 0%' }}
          callback={updateIsInView}
        >
          <div className={styles.cardsDesktop}>
            <div className={styles.cardsColLeft}>
              {cards.flatMap((card: BentoCardProps, index: number) =>
                index % 2 === 0 ? (
                  <BentoCard
                    {...card}
                    key={card._key}
                    className={cn(
                      styles.card,
                      glowConfig[index] ? '' : styles.noGlow,
                    )}
                    gradient={gradient}
                    orderIndex={card.orderIndex}
                    glow={glowConfig[index]}
                    ref={(element: HTMLDivElement) =>
                      $cards && addToRefArray(element, $cards)
                    }
                  />
                ) : null,
              )}
            </div>
            <div className={styles.cardsColRight}>
              {cards.flatMap((card: BentoCardProps, index: number) =>
                index % 2 > 0 ? (
                  <BentoCard
                    {...card}
                    key={card._key}
                    className={cn(
                      styles.card,
                      glowConfig[index] ? '' : styles.noGlow,
                    )}
                    gradient={gradient}
                    orderIndex={card.orderIndex}
                    glow={glowConfig[index]}
                    ref={(element: HTMLDivElement) =>
                      $cards && addToRefArray(element, $cards)
                    }
                  />
                ) : null,
              )}
            </div>
          </div>
          <div className={styles.cardsMobile}>
            {cards.map((card: BentoCardProps, index: number) => (
              <BentoCard
                {...card}
                key={card._key}
                className={cn(
                  styles.card,
                  glowConfig[index] ? '' : styles.noGlow,
                )}
                gradient={gradient}
                orderIndex={card.orderIndex}
                glow={glowConfig[index]}
                isInView={isInView !== false}
              />
            ))}
          </div>
        </Observer>
        <Glow
          className={styles.bottomGlow}
          type={GlowType.BAKED_CANVAS}
          config={{
            color: '#4D5869',
            intensity: 0.4,
            scale: 1.5,
            type: 'COLOR',
          }}
        />
      </div>
      <Shadow className={styles.bottomGlassContainer}>
        <figure className={styles.bottomGlass} />
      </Shadow>
    </section>
  );
};

export default Bento;
