import { ForwardedRef, forwardRef } from 'react';

import Image from '~/components/atoms/image';
import { CMSImage } from '~/components/atoms/image/image.types.d';
import { CMSVideoProps, VideoRef } from '~/components/atoms/video/types';
import Video from '~/components/atoms/video/Video';

import { MediaProps } from './types';

const Media = (
  props: MediaProps,
  ref: ForwardedRef<HTMLElement | VideoRef>,
) => {
  const {
    canAutoplay,
    className,
    contain,
    cover,
    isLooping,
    isMuted,
    willAutoplay,
    name = '',
    onReady,
    preload = false,
    sanityMedia,
    sizes,
    isDisplayed,
  } = props;

  if (sanityMedia?.mediaType === 'image') {
    const imageRef = ref as ForwardedRef<HTMLElement>;

    const imageProps = sanityMedia as CMSImage;
    imageProps.alt = name || imageProps.alt;

    let conditionalProps = {};

    if (sizes) {
      if (preload) {
        conditionalProps = { ...conditionalProps, preload: true, sizes };
      } else {
        conditionalProps = { ...conditionalProps, sizes };
      }
    }

    let fit:
      | ({ cover: true } & { contain?: never })
      | ({ cover?: never } & { contain: true })
      | Record<string, never> = {};

    if (cover) {
      fit = { cover: true };
    } else if (contain) {
      fit = { contain: true };
    }

    return (
      <Image
        source={imageProps}
        className={className}
        ref={imageRef}
        onReady={onReady}
        {...fit}
        {...conditionalProps}
        isDisplayed={isDisplayed}
      />
    );
  }

  if (sanityMedia?.mediaType === 'video') {
    const videoRef = ref as ForwardedRef<VideoRef>;
    const videoProps = sanityMedia as CMSVideoProps;

    return (
      <Video
        aspectRatio={videoProps.aspectRatio}
        src={videoProps.url}
        isLooping={isLooping ?? videoProps.isLooping}
        isMuted={isMuted ?? videoProps.isMuted}
        canAutoplay={canAutoplay}
        willAutoplay={willAutoplay ?? videoProps.willAutoplay}
        className={className}
        onReady={onReady}
        cover={cover}
        ref={videoRef}
        thumbnail={videoProps.thumbnail}
      />
    );
  }

  console.warn('Media Error: No valid Image or Video.', props);
  return null;
};

export default forwardRef(Media);
