const assetFragment = `
  asset -> {
    _id,
    "darkVibrantBackground": metadata.palette.darkVibrant.background,
    "dominantBackground": metadata.palette.dominant.background,
    "darkMutedBackground": metadata.palette.darkMuted.background,
    "lightMutedBackground": metadata.palette.lightMuted.background,
    "height": metadata.dimensions.height,
    "width": metadata.dimensions.width,
    "aspectRatio": metadata.dimensions.aspectRatio,
    "isOpaque": metadata.isOpaque,
  }
`;

const fragment = `
  defined(_key) => {_key},
  "alt": coalesce(alt, ""),
  defined(crop) => {crop},
  defined(hotspot) => {hotspot},
  'mediaType' : 'image',
  ${assetFragment}
`;

export default fragment;
