import { gsap } from 'gsap';
import { KeyboardEvent, useCallback, useEffect, useRef, useState } from 'react';

import Modal from '~/components/molecules/Modal';
import {
  ModalBackgroundType,
  TransitionFunction,
} from '~/components/molecules/Modal/Modal.types.d';

import styles from './styles.module.css';
import TestimonialsModalContent from './TestimonialsModalContent';
import {
  EnterTimelineObject,
  TestimonialsModalContentRef,
  TestimonialsModalProps,
} from './types';

const TestimonialsModal = ({
  currentIndex,
  sourceRect,
  testimonials,
  onClose,
}: TestimonialsModalProps) => {
  const [carouselIndex, setCarouselIndex] = useState<number | undefined>(
    currentIndex,
  );
  //  use to trigger closing animation
  const [shouldCloseModal, setShouldCloseModal] = useState<boolean>(false);

  const testimonialsModalContent = useRef<TestimonialsModalContentRef>(null);

  const transitionPlaying = useRef<boolean>(false);

  useEffect(() => {
    if (currentIndex !== undefined && !transitionPlaying.current) {
      setCarouselIndex(currentIndex);
    }
  }, [currentIndex]);

  const handlePrevious = useCallback(() => {
    if (carouselIndex !== undefined && carouselIndex > 0) {
      setCarouselIndex(carouselIndex - 1);
    }
  }, [carouselIndex]);

  const handleNext = useCallback(() => {
    if (carouselIndex !== undefined && carouselIndex < testimonials.length) {
      setCarouselIndex(carouselIndex + 1);
    }
  }, [carouselIndex, testimonials.length]);

  const handleKeyDown = useCallback(
    (event: KeyboardEvent<HTMLDivElement>) => {
      if (event.code === 'ArrowLeft') {
        handlePrevious();
      }
      if (event.code === 'ArrowRight') {
        handleNext();
      }
    },
    [handlePrevious, handleNext],
  );

  const modalTransitionEnter: TransitionFunction = useCallback(
    ({ $modal, $modalBackground, done }) => {
      // since enter timeline of content is async, we need to wait for it
      if (!testimonialsModalContent.current) return;

      testimonialsModalContent.current
        .enterTimeline()
        .then(({ timeline: modalContentTl }: EnterTimelineObject) => {
          const tl = gsap.timeline({
            onComplete: () => {
              done();
            },
          });

          tl.set($modal.current, {
            alpha: 1,
          });

          tl.add(modalContentTl, '0');

          tl.fromTo(
            $modalBackground.current,
            {
              alpha: 0,
            },
            {
              alpha: 1,
            },
            '<+0.5',
          );
        });
    },
    [],
  );

  const modalTransitionLeave: TransitionFunction = useCallback(
    ({ $modal, done }) => {
      const tl = gsap.timeline({
        onComplete: () => {
          //  call done when animation is over to close modal
          done();
        },
      });
      if (testimonialsModalContent.current)
        tl.add(testimonialsModalContent.current.leaveTimeline(), '0');

      tl.to($modal.current, {
        alpha: 0,
        duration: 0.5,
      });
    },
    [],
  );

  return (
    <Modal
      contentClassName={styles.modalWrapper}
      backgroundType={ModalBackgroundType.ALPHA}
      defaultCloseButton={false}
      shouldClose={shouldCloseModal}
      onKeyDown={handleKeyDown}
      transitionEnter={modalTransitionEnter}
      transitionLeave={modalTransitionLeave}
      onClose={onClose}
    >
      <TestimonialsModalContent
        ref={testimonialsModalContent}
        testimonials={testimonials}
        carouselIndex={carouselIndex}
        sourceRect={sourceRect}
        handleNext={handleNext}
        handlePrevious={handlePrevious}
        startClosingModal={() => setShouldCloseModal(true)}
      />
    </Modal>
  );
};

export default TestimonialsModal;
