import Form from '~/components/molecules/form';
import addToRefArray from '~/utils/addToRefArray';

import { getElementAttributesFromOptions } from '../../utils';
import getBlockClassesFromContent from '../../utils/getBlockClassesFromContent';
import getRefFromOptions from '../../utils/getRefFromOptions';
import { PortableTextFormProps } from './types';

const PortableTextForm = ({
  module,
  value,
  typeName,
  typeCategory,
}: PortableTextFormProps) => {
  const optionsProps = getElementAttributesFromOptions(
    module,
    typeCategory,
    typeName,
  );
  const className = optionsProps?.className ?? '';
  const { _key, _type, form, moduleStyles = null } = value;
  const ref = getRefFromOptions(module, typeCategory, typeName);

  const renderClass = getBlockClassesFromContent(_key, value, className, [
    moduleStyles,
  ]);

  return (
    <Form
      ref={(element: HTMLDivElement) => ref && addToRefArray(element, ref)}
      {...form}
      _key={_key}
      _type={_type}
      className={renderClass}
    />
  );
};

export default PortableTextForm;
