import { groq } from 'next-sanity';

import imageFragment from '~/components/atoms/image/fragment';
import { CMSImage } from '~/components/atoms/image/image.types.d';
import { CMSModuleBase } from '~/components/organisms/types';
import { CMSPage } from '~/types/index.d';

import getDrafts from './fragments/getDrafts';
// Fragments
import pageFragment from './fragments/page';
import siteConfig from './fragments/siteConfig';
import { getClient } from './sanity.server';
import { DictionaryEntry } from './stores/types';

// TODO: split this file into multiple fragments

export async function getGlobalConfig(preview: boolean): Promise<any> {
  // TODO: let's try to reduce the logic here and create helper functions to manage all the coalesce logic
  const query = groq`{
    "config": coalesce(
      *[_id == "drafts.global-config"]{
        ${siteConfig}
      }[0],
      *[_id == "global-config"]{
        ${siteConfig}
      }[0]
    ),
    "assets":
      coalesce(
        *[_id == "drafts.global-assets"]{
          assets[] {
            "key": key.current,
            "image": image{${imageFragment}}
          }
        }[0].assets,
        *[_id == "global-assets"]{
          assets[] {
            "key": key.current,
            "image": image{${imageFragment}}
          }
        }[0].assets
      ),
    "dictionary":
      coalesce(
        *[_id == "drafts.dictionary"]{
          dictionary[] {
            key,
            label
          }
        }[0].dictionary,
        *[_id == "dictionary"]{
          dictionary[] {
            key,
            label
          }
        }[0].dictionary
      )
    }
  `;

  let data = await getClient(preview).fetch(query);
  data = {
    assets: data.assets?.reduce(
      (
        object: { [key: string]: CMSImage },
        element: { key: string; image: CMSImage },
      ) => {
        object[element.key] = element.image;
        return object;
      },
      {},
    ),
    ...data.config,
    dictionary: data.dictionary
      ? data.dictionary.reduce(
          (prev: { [key: string]: string }, current: DictionaryEntry) => {
            prev[current.key] = current.label;
            return prev;
          },
          {},
        )
      : {},
  };

  return { data, query };
}

export async function getAllPagesWithSlug(preview = true): Promise<CMSPage[]> {
  const query = groq`
    *[_type == "page" || _type match "page.*"]{
      'id': _id,
      'slug': slug.current
    }
  `;
  const data = await getClient(preview).fetch(query);

  // Make sure we don't have an empty slug
  const pages = data.map((page: CMSPage) => {
    if (!page.slug) {
      page.slug = '';
    }
    return page;
  });

  return pages;
}

export async function getAllModulesWithId(
  preview = true,
): Promise<CMSModuleBase[]> {
  const query = groq`
    *[_type match "module*"]{
      _id,
    }
  `;
  const data = await getClient(preview).fetch(query);

  return data;
}

export async function getPageBySlug(
  slug: string,
  preview: boolean,
): Promise<any> {
  const getPageQuery =
    slug === '' ? `!defined(slug.current)` : `slug.current == $slug`;
  const query = groq`
    ${getDrafts(`(_type == "page" || _type match "page.*") && ${getPageQuery}`)}
    {
      ${pageFragment}
    }
    `;

  const queryParams = { slug };
  const data = await getClient(preview).fetch(query, queryParams);

  return { data, query, queryParams };
}

export async function getDocumentBySlug(
  slug: string,
  preview: boolean,
  // locale?: string,
) {
  const getPageQuery =
    slug === '' ? `!defined(slug.current)` : `slug.current == $slug`;
  const query = groq`
    *[${getPageQuery}][0] {
      ${pageFragment}
    }
  `;
  const queryParams = { slug };
  const data = await getClient(preview).fetch(query, queryParams);
  return { data, query, queryParams };
}

export async function getModuleById(
  moduleId: string,
  preview: boolean,
  // locale?: string,
): Promise<any> {
  const query = groq`
  ${getDrafts(`_type match "module*" && _id == $moduleId`)}
  {
    ...
  }
  `;
  const queryParams = { moduleId };
  const data = await getClient(preview).fetch(query, queryParams);
  return { data, query, queryParams };
}
