import { CSSProperties } from 'react';

import PortableText from '~/components/molecules/portableText';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { cn, getSpacersStyles } from '~/utils';

import CardRows from './rows';
import styles from './styles.module.css';
import { CMSModuleAsymmetricalCardsProps } from './types';
import XsCards from './xscards';

export const AsymmetricalCards = ({
  asymmetricalCardItems,
  hasSpacers,
  accentColor,
  text,
  background,
  className,
  garageDoorRevealParent,
}: CMSModuleAsymmetricalCardsProps) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  return (
    <section
      className={cn(
        styles.asymmetricalCards,
        background && styles.hasBackground,
        hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        className,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
    >
      <PortableText content={text} className={styles.text} isAutoGrid={false} />

      <XsCards asymmetricalCardItems={asymmetricalCardItems} />

      <CardRows
        asymmetricalCardItems={asymmetricalCardItems}
        garageDoorRevealParent={garageDoorRevealParent}
      />
    </section>
  );
};
