import { CSSProperties, ForwardedRef, forwardRef, useRef } from 'react';

import Image from '~/components/atoms/image';
import { CMSImage } from '~/components/atoms/image/image.types.d';
import Link from '~/components/atoms/link';
import Glow from '~/components/molecules/glow';
import { GlowRef, GlowType } from '~/components/molecules/glow/types.d';
import PortableText from '~/components/molecules/portableText';
import Shadow from '~/components/molecules/shadow';
import { cn } from '~/utils';

import { gradientClassesMap } from '../Bento';
import styles from './styles.module.css';
import { BentoCardProps } from './types.d';

const BentoCard = (
  {
    className,
    content,
    glow,
    gradient = 'landing',
    image,
    imageBackground,
    link,
    orderIndex = 0,
    isInView,
  }: BentoCardProps,
  ref: ForwardedRef<HTMLDivElement>,
) => {
  const $glowWrapper = useRef<GlowRef>(null);

  const glowClasses = cn(
    className,
    styles.bentoCardWrapper,
    styles[gradientClassesMap[gradient]],
  );

  const renderClasses = cn(
    styles.bentoCard,
    typeof link?.url !== 'undefined' && styles.bentoCardIsLinked,
  );

  if ($glowWrapper.current?.$wrapper?.current) {
    $glowWrapper.current.$wrapper.current.tabIndex = (orderIndex + 1) * 10 + 1;
  }

  const ImageWrapper = link ? Link : 'div';

  return (
    <Glow
      className={glowClasses}
      style={{ '--flex-order': orderIndex } as CSSProperties}
      ref={$glowWrapper}
      type={GlowType.BAKED_CANVAS}
      config={glow.config}
    >
      <div ref={ref} className={styles.cardRefWrapper}>
        <Shadow className={styles.shadow}>
          <div className={renderClasses} data-order-index={orderIndex}>
            <div className={styles.imageWrapper}>
              <ImageWrapper to={link}>
                {image ? (
                  <Image
                    source={{ ...image, alt: link?.label || '' } as CMSImage}
                    className={styles.image}
                    quality={100}
                    isDisplayed={isInView}
                  />
                ) : (
                  <div className={styles.imageSpacer}></div>
                )}
              </ImageWrapper>
            </div>
            <div className={styles.contentContainer}>
              <PortableText
                isAutoGrid={false}
                content={content}
                className={styles.content}
                options={{
                  block: {
                    titles: {
                      className: styles.titles,
                      tagName: 'h3',
                    },
                    bodies: {
                      className: styles.bodies,
                    },
                  },
                  types: {
                    'block.buttons': {
                      className: styles.buttons,
                    },
                  },
                }}
              />
            </div>
            <div className={styles.backgroundWrapper}>
              {imageBackground && (
                <Image
                  source={imageBackground}
                  className={styles.backgroundImage}
                  quality={90}
                />
              )}
            </div>
          </div>
        </Shadow>
      </div>
    </Glow>
  );
};
export default forwardRef<HTMLDivElement, BentoCardProps>(BentoCard);
