import { cn } from '~/utils';

import { CMSPortableTextCustomContent, CssStyles } from '../types';
import getColumnClassesFromKey from './getColumnClassesFromKey';

function getBlockClassesFromContent(
  _key: string | undefined,
  content: CMSPortableTextCustomContent | CMSPortableTextCustomContent[],
  classes: string,
  styles?: CssStyles | CssStyles[],
) {
  let renderClass = classes;

  // get css classes to split content into columns as designated by usage of block.column in sanity
  if (_key) {
    renderClass = cn(
      renderClass,
      getColumnClassesFromKey(content, _key, styles),
    );
  }

  return renderClass;
}

export default getBlockClassesFromContent;
