import { MouseEvent, TouchEvent } from 'react';

export function getPointerCoords(event: MouseEvent | TouchEvent) {
  let x = 0,
    y = 0;

  if ('touches' in event.nativeEvent) {
    x = event.nativeEvent.touches[0].clientX;
    y = event.nativeEvent.touches[0].clientY;
  }

  if ('clientX' in event.nativeEvent) {
    x = event.nativeEvent.clientX;
    y = event.nativeEvent.clientY;
  }

  return { x, y };
}

export function disableDocumentScroll() {
  document.body.style.overflowX = 'hidden';
}

export function enableDocumentScroll() {
  document.body.style.removeProperty('overflowX');
}
