import { gsap } from 'gsap';
import { RefObject } from 'react';

export const doorAnimation = ({
  $element,
  $background,
  foregroundElements,
}: {
  $element: RefObject<HTMLDivElement>;
  $background: RefObject<HTMLElement>;
  foregroundElements: HTMLElement[];
}) => {
  const tl = gsap.timeline({ paused: true });
  if ($background.current) {
    tl.fromTo(
      $background.current,
      {
        scale: 1,
      },
      {
        scale: 1.08,
        ease: 'none',
      },
    );
  }

  if ($element.current) {
    tl.fromTo(
      $element.current,
      {
        '--overlay-opacity': 0,
      },
      {
        '--overlay-opacity': 0.9,
      },
      0,
    );
  }

  for (const [index, foregroundElement] of foregroundElements.entries()) {
    if (foregroundElement) {
      tl.fromTo(
        foregroundElement,
        {
          y: 0,
        },
        {
          y: `-${2 * (index + 1)}vh`,
        },
        0,
      );
    }
  }
  return tl;
};
