const imageFragment = /* glsl */ `
#ifdef GL_ES
precision highp float;
#endif

#define PI 3.1415926535897932384626433832795

varying vec2 vUv;
// uniform float u_circle;
uniform float u_circleSize;
uniform float u_intensity;
uniform vec2 u_resolution;
uniform vec2 u_offset;
uniform vec2 u_scale;
uniform sampler2D u_texture;
uniform float u_dither;
uniform vec2 u_texture_size;

highp float random(highp vec2 coords) {
   return fract(sin(dot(coords.xy, vec2(12.9898,78.233))) * 43758.5453);
}

float map(float inMin, float inMax, float outMin, float outMax, float value) {
	return outMin + (outMax - outMin) * (value - inMin) / (inMax - inMin);
  }

  float Circle( vec2 p, float r )
  {
	  return 1. - smoothstep(-0.2, r, length(p) - r);
  }


void main() {
	highp float NOISE_GRANULARITY = u_dither / 255.0;
	vec2 textureCoordinates = gl_FragCoord.xy / u_resolution.xy;

	float mixer = mix(-NOISE_GRANULARITY, NOISE_GRANULARITY, random(textureCoordinates));

	vec2 scaledUv = (vUv - 0.5) * (1.0 / u_scale) + 0.5;

	vec4 texture = texture2D(u_texture, scaledUv);

	float circleSize = u_circleSize;
	// map offset 0 to 1 to texture size minus radius size, so that it doesn't go too far outside the frame
	vec2 offset = vec2(map(0.,1.0, circleSize, 1.0 - circleSize, u_offset.x),map(0., 1., circleSize, 1.0 - circleSize,  1.0 - u_offset.y));

	// second cicle
	float circleSize2 = circleSize * 0.75;
	vec2 offset2 = vec2(map(0.,1.0, circleSize2, 1.0 - circleSize2, u_offset.x),map(0., 1., circleSize2, 1.0 - circleSize2,  1.0 - u_offset.y));

	float alpha = Circle((scaledUv - offset ) , circleSize);

	// add circle to opposite position
	alpha = mix(alpha, Circle((scaledUv - abs(1.0 - offset2) ) , circleSize2), 0.5) * 2.0;

	gl_FragColor = texture;
	gl_FragColor.a = alpha;
    gl_FragColor += mixer;
}
`;

export default imageFragment;
