import linkFragment from '~/components/atoms/link/fragment';

import navGroupFragment from './navGroup/fragment';

const navItemTypes = `
  _type == 'link' => {
    _key,
    ${linkFragment}
  },
  _type == 'navGroup' => {
    _key,
    ${navGroupFragment}
  },
  _type == 'button' => {
    _key,
    _type,
    "to": {
      ${linkFragment}
    }
  }
`;

const navigation = `
  "title": coalesce(title, ''),
  "primaryLinks": coalesce(
    primaryLinks[] {
      ${navItemTypes}
    },
    []
  ),
  "rightLinks": coalesce(rightLinks[] {
    ${navItemTypes}
  }, []),
  "mobileNavBottomLinks": coalesce(mobileNavBottomLinks[] {
    ${navItemTypes}
  }, []),

`;

export default navigation;
