import { breakpoints } from '@frameio-bs/tokens';
import { gsap } from 'gsap';
import { CSSProperties, useCallback, useRef } from 'react';

import {
  cn,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';
import addToRefArray from '~/utils/addToRefArray';

import {
  getBlockClassesFromContent,
  getElementAttributesFromOptions,
} from '../../utils';
import getRefFromOptions from '../../utils/getRefFromOptions';
import blockStyles from './styles.module.css';
import { PortableTextSpacerProps } from './types';

const Spacer = ({
  module,
  value,
  typeName,
  typeCategory,
}: PortableTextSpacerProps) => {
  const optionsProps = getElementAttributesFromOptions(
    module,
    typeCategory,
    typeName,
  );
  const { className } = optionsProps;
  const { _key, sizes, showKeyline, moduleStyles = null } = value;
  const ref = getRefFromOptions(module, typeCategory, typeName);

  const renderClass = getBlockClassesFromContent(
    _key,
    value,
    cn(className, blockStyles.spacer, showKeyline && blockStyles.showKeyline),
    [blockStyles, moduleStyles],
  );

  const style: Record<string, string> = {};

  const toRem = (pixels: number): string => `${pixels / 10}rem`;

  const progressSetter = useRef<(value: string) => void>();
  const $keyline = useRef<HTMLHRElement>(null);
  const onProgress = useCallback((progress: number) => {
    if (progressSetter.current && blockStyles.showKeyline)
      progressSetter.current(`${Math.min(100, progress * 300)}%`);
  }, []);

  useLayoutEffect(() => {
    if ($keyline.current) {
      progressSetter.current = gsap.quickSetter(
        $keyline.current,
        '--keyline-scale',
      ) as (value: string) => void;
    }
  }, []);

  useScrollProgress($keyline, onProgress);

  if (sizes && sizes.length) {
    const allBreakpoint = sizes.find((size) => size.breakpoint === 'all');
    let lastHeight = 0;
    for (const breakpoint of breakpoints) {
      const height = allBreakpoint
        ? allBreakpoint.height
        : sizes.find((size) => size.breakpoint === breakpoint.name)?.height ||
          lastHeight;
      lastHeight = height;
      style[`--height-${breakpoint.name}`] = toRem(height);
    }
  }

  return (
    <div
      ref={(element: HTMLDivElement) => {
        if (ref) {
          addToRefArray(element, ref);
        }
      }}
      className={renderClass}
      style={style as CSSProperties}
    >
      {showKeyline && <hr ref={$keyline} className={blockStyles.keyline} />}
    </div>
  );
};

export default Spacer;
