import { PortableText as PortableTextToReact } from '@portabletext/react';
import { mergeRefs } from '@react-aria/utils';
import { ForwardedRef, forwardRef, useRef } from 'react';

import { RaggingType } from '~/types/decorations.d';
import { cn } from '~/utils';
import addToRefArray from '~/utils/addToRefArray';

import { listItemConfig } from './config';
import styles from './styles.module.css';
import {
  PortableTextConfig,
  PortableTextCustomContent,
  PortableTextProps,
} from './types.d';
import { makeBlock, makeList, makeMarks, makeTypes } from './utils';

const PortableText = (
  module: PortableTextProps,
  ref: ForwardedRef<HTMLDivElement>,
) => {
  const {
    alignment,
    className,
    content = [],
    customTag,
    isAutoGrid = true,
    isAutoRag = true,
    ragging = RaggingType.DEFAULT,
  } = module;

  const $block = useRef<HTMLDivElement>(null);

  const $columnRefs = useRef<HTMLDivElement[]>([]);

  // empty field, don't render
  if ('content' in content && content.content === null) {
    return null;
  }

  // columns (start)
  let columnIndex = 0;
  const contentPerColumns: PortableTextCustomContent[][] = [];

  // If we have both, we want the first column to be even smaller
  let hasToutsAndColumns = false;
  if (Array.isArray(content)) {
    for (let i = 0; i < content.length; i++) {
      const columnContent: PortableTextCustomContent = content[i];
      if (columnContent._type === 'block.touts') {
        hasToutsAndColumns = true;
      }

      if (columnContent._type === 'block.column') {
        columnIndex++;
      } else {
        if (!contentPerColumns[columnIndex]) {
          contentPerColumns[columnIndex] = [];
        }
        contentPerColumns[columnIndex].push(columnContent);
      }
    }
  } else {
    contentPerColumns.push([content]);
  }

  const portableTextComponents: PortableTextConfig = {
    block: makeBlock(module),
    list: makeList(module),
    listItem: listItemConfig, // keeping this one simple, no custom rendering
    marks: makeMarks(module),
    types: makeTypes(module),
  };

  const renderClass = cn(
    styles.portableText,
    contentPerColumns.length > 1 &&
      isAutoGrid &&
      styles.isMultipleColumnsWrapper,
    contentPerColumns.length === 0 &&
      isAutoGrid &&
      styles.isSingleColumnWrapper,
    className,
  );

  const columnClass = cn(
    styles.portableTextColumn,
    alignment === ('center' || alignment === 'centerAll') && styles.alignCenter,
    alignment === 'centerAll' && styles.alignCenterAll,
    hasToutsAndColumns && styles.hasTouts,
    isAutoGrid && styles.isAutoGrid,
    isAutoRag && styles.isAutoRag,
    ragging === RaggingType.TIGHT && styles.ragTight,
    ragging === RaggingType.WIDE && styles.ragWide,
    contentPerColumns.length > 1 && styles.isMultipleColumns,
    contentPerColumns.length === 1 && styles.isSingleColumn,
  );

  const Tag = (customTag as 'div') || 'div';

  return (
    <Tag className={renderClass} ref={mergeRefs($block, ref)}>
      {contentPerColumns.map(
        (columnContent: PortableTextCustomContent[], index: number) => {
          return (
            <Tag
              key={columnContent[0]._key}
              className={cn(
                columnClass,
                contentPerColumns.length > 1 && styles[`isColumn${index + 1}`],
              )}
              ref={(element: HTMLDivElement) =>
                $columnRefs && addToRefArray(element, $columnRefs)
              }
            >
              <PortableTextToReact
                value={columnContent}
                components={portableTextComponents}
              />
            </Tag>
          );
        },
      )}
    </Tag>
  );
};

export default forwardRef(PortableText);
