const gradient = /* glsl */ `
#ifdef GL_ES
precision highp float;
#endif

#define PI 3.1415926535897932384626433832795

varying vec2 vUv;
uniform vec2 u_resolution;
uniform float u_intensity;
uniform float u_blurAmount;
uniform vec3 u_startColor;
uniform vec3 u_stopColor;
uniform float u_startPosition;
uniform float u_stopPosition;
uniform float u_orientation;

const highp float NOISE_GRANULARITY = 2./255.0;

highp float random(highp vec2 coords) {
   return fract(sin(dot(coords.xy, vec2(12.9898,78.233))) * 43758.5453);
}

float Circle( vec2 p, float r )
{
    return 1. - smoothstep(-0.2, r, length(p) - r);
}

mat2 rotate2d(float _angle){
    return mat2(cos(_angle),-sin(_angle),
                sin(_angle),cos(_angle));
}

void main() {
	vec2 textureCoordinates = gl_FragCoord.xy/u_resolution.xy;

	float mixer = mix(-NOISE_GRANULARITY, NOISE_GRANULARITY, random(textureCoordinates));

	vec2 rotatedTextureCoordinates = rotate2d(u_orientation) * textureCoordinates;
	vec3 color = mix(u_startColor, u_stopColor, smoothstep(u_startPosition, u_stopPosition, rotatedTextureCoordinates.x));

	textureCoordinates -= vec2(0.5, 0.5);

	float circle = Circle(textureCoordinates, 0.25);

	gl_FragColor = vec4(color.r, color.g, color.b, circle * u_intensity);

    gl_FragColor.a += mixer;
}
`;

export default gradient;
