//  Video is meant to be embedded within a  molecule.
// It is not meant as a standalone - its SEO properties come from .
import { gsap } from 'gsap';
import {
  CSSProperties,
  ForwardedRef,
  forwardRef,
  RefObject,
  useEffect,
  useImperativeHandle,
  useRef,
  useState,
} from 'react';
import ReactPlayer, { FileConfig } from 'react-player/file';

import Image from '~/components/atoms/image';
import useUIStore from '~/state/ui';
import { cn } from '~/utils';

import Controls from './controls/Controls';
import styles from './styles.module.css';
import { VideoProps, VideoRef } from './types';
import useVideoState from './utils/useVideoState';

const Video = (
  {
    aspectRatio,
    canAutoplay = false,
    className,
    controls = false,
    cover,
    details,
    isLooping = true,
    isMuted: isInitiallyMuted = false,
    playsInline = true,
    src,
    thumbnail,
    willAutoplay = false,
    onReady,
    onEnded,
  }: VideoProps,
  ref: ForwardedRef<VideoRef>,
) => {
  const hlsSupport = useUIStore((state) => state.hlsSupport);
  const $wrapper = useRef<HTMLDivElement>(null);
  const playerRef = useRef<ReactPlayer>(null);

  const [
    playedSeconds,
    loadedSeconds,
    duration,
    isPlaying,
    isMuted,
    {
      onProgress,
      onDuration,
      onPlay,
      onPause,
      onSeek,
      seekTo,
      play,
      pause,
      toggleMute,
      toggleFullScreen,
    },
  ] = useVideoState(playerRef, $wrapper, isInitiallyMuted);

  const getInternalPlayer = () => {
    if (playerRef.current && playerRef.current instanceof ReactPlayer) {
      return playerRef.current?.getInternalPlayer();
    } else if (playerRef.current) {
      return playerRef.current;
    }
    return null;
  };

  useImperativeHandle(
    ref,
    () => ({
      get $wrapper() {
        return $wrapper;
      },
      get $player() {
        return (getInternalPlayer() as HTMLVideoElement) || null;
      },
      play: () => {
        const internalPlayer = getInternalPlayer();
        if (internalPlayer) {
          internalPlayer.play();
        }
      },
      pause: () => {
        const internalPlayer = getInternalPlayer();
        if (internalPlayer) {
          internalPlayer.pause();
        }
      },
      restart: () => {
        const internalPlayer = getInternalPlayer();
        if (internalPlayer instanceof HTMLVideoElement) {
          internalPlayer.currentTime = 0;
        } else if (internalPlayer) {
          internalPlayer.seekTo(0);
        }
      },
      setVolume: (volume: number) => {
        const internalPlayer = getInternalPlayer();
        if (internalPlayer) {
          internalPlayer.volume = volume;
        }
      },
      muteSmoothly: (duration?: number) => {
        const internalPlayer = getInternalPlayer();
        if (internalPlayer) {
          gsap.to(internalPlayer, {
            volume: 0,
            duration: duration || 0.4,
          });
        }
      },
    }),
    [],
  );

  // Because react-player doesn't support SSR
  const [isBrowser, setIsBrowser] = useState(false);
  useEffect(() => {
    setIsBrowser(true);
  }, []);

  const fileConfig: FileConfig = {
    forceHLS: !hlsSupport,
    hlsOptions: {
      testBandwidth: false,
      startLevel: 4,
      capLevelToPlayerSize: true,
    },
  };

  return (
    <div
      className={cn(styles.wrapper, className)}
      ref={$wrapper}
      {...(typeof aspectRatio === 'number'
        ? { style: { '--aspect-ratio': aspectRatio } as CSSProperties }
        : {})}
    >
      {thumbnail && (
        <div aria-hidden={true}>
          <Image
            source={thumbnail}
            className={styles.thumbnail}
            quality={20}
            onReady={onReady}
          />
        </div>
      )}
      {isBrowser && (
        <>
          <ReactPlayer
            ref={playerRef as RefObject<ReactPlayer>}
            className={cn(styles.video, cover && styles.cover)}
            autoPlay={canAutoplay && willAutoplay}
            config={fileConfig}
            controls={false}
            height="100%"
            loop={isLooping}
            muted={isMuted}
            playing={canAutoplay && willAutoplay}
            playsinline={playsInline}
            url={src}
            width="100%"
            onDuration={onDuration}
            onPause={onPause}
            onPlay={onPlay}
            onProgress={onProgress}
            onSeek={onSeek}
            onEnded={onEnded}
          />
          {controls && (
            <Controls
              details={details}
              duration={duration}
              isMuted={isMuted}
              isPlaying={isPlaying}
              loadedSeconds={loadedSeconds}
              playedSeconds={playedSeconds}
              pause={pause}
              play={play}
              seekTo={seekTo}
              toggleFullScreen={toggleFullScreen}
              toggleMute={toggleMute}
            />
          )}
        </>
      )}
    </div>
  );
};

export default forwardRef(Video);
