import { JsxRenderProps } from '../../types';

const MailTo = ({ children, className, value, text }: JsxRenderProps) => {
  const href = value?.email || value?.href || '';
  return (
    <span className={className}>
      <a href={`mailto:${href}`}>{text || children}</a>
    </span>
  );
};

export default MailTo;
