import { cn } from '~/utils';

import defaultStyles from '../styles.module.css';
import { BlockElementProps, PortableTextProps } from '../types';
import getElementPropsFromOptions from './getElementPropsFromOptions';

// return spreadable element attributes based on module.options

const getElementAttributesFromOptions = (
  module: PortableTextProps,
  blockKey: string,
  elementKey: string,
  groupKey = '',
): BlockElementProps => {
  const elementProps = getElementPropsFromOptions(module, blockKey, elementKey);

  const { className = '', ref, moduleStyles } = elementProps;

  const elementKeyStyle = elementKey.replace(new RegExp('block.', 'g'), '');
  // there can be default styles for blocks, lists, marks, types
  // this is in addition to styles for group keys and individual style styles
  const renderClass = cn(
    defaultStyles[elementKeyStyle],
    defaultStyles[blockKey],
    defaultStyles[groupKey],
    className,
  );

  return { className: renderClass, ref, moduleStyles };
};

export default getElementAttributesFromOptions;
