import { gsap } from 'gsap';
import { CSSProperties, useCallback, useEffect, useRef, useState } from 'react';

import Observer from '~/components/atoms/observer';
import GlowMedia from '~/components/molecules/glowMedia';
import PortableText from '~/components/molecules/portableText';
import { lightAnimation } from '~/components/molecules/portableText/animation';
import {
  cn,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';
import { EaseType } from '~/utils/singletons/Easing';

import styles from './styles.module.css';
import { CMSModuleHeroImageWallProps } from './types';

const HeroImageWall = ({
  content,
  images,
  background,
  className,
  garageDoorRevealParent,
  slug,
}: CMSModuleHeroImageWallProps) => {
  const hasAnimated = useRef(false);
  const $element = useRef<HTMLDivElement>(null);
  const $title = useRef<HTMLDivElement>(null);

  const visibleImages = useRef<null | number>(null);
  const hiddenImages = useRef<null | number>(null);
  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  const loadedImages = useRef(0);

  const $images = useRef(Array(images.length));

  const progressSetter = useRef<ReturnType<typeof gsap.quickSetter>>();

  const onImageReady = useCallback(() => {
    loadedImages.current++;
    if (
      visibleImages.current !== null &&
      loadedImages.current >= visibleImages.current &&
      $element.current &&
      !hasAnimated.current &&
      $title.current
    ) {
      hasAnimated.current = true;
      const tl = gsap.timeline({ delay: 0.3 });
      tl.add(
        lightAnimation({
          $columnRefs: { current: [$title.current] },
          shouldWaitForFontLoad: false,
          paused: false,
          amplitude: 1.7,
        }),
      );
      tl.add('image animation', 0);
      tl.to(
        $element.current,
        {
          '--entry-animation-progress': 1,
          duration: 1.4,
          ease: EaseType.DEFAULT,
        },
        'image animation',
      );
      tl.to(
        $element.current,
        {
          '--entry-animation-opacity': 1,
          duration: 0.3,
        },
        'image animation',
      );
    }
  }, []);

  useLayoutEffect(() => {
    progressSetter.current = gsap.quickSetter(
      $element.current,
      '--image-wall-progress',
    );
  }, []);

  const onProgress = useCallback((progress: number) => {
    if (progressSetter.current) {
      progressSetter.current(progress);
    }
  }, []);

  useScrollProgress($element, onProgress, {
    $garageDoorElement: garageDoorRevealParent,
  });

  useEffect(() => {
    const observer = new IntersectionObserver((entries) => {
      for (const entry of entries) {
        if (entry.isIntersecting) {
          visibleImages.current === null
            ? (visibleImages.current = 1)
            : visibleImages.current++;
        } else {
          hiddenImages.current === null
            ? (hiddenImages.current = 1)
            : hiddenImages.current++;
        }
        observer.unobserve(entry.target);
      }
    });

    for (const $image of $images.current) {
      observer.observe($image.$media.current);
    }
  }, []);

  return (
    <section
      className={cn(
        styles.heroImageWall,
        background && styles.hasBackground,
        className,
      )}
      style={
        {
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
      ref={$element}
    >
      <div className={styles.contentWrapper}>
        <PortableText
          content={content}
          className={styles.content}
          animate={false}
          ref={$title}
          options={{
            block: {
              bodyLarge: {
                className: styles.subtitle,
              },
              title1: {
                className: styles.title,
              },
            },
          }}
        />
        <Observer
          callback={updateIsInView}
          options={{ rootMargin: '200% 0%' }}
          className={styles.imagesWrapper}
        >
          {images.map(
            (image, i) =>
              image && (
                <GlowMedia
                  key={image._key}
                  ref={(node) => ($images.current[i] = node)}
                  sanityMedia={image}
                  className={styles.imageWrapper}
                  elementClassName={styles.image}
                  glowConfig={{
                    intensity: 0.3,
                    scale: 1.5,
                    type: 'IMAGE',
                  }}
                  onReady={onImageReady}
                  aspectRatio={parseInt(styles.aspectRatio)}
                  isDisplayed={isInView !== false}
                />
              ),
          )}
        </Observer>
      </div>
    </section>
  );
};

export default HeroImageWall;
