/**
 * Implements Drift Button from Portable text with an integration link and a fallback link, if Drift is not available.
 * @param props see ButtonBase props
 * @param integrationLink Link for when Drift is available in the window
 * @param fallbackLink Link for when Drift is not available in the window
 * @example <DriftButton props={props} />
 */
import { ForwardedRef, forwardRef } from 'react';

import ButtonBase from '../ButtonBase';
import { ForwardedButtonRef } from '../ButtonBase.types.d';
import { DriftButtonProps } from './DriftButton.types.d';

declare global {
  interface Window {
    drift?: any;
  }
}

const DriftButton = (
  props: DriftButtonProps,
  ref: ForwardedRef<ForwardedButtonRef>,
) => {
  const { integrationLink, fallbackLink } = props;
  // TODO: implement functionality to check for changes in window.drift
  const to =
    typeof window !== 'undefined' && typeof window.drift !== 'undefined'
      ? integrationLink
      : fallbackLink;

  if (!to) return null;

  return (
    <ButtonBase {...props} to={to} ref={ref}>
      {to.label}
    </ButtonBase>
  );
};

export default forwardRef(DriftButton);
