import { create } from 'zustand';

type State = {
  whichSubNavShowing: string | null | undefined;
  currentGroup: string | null;
  mobileNavOpen: boolean;
  isMobileNav: boolean;
  setWhichSubNavShowing: (value: string | null | undefined) => void;
  setCurrentGroup: (value: string | null) => void;
  setMobileNavOpen: (value: boolean) => void;
  setIsMobileNav: (value: boolean) => void;
};

const NavState = create<State>((set) => ({
  whichSubNavShowing: null,
  currentGroup: null,
  mobileNavOpen: false,
  isMobileNav: true,
  setWhichSubNavShowing: (value: string | null | undefined) =>
    set({ whichSubNavShowing: value }),
  setCurrentGroup: (value: string | null) => set({ currentGroup: value }),
  setMobileNavOpen: (value: boolean) => set({ mobileNavOpen: value }),
  setIsMobileNav: (value: boolean) => set({ isMobileNav: value }),
}));

export default NavState;
