import {
  CSSProperties,
  ForwardedRef,
  forwardRef,
  useImperativeHandle,
  useRef,
} from 'react';

import SvgPlusIcon from '~/assets/svg/plus-icon.svg';
import ModalCarouselArrows from '~/components/atoms/carouselControls/modalCarouselArrows';
import Image from '~/components/atoms/image';
import { MemoPortableText as PortableText } from '~/components/molecules/portableText';
import { dict } from '~/data/stores/Dictionary';
import { cn } from '~/utils';

import { FancyTestimonial } from '../../types';
import { modalEnterTimeline, modalLeaveTimeline } from './animation';
import styles from './styles.module.css';
import {
  TestimonialsModalContentProps,
  TestimonialsModalContentRef,
} from './types';
const TestimonialsModalContent = (
  {
    carouselIndex,
    sourceRect,
    testimonials,
    handleNext,
    handlePrevious,
    startClosingModal,
  }: TestimonialsModalContentProps,
  ref: ForwardedRef<TestimonialsModalContentRef>,
) => {
  // Dummy div used to compute in grid size
  const $dummy = useRef<HTMLDivElement>(null);
  const $closeButton = useRef<HTMLButtonElement>(null);
  const $modalControls = useRef<HTMLDivElement>(null);
  const $modalContent = useRef<HTMLDivElement>(null);
  const $carouselItems = useRef<HTMLLIElement[]>(Array(testimonials.length));

  useImperativeHandle(
    ref,
    () => {
      return {
        enterTimeline: () =>
          modalEnterTimeline({
            carouselIndex,
            $dummy,
            $modalContent,
            $modalControls,
            $closeButton,
            $carouselItems,
            sourceRect,
          }),
        leaveTimeline: () =>
          modalLeaveTimeline({
            $modalContent,
          }),
      };
    },
    [carouselIndex, sourceRect],
  );

  //   TODO: Use Keen slider
  return (
    <>
      <div
        aria-hidden={true}
        className={cn(styles.modalBackground)}
        onClick={startClosingModal}
      />
      <div ref={$dummy} aria-hidden="true" className={styles.dummy}></div>
      <div
        ref={$modalContent}
        aria-modal="true"
        role="dialog"
        className={styles.modalContent}
      >
        <ul
          className={cn(styles.carousel)}
          style={{ '--carousel-index': carouselIndex } as CSSProperties}
        >
          {testimonials.map((testimonial: FancyTestimonial, index: number) => {
            const isActive = carouselIndex === index;
            return (
              <li
                ref={(node: HTMLLIElement) =>
                  ($carouselItems.current[index] = node)
                }
                className={styles.carouselItem}
                key={testimonial._key}
                aria-hidden={isActive ? 'false' : 'true'}
              >
                <Image
                  className={styles.testimonialMedia}
                  source={testimonial.image}
                />

                <div className={styles.testimonialContent}>
                  <h6 className={styles.testimonialTitle}>
                    {testimonial.label}
                  </h6>

                  <PortableText
                    animate={false}
                    isAutoGrid={false}
                    content={testimonial.text}
                    options={{
                      block: {
                        titles: {
                          className: styles.titles,
                        },
                        bodySmall: {
                          className: styles.body,
                        },
                      },
                    }}
                  />
                </div>
              </li>
            );
          })}
        </ul>
        <button
          ref={$closeButton}
          className={styles.closeButton}
          onClick={startClosingModal}
        >
          <SvgPlusIcon className={styles.buttonIcon} />
          <span className="visuallyHidden">{dict('clickToClose')}</span>
        </button>

        <ModalCarouselArrows
          ref={$modalControls}
          activeIndex={carouselIndex}
          total={testimonials.length}
          handleNext={handleNext}
          handlePrevious={handlePrevious}
          prevTitle={testimonials[(carouselIndex || 0) - 1]?.label}
          nextTitle={testimonials[(carouselIndex || 0) + 1]?.label}
        />
      </div>
    </>
  );
};

export default forwardRef(TestimonialsModalContent);
