/**
 * The default CTA button, used to render buttons from Portable Text. Composes functionality from ButtonBase
 * @param props see ButtonBase props
 * @param disabled If the cta is disabled
 * @param onClick Callback function for when the button is clicked
 * @param asNonInteractive Renders a <span> instead of a button or anchor tag. Used in cases where the interactive element is the parent of the cta
 * @param className
 * @param children
 * @example <Button props={props} />
 */

import { ForwardedRef, forwardRef } from 'react';

import ButtonBase from '../ButtonBase';
import { ForwardedButtonRef } from '../ButtonBase.types.d';
import { ButtonProps } from './Button.types.d';

const Button = (props: ButtonProps, ref: ForwardedRef<ForwardedButtonRef>) => {
  const { children } = props;

  return (
    <ButtonBase {...props} ref={ref}>
      {children}
    </ButtonBase>
  );
};

export default forwardRef(Button);
