import { SanityImageCrop, SanityImageHotspot } from '@sanity/asset-utils';

// TODO: Try to see if we can infer the keys from the dynamic breakpoint tokens
export type breakpointName = 'base' | 'xs' | 'sm' | 'md' | 'lg' | 'xl';
export type ImageSizes =
  | Record<
      breakpointName,
      {
        width: number;
        height?: number;
      }
    >
  | {
      width: number;
      height?: number;
    };

export type CMSAsset = {
  _id: string;
  darkVibrantBackground: string;
  dominantBackground: string;
  darkMutedBackground: string;
  lightMutedBackground: string;
  height: number;
  width: number;
  aspectRatio: number;
  isOpaque: boolean;
};

export type CMSImage = {
  _key?: string;
  alt: string;
  crop: SanityImageCrop;
  hotspot: SanityImageHotspot;
  mediaType: 'image';
  asset: CMSAsset;
};

export type ImageProps = {
  source: CMSImage;
  className?: string;
  quality?: number;
  onReady?: ($element: HTMLImageElement) => void;
} & (
  | {
      preload: true;
      sizes: ImageSizes;
      isDisplayed?: never;
    }
  | {
      preload?: never;
      sizes?: never;
      isDisplayed?: boolean;
    }
) &
  (
    | {
        cover: true;
        contain?: never;
      }
    | {
        cover?: never;
        contain: true;
      }
    | {
        cover?: undefined;
        contain?: undefined;
      }
  );

export const MAX_DPR = 2;

export const MAX_WIDTH = 3840;

export const QUALITY_CUSHION = 1.2;
