import { SanityClient } from '@sanity/client';
import { createClient } from 'next-sanity';

import { config } from './config';

const NEXT_PUBLIC_SANITY_STUDIO_API_USE_DRAFTS =
  process.env.NEXT_PUBLIC_SANITY_STUDIO_API_USE_DRAFTS;
const NEXT_PUBLIC_SANITY_STUDIO_API_TOKEN =
  process.env.NEXT_PUBLIC_SANITY_STUDIO_API_TOKEN;
const VERCEL_ENV = process.env.VERCEL_ENV;

// Set up the client for fetching data in the getProps page functions
export const publishedClient = createClient(config);

// Set up a draft client with serverless authentication for drafts
let draftClient: SanityClient;
if (VERCEL_ENV !== 'production') {
  let extraConfig = {};
  if (process.env.SANITY_STUDIO_API_USE_CDN !== 'true') {
    extraConfig = {
      useCdn: false,
      token: NEXT_PUBLIC_SANITY_STUDIO_API_TOKEN,
    };
  }
  draftClient = createClient({
    ...config,
    ...extraConfig,
  });
}

// Helper function for easily switching between normal client and draft client
// If `NEXT_PUBLIC_SANITY_STUDIO_API_USE_DRAFTS` is set to `true` in `.env` then we use the draft client
// If set to `false` then the use of the draft client is based on Next.js's preview mode
export const getClient = (useDraftDocuments: boolean): SanityClient => {
  return (useDraftDocuments ||
    NEXT_PUBLIC_SANITY_STUDIO_API_USE_DRAFTS === 'true') &&
    VERCEL_ENV !== 'production'
    ? draftClient
    : publishedClient;
};
