import { CSSProperties, ReactNode, RefObject } from 'react';

export enum GlowType {
  BAKED_CANVAS = 'bakedCanvas',
}

export enum GlowPosition {
  CENTER = 'center',
  TOP_CENTER = 'topCenter',
  BOTTOM_CENTER = 'bottomCenter',
  LEFT_CENTER = 'leftCenter',
  RIGHT_CENTER = 'rightCenter',
  TOP_LEFT = 'topLeft',
  TOP_RIGHT = 'topRight',
  BOTTOM_RIGHT = 'bottomRight',
  BOTTOM_LEFT = 'bottomLeft',
}
export enum GlowAlignment {
  CENTER = 'alignCenter',
  TOP = 'alignTop',
  LEFT = 'alignLeft',
  RIGHT = 'alignRight',
  BOTTOM = 'alignBottom',
}

export type GlowGradient = {
  startColor: string;
  stopColor: string;
  startPosition: number;
  stopPosition: number;
  orientation: number;
};

type GlowBaseConfig = {
  /**
   * The intensity of glow via CSS opacity. Will update in realtime.
   */
  intensity?: number;
  /**
   * The CSS scale of the canvas element. Will update in realtime.
   */
  scale?: number;
  /**
   * The CSS Blur amount (not used for Image glows). Will update in realtime.
   */
  blurAmount?: number;
  /**
   * Deprecated
   */
  animated?: boolean;
  /**
   * Set the position of Glow Circles. Changes require refresh.
   */
  position?: GlowPosition;
  /**
   * Scale the image within the glow (used only for Image glows). Changes require refresh.
   */
  imageScale?: number;
  /**
   * Rendered blur size within the canvas. Changes require refresh.
   */
  circleSize?: number;
  /**
   * Set the dithering value within the shader (nitty but sometimes needed). Changes require refresh.
   */
  dither?: number;
  /**
   * The CSS alignment of the canvas element. Will update in realtime.
   */
  align?: GlowAlignment;
};

export type GlowRef = {
  startGlow: (element: HTMLImageElement | HTMLVideoElement) => void;
  $wrapper: RefObject<HTMLDivElement>;
};

export type GlowProps = {
  type?: GlowType;
  glowImageSrc?: string;
  hasMedia?: boolean;
  className?: string;
  backgroundClassName?: string;
  style?: CSSProperties;
  config: GlowSource;
  children?: ReactNode;
};

export type GlowColorSource = GlowBaseConfig & {
  color: string;
  type: 'COLOR';
};

export type GlowGradientSource = GlowBaseConfig & {
  gradient: GlowGradient;
  type: 'GRADIENT';
};

export type GlowImageSource = GlowBaseConfig & {
  image?: HTMLImageElement;
  type: 'IMAGE';
};

export type GlowSource = GlowColorSource | GlowGradientSource | GlowImageSource;
