/* eslint-disable  @typescript-eslint/no-explicit-any */

type KeyProp = string | number;
type PrimitiveProps = string | number | boolean;
type NodeProps =
  | Record<string, object | PrimitiveProps>
  | Record<string, object | PrimitiveProps>[];
type ValueProp = PrimitiveProps;

type ResultProps = {
  node: NodeProps;
  key: KeyProp;
  value: ValueProp;
  parent: NodeProps;
  parentKey?: KeyProp;
};

type ResultsProps = {
  haystack: NodeProps;
  results: ResultProps[];
};

function recursiveSearchAll(
  haystack: NodeProps,
  key: KeyProp,
  value: ValueProp,
) {
  // Some validation
  if (typeof key !== 'string') {
    throw new Error('Invalid parameter: `key` has to be a string.');
  }

  const returnValue: ResultsProps = {
    haystack,
    results: [],
  };

  function search(
    node: NodeProps,
    key: KeyProp,
    value: ValueProp,
    parent: NodeProps,
    parentKey?: KeyProp,
  ) {
    // Accepts arrays of objects.
    if (Array.isArray(node)) {
      for (const child of node) {
        search(child, key, value, node, parentKey);
      }
    }

    // Process objects.
    if (node !== null && typeof node === 'object') {
      // IMPORTANT: typeof null === 'object', this is a known JavaScript bug [1].

      const nodeObject = node as Record<string, object | PrimitiveProps>;

      if (
        key in nodeObject &&
        typeof key === 'string' &&
        nodeObject[key] === value
      ) {
        // See [2].
        // We found it!
        const result: ResultProps = {
          node,
          key,
          value,
          parent,
          parentKey,
        };

        returnValue.results.push(result);
      }

      // Check for other nested objects or arrays.
      for (const curKey in nodeObject) {
        // The hasOwnProperty function is used to exclude properties found in the prototype chain.
        if (
          (typeof nodeObject.hasOwnProperty === 'function' &&
            nodeObject.hasOwnProperty(curKey)) ||
          (nodeObject[curKey] ?? false)
        ) {
          // Recursive call: iterates through all the object properties.
          const nodeProp = nodeObject[curKey] as NodeProps;
          search(nodeProp, key, value, node, curKey);
        }
      }
    }

    // Nothing worked, tough luck.
    return returnValue;
  }

  return search(haystack, key, value, haystack, '');
}

export default recursiveSearchAll;
