import { mergeRefs } from '@react-aria/utils';
import { gsap } from 'gsap';
import {
  ForwardedRef,
  forwardRef,
  KeyboardEvent,
  useCallback,
  useEffect,
  useRef,
} from 'react';

import ButtonClose from '~/components/atoms/Buttons/UI/ButtonClose';
import Shadow from '~/components/molecules/shadow';
import useUIStore from '~/state/ui';
import { cn, useIsomorphicLayoutEffect as useLayoutEffect } from '~/utils';
import ScrollPosition from '~/utils/domEvents/ScrollPosition';
import useFocusTrap from '~/utils/useFocusTrap';

import { fadeInTimeline, fadeOutTimeline } from './Modal.animation';
import { ModalBackgroundType, ModalProps } from './Modal.types.d';
import styles from './ModalWrapper.styles.module.css';

const ModalWrapper = (
  {
    children,
    className,
    backgroundType = ModalBackgroundType.ALPHA,
    wrapperClassName,
    backgroundClassName,
    contentClassName,
    defaultCloseButton = true,
    shouldClose = false,
    hasShadow = true,
    footer,
    onClose,
    onKeyDown,
    transitionEnter,
    transitionLeave,
    onEnter,
    onLeave,
  }: ModalProps,
  ref: ForwardedRef<HTMLDivElement>,
) => {
  const isAnimating = useRef<boolean>(false);
  const scrollYStart = useRef(0);
  const $modal = useRef<HTMLDivElement>(null);
  const $modalBackground = useRef<HTMLDivElement>(null);
  const $modalContent = useRef<HTMLDivElement>(null);
  const $modalFooter = useRef<HTMLDivElement>(null);
  const hasFullscreenVideo = useUIStore((state) => state.hasFullscreenVideo);

  const prevWindowOrientation = useRef<number | string | null>();
  const onFocusTrapKeyDown = useFocusTrap($modal);

  useLayoutEffect(() => {
    $modal.current?.focus();
    isAnimating.current = true;
    if (transitionEnter) {
      transitionEnter({
        $modal,
        $modalContent,
        $modalBackground,
        done: () => {
          isAnimating.current = false;
        },
      });
    } else {
      //  if no custom transition, play the default on
      fadeInTimeline({
        $modal,
        $modalContent,
        $modalBackground,
        $modalFooter,
        onStart: () => {
          if (onEnter) onEnter();
        },
        onComplete: () => {
          isAnimating.current = false;
        },
      });
    }
  }, [transitionEnter, onEnter]);

  const handleKeyDown = (event: KeyboardEvent<HTMLElement>) => {
    if (isAnimating.current) return;
    if (event.code === 'Escape') {
      startClose();
    }
    if (onKeyDown) onKeyDown(event);

    // Focus trap
    if (onFocusTrapKeyDown) onFocusTrapKeyDown(event);
  };

  const resetContentState = ($el: HTMLDivElement | null) => {
    if (isAnimating.current) {
      return;
    }
    scrollYStart.current = ScrollPosition.y || 0;
    gsap.to($el, {
      y: 0,
      scale: 1,
      opacity: 1,
      duration: 0.2,
    });
  };

  const startClose = useCallback(() => {
    isAnimating.current = true;

    if (transitionLeave) {
      transitionLeave({
        $modal,
        $modalContent,
        $modalBackground,
        done: () => {
          if (onClose) onClose();
          isAnimating.current = false;
        },
      });
      return;
    }

    fadeOutTimeline({
      $modal,
      $modalContent,
      $modalBackground,
      $modalFooter,
      onStart: () => {
        if (onLeave) onLeave();
      },
      onComplete: () => {
        if (onClose) onClose();
      },
    });
  }, [onClose, transitionLeave, onLeave]);

  useEffect(() => {
    scrollYStart.current = ScrollPosition.y || 0;
    const unsubscribe = ScrollPosition.subscribe(({ y: scrollY }) => {
      if (isAnimating.current || hasFullscreenVideo) {
        return;
      }
      // window.orientation is deprecated in favor of screen.orientation, but it's not supported in safari
      const orientation = screen.orientation?.type || window.orientation;

      // when rotating on ios, a scroll event gets triggered before the resize/orientation event, causing the modal to close. ignore the scroll event if it's an orientation change, so users don't have to reopen the video to watch in landscape
      if (prevWindowOrientation.current !== orientation) {
        prevWindowOrientation.current = orientation;
        return;
      }
      if (scrollY && scrollYStart.current >= 0) {
        const scrollDiff = scrollY - scrollYStart.current;
        if (Math.abs(scrollDiff) > 100) {
          startClose();
        } else if ($modalContent.current && Math.abs(scrollDiff) > 0) {
          gsap.to([$modalContent.current, $modalFooter.current], {
            y: scrollDiff * -1,
            scale: 1 - Math.abs(scrollDiff) / 1000,
            duration: 0.2,
            overwrite: true,
            onComplete: () => resetContentState($modalContent.current),
          });
        }
      }
    });

    return () => {
      unsubscribe();
    };
  }, [startClose, hasFullscreenVideo]);

  useEffect(() => {
    if (shouldClose) {
      startClose();
    }
  }, [shouldClose, startClose]);

  const Wrapper = hasShadow ? Shadow : 'div';

  return (
    <section
      ref={mergeRefs(ref, $modal)}
      className={cn(styles.modal, className)}
      tabIndex={-1}
    >
      <div
        aria-hidden={true}
        ref={$modalBackground}
        className={cn(
          styles.modalBackground,
          styles[backgroundType],
          backgroundClassName,
        )}
        onClick={startClose}
        onKeyDown={handleKeyDown}
      />
      <Wrapper
        ref={$modalContent}
        className={cn(
          styles.modalWrapper,
          wrapperClassName,
          footer && styles.hasFooter,
        )}
      >
        <div
          aria-modal="true"
          role="dialog"
          className={cn(styles.modalContent, contentClassName)}
        >
          {defaultCloseButton && (
            <ButtonClose className={styles.closeButton} onClick={startClose} />
          )}
          {children}
        </div>
      </Wrapper>
      {footer && (
        <div ref={$modalFooter} className={styles.modalFooter}>
          {footer}
        </div>
      )}
    </section>
  );
};

export default forwardRef(ModalWrapper);
