import {
  CSSProperties,
  ForwardedRef,
  forwardRef,
  RefObject,
  useCallback,
  useImperativeHandle,
  useRef,
  useState,
} from 'react';

import DeviceWrapper from '~/components/atoms/deviceWrapper';
import { urlFor } from '~/components/atoms/image/utils';
import Observer from '~/components/atoms/observer';
import { VideoRef } from '~/components/atoms/video/types.d';
import Glow from '~/components/molecules/glow';
import {
  GlowAlignment,
  GlowPosition,
  GlowRef,
  GlowType,
} from '~/components/molecules/glow/types.d';
import Media from '~/components/molecules/media';
import PortableText from '~/components/molecules/portableText';
import Shadow from '~/components/molecules/shadow';
import { RaggingType } from '~/types/decorations.d';
import { cn, useScrollProgress } from '~/utils';

import styles from '../styles.module.css';
import { CustomGlowLayout, WrapInDeviceOptions } from '../types.d';
import { MediaWithTextContentProps, MediaWithTextContentRef } from './types';

const MAX_PARALLAX_PERCENT = 16;

const MediaWithTextContent = (
  {
    wrapInDevice,
    media,
    isReverseGarageDoor,
    text,
    onMediaProgress,
    textContentClassName,
    mediaWrapperClassName,
    glowConfig,
    textPosition,
    customGlowLayout,
    customGlowImage,
  }: MediaWithTextContentProps,
  ref: ForwardedRef<MediaWithTextContentRef>,
) => {
  const [isVisible, setIsVisible] = useState<boolean>(false);
  const $mediaContainer = useRef<HTMLDivElement>(null);
  const $media = useRef<HTMLElement | VideoRef>(null);
  const $glow = useRef<GlowRef>(null);
  const needsParallax = false;
  const CMSAspectRatio =
    'asset' in media.sanityMedia
      ? media.sanityMedia.asset.aspectRatio
      : media.sanityMedia.aspectRatio;
  const mediaAspectRatio =
    CMSAspectRatio && needsParallax
      ? CMSAspectRatio * (needsParallax ? 1 + MAX_PARALLAX_PERCENT / 100 : 1)
      : CMSAspectRatio;
  const mediaOriginalAspectRatio = CMSAspectRatio || null;

  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  const glowType = GlowType.BAKED_CANVAS;

  const handleMediaReady = useCallback(
    (element: HTMLImageElement | HTMLVideoElement) => {
      if ($glow.current) $glow.current.startGlow(element);
    },
    [],
  );

  useImperativeHandle(
    ref,
    () => ({
      onProgress: (progress: number, isInView: boolean) => {
        if (isInView && !isVisible) {
          setIsVisible(isInView);
        }
        if (media.sanityMedia?.mediaType === 'video') {
          const $videoRef = $media as RefObject<VideoRef>;

          if (
            $videoRef.current &&
            isInView &&
            progress > 0.3 &&
            $videoRef.current.$player?.paused === true &&
            (!$videoRef.current.$player?.ended || media.sanityMedia.isLooping)
          ) {
            $videoRef.current.play();
          }
          // If we scrolled past it
          if (!isInView) {
            $videoRef.current?.restart();
          }
        }
      },
    }),
    [media.sanityMedia],
  );

  const $buttons = useRef([] as HTMLDivElement[]);
  const $forms = useRef([] as HTMLDivElement[]);
  const $stats = useRef([] as HTMLDivElement[]);
  const $touts = useRef([] as HTMLDivElement[]);

  useScrollProgress($mediaContainer, onMediaProgress);

  const renderCustomGlow = () => {
    switch (customGlowLayout) {
      case CustomGlowLayout.CenterRightPurple:
        return (
          <Glow
            className={styles.centerRightGlow}
            type={GlowType.BAKED_CANVAS}
            config={{
              intensity: 0.4,
              scale: 1.5,
              color: '#9947EB',
              type: 'COLOR',
            }}
          />
        );
      case CustomGlowLayout.FullImageGlow:
        const url = urlFor(customGlowImage).quality(90).width(300).url();
        return (
          <>
            <Glow
              className={styles.fullGlow}
              glowImageSrc={isVisible ? url : undefined}
              type={GlowType.BAKED_CANVAS}
              config={{
                intensity: 0.55,
                scale: 1,
                circleSize: 0.35,
                imageScale: 1,
                align: GlowAlignment.TOP,
                position: GlowPosition.BOTTOM_CENTER,
                type: 'IMAGE',
              }}
            />
          </>
        );
    }

    return null;
  };

  const renderText = () => {
    if (text && Array.isArray(text)) {
      return (
        <PortableText
          content={text}
          ragging={
            wrapInDevice === WrapInDeviceOptions.studioDisplay ||
            wrapInDevice === WrapInDeviceOptions.macbookPro
              ? RaggingType.TIGHT
              : RaggingType.DEFAULT
          }
          className={cn(
            styles.textContent,
            textPosition === 'above' && styles.isTextAbove,
            wrapInDevice === WrapInDeviceOptions.studioDisplay &&
              styles.studioDisplay,
            wrapInDevice === WrapInDeviceOptions.macbookPro &&
              styles.macbookPro,
            textContentClassName,
          )}
          options={{
            block: {
              eyebrow: {
                className: styles.eyebrow,
              },
              title4: {
                tagName: 'h3',
              },
              titles: {
                className: styles.titles,
              },
              bodies: {
                className: styles.bodies,
              },
            },
            types: {
              'block.buttons': {
                className: styles.buttons,
                ref: $buttons,
              },
              'block.form': {
                className: styles.forms,
                ref: $forms,
              },
              'block.touts': {
                ref: $touts,
              },
              'block.stats': {
                ref: $stats,
              },
            },
          }}
        />
      );
    }
    return null;
  };

  return (
    <>
      {textPosition === 'above' && renderText()}
      <div
        className={cn(
          styles.mediaWrapper,
          wrapInDevice === WrapInDeviceOptions.studioDisplay &&
            styles.studioDisplay,
          wrapInDevice === WrapInDeviceOptions.macbookPro && styles.macbookPro,
          mediaWrapperClassName,
        )}
      >
        <Glow
          ref={$glow}
          type={glowType}
          className={styles.glow}
          hasMedia
          config={
            glowConfig || {
              type: 'IMAGE',
              intensity: 0.4,
              scale: 1.35,
              position: GlowPosition.TOP_RIGHT,
            }
          }
        />
        <Shadow className={styles.dropShadow} />
        <Observer
          className={styles.contentWrapper}
          callback={updateIsInView}
          options={{ rootMargin: '200% 0%' }}
        >
          <div
            ref={$mediaContainer}
            className={styles.mediaContainer}
            style={
              {
                ...(mediaAspectRatio
                  ? { '--media-aspect-ratio': mediaAspectRatio }
                  : {}),
                ...(mediaOriginalAspectRatio
                  ? {
                      '--media-original-aspect-ratio': mediaOriginalAspectRatio,
                    }
                  : {}),
              } as CSSProperties
            }
          >
            <Media
              ref={$media}
              className={cn(
                styles.media,
                isReverseGarageDoor && styles.noMediaParallax,
              )}
              name={media.name}
              sanityMedia={media.sanityMedia}
              onReady={handleMediaReady}
              isDisplayed={isInView !== false}
            />
          </div>
        </Observer>
        {wrapInDevice && (
          <DeviceWrapper
            deviceType={wrapInDevice}
            isInView={isInView !== false}
          />
        )}
      </div>
      {textPosition !== 'above' && renderText()}
      {renderCustomGlow()}
    </>
  );
};

export default forwardRef<MediaWithTextContentRef, MediaWithTextContentProps>(
  MediaWithTextContent,
);
