import { gsap } from 'gsap';
import { CSSProperties, useCallback, useRef } from 'react';

import decorationsStyles from '~/styles/theme/decorations.module.css';
import {
  cn,
  getSpacersStyles,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';

import MediaWithTextContent from './mediaWithTextContent';
import { MediaWithTextContentRef } from './mediaWithTextContent/types.d';
import styles from './styles.module.css';
import { CMSModuleMediaWithText } from './types.d';

const MAX_PARALLAX_PERCENT = 16;

const MediaWithText = ({
  media,
  mobileAlignment,
  text,
  hasSpacers,
  accentColor,
  wrapInDevice,
  garageDoorRevealParent,
  textPosition,
  background,
  isReverseGarageDoor,
  customGlowImage,
  customGlowLayout,
  slug,
}: CMSModuleMediaWithText) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  const $element = useRef<HTMLDivElement>(null);

  const progressSetter = useRef<(value: number) => void>();

  // eslint-disable-next-line @typescript-eslint/no-empty-function
  const contentRef = useRef<MediaWithTextContentRef>(null);

  useLayoutEffect(() => {
    progressSetter.current = gsap.quickSetter(
      $element.current,
      '--media-with-text-progress',
    ) as (value: number) => void;
  }, []);

  const onMediaProgress = useCallback(() => {
    // if (progressSetter.current && !isReverseGarageDoor) {
    //   progressSetter.current(progress);
    // }
  }, [isReverseGarageDoor]);

  useScrollProgress($element, contentRef.current?.onProgress, {
    $garageDoorElement: garageDoorRevealParent,
  });

  return (
    <section
      className={cn(
        styles.mediaWithText,
        text && textPosition !== 'above' && styles.withText,
        background && styles.hasBackground,
        mobileAlignment === 'right' && styles.isMobileRightAligned,
        mobileAlignment === 'left' && styles.isMobileLeftAligned,
        hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        isReverseGarageDoor &&
          (!Array.isArray(text) || textPosition === 'above') &&
          styles.isReverseGarageDoor,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
          '--max-parallax-percent': MAX_PARALLAX_PERCENT - 2,
        } as CSSProperties
      }
      ref={$element}
      id={slug}
    >
      <MediaWithTextContent
        text={text}
        wrapInDevice={wrapInDevice}
        media={media}
        isReverseGarageDoor={isReverseGarageDoor}
        onMediaProgress={onMediaProgress}
        ref={contentRef}
        textPosition={textPosition}
        customGlowLayout={customGlowLayout}
        customGlowImage={customGlowImage}
      />
    </section>
  );
};

export default MediaWithText;
