import { gsap } from 'gsap';
import { CSSProperties, useCallback, useRef } from 'react';

import PortableText from '~/components/molecules/portableText';
import useUIStore from '~/state/ui';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { cn, getSpacersStyles, useScrollProgress } from '~/utils';
import isBreakpointOrGreater from '~/utils/isBreakpointOrGreater';

import ImageSequence from '../../imageSequence';
import styles from './styles.module.css';
import { EndsWithTextProps } from './types';

const EndsWithText = ({
  sequence,
  top,
  belowText,
  garageDoorRevealParent,
  hasSpacers,
  accentColor,
  startOnMiddleOfScreen = false,
  background,
  className,
  slug,
}: EndsWithTextProps) => {
  const $element = useRef<HTMLDivElement>(null);
  const $textContainer = useRef<HTMLDivElement>(null);
  const $scrollOverlay = useRef<HTMLDivElement>(null);
  const previousIsInView = useRef<boolean | null>(null);
  const breakpoint = useUIStore((state) => state.breakpoint);

  const fadeOutOverlayEnd =
    breakpoint && isBreakpointOrGreater(breakpoint, 'sm') ? 0.3 : 0.4;
  const fadeOutImageEnd =
    breakpoint && isBreakpointOrGreater(breakpoint, 'sm') ? 0.5 : 0.5;

  const onProgress = useCallback((progress: number, isInView: boolean) => {
    // trigger overlay darkening when last frame of image sequences starts scrolling in
    // end animation when last frame is at 50%

    const clampedProgressStart = gsap.utils.clamp(
      0,
      fadeOutOverlayEnd,
      progress,
    );
    const clampedProgressEnd = gsap.utils.clamp(
      fadeOutOverlayEnd,
      fadeOutImageEnd,
      progress,
    );

    const normalizedProgressOverlayOpacity = gsap.utils.normalize(
      0,
      fadeOutOverlayEnd,
      clampedProgressStart,
    );

    const imageOpacity = gsap.utils.mapRange(
      fadeOutOverlayEnd,
      fadeOutImageEnd,
      1,
      0,
      clampedProgressEnd,
    );

    gsap.set($element.current, {
      '--text-overlay-opacity': normalizedProgressOverlayOpacity,
      '--image-opacity': imageOpacity,
    });

    if (isInView !== previousIsInView.current) {
      gsap.set($element.current, {
        // dont want this to show up until it is sticky
        '--text-overlay-display': isInView ? 'block' : 'none',
      });
    }

    previousIsInView.current = isInView;
  }, []);

  useScrollProgress($scrollOverlay, onProgress);
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);

  return (
    <section
      className={cn(
        className,
        background && styles.hasBackground,
        hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        hasSpacers?.after && decorationsStyles.hasSpacerAfter,
      )}
      ref={$element}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <ImageSequence
        canvasClassName={styles.canvas}
        sequence={sequence}
        startOnMiddleOfScreen={startOnMiddleOfScreen}
        top={top}
        garageDoorRevealParent={garageDoorRevealParent}
        className={styles.container}
      >
        {/* 100vh element at bottom of image sequence to track scroll progress of last frame only. not ideal to have this extra element but saves us many calculations to find out if we are scrolled to the last frame or not */}
        <div className={styles.scrollOverlay} ref={$scrollOverlay}></div>
        <figure className={styles.gradientOverlay}></figure>
      </ImageSequence>

      <div className={styles.textContainer} ref={$textContainer}>
        {belowText && (
          <PortableText
            content={belowText}
            className={styles.content}
            options={{
              block: {
                eyebrow: {
                  className: styles.eyebrow,
                },
                titles: {
                  className: styles.titles,
                },
                bodies: {
                  className: styles.bodies,
                },
              },
              types: {
                'block.buttons': {
                  className: styles.buttons,
                },
                'block.form': {
                  className: styles.forms,
                },
              },
            }}
          />
        )}
      </div>
    </section>
  );
};

export default EndsWithText;
