import { breakpoints } from '@frameio-bs/tokens';

import { Breakpoint } from '~/types/index.d';

// pass in a breakpoint using `const breakpoint = useUIStore((state) => state.breakpoint);` from your react component
// you will likely have to do this in a useLayoutEffect

// TODO: use enumerated breakpoints

function isBreakpointOrGreater(
  breakpoint: Breakpoint,
  breakpointToMatch: string,
): boolean {
  const isBreakpointOrGreater =
    (breakpoint?.min ?? false) &&
    breakpoint.min >=
      breakpoints.filter(
        (breakpoint) => breakpoint.name === breakpointToMatch,
      )[0].min;

  return isBreakpointOrGreater === true;
}

export default isBreakpointOrGreater;
