import { yupResolver } from '@hookform/resolvers/yup';
import mapKeys from 'lodash/mapKeys';
import { ForwardedRef, forwardRef, useState } from 'react';
import { SubmitHandler, useForm } from 'react-hook-form';
import { AnySchema, object, string } from 'yup';

import getBaseTrackingData from '~/analytics/getBaseTrackingData';
import buttonStyles from '~/components/atoms/Buttons/Ctas/ButtonBase.styles.module.css';
import Field from '~/components/atoms/field';
import { dict } from '~/data/stores/Dictionary';
import { cn } from '~/utils';
import {
  getHasEnterpriseEmailProvider,
  getHasEnterpriseEmployeeCount,
} from '~/utils/reveal';

import styles from './styles.module.css';
import { CMSFormField, CMSFormProps, FormValues } from './types';

// TODO: refactor; it's not clear what functionality all these options are supposed to do
// it's very difficult to style individual elements without cascading effects

const Form = (props: CMSFormProps, ref: ForwardedRef<HTMLFormElement>) => {
  const [formHasError, setFormHasError] = useState(false);
  const schemaObject: { [key: string]: AnySchema } = {};
  const formId = `form_${props._key}_id_${props._id}`;

  for (const field of props.fields) {
    let baseValidation;
    switch (field.type) {
      case 'textarea':
      case 'tel':
      case 'text':
        baseValidation = string();
        break;
      case 'email':
        baseValidation = string().email(dict('mustBeValidEmail'));
        break;
      default:
        break;
    }
    schemaObject[field.id] = field.required
      ? (baseValidation?.required() as AnySchema)
      : (baseValidation as AnySchema);
  }

  const schema = object(schemaObject).required();
  const [formSubmitted, setFormSubmitted] = useState(false);
  const {
    register,
    getValues,
    handleSubmit,
    setFocus,
    clearErrors,
    formState: { isValid, errors },
  } = useForm({
    resolver: yupResolver(schema),
    mode: 'onSubmit',
    reValidateMode: 'onBlur',
  });

  const hasErrorInForm = Object.keys(errors).length > 0;

  const submitAnalytics = async (data: { [key: string]: string }) => {
    if (window.analytics) {
      const mergedData = {
        ...getBaseTrackingData(),
        ...mapKeys(data, (_: unknown, k: unknown) => `mktg_form_${k}`),
      };

      window.analytics.track('mktg-form-completed', mergedData);

      const hasEnterpriseEmailProvider = await getHasEnterpriseEmailProvider();
      if (hasEnterpriseEmailProvider) {
        window.analytics.track('mgl-email-captured-client', mergedData);
      }

      const hasEnterpriseEmployeeCount = await getHasEnterpriseEmployeeCount();
      if (hasEnterpriseEmployeeCount) {
        window.analytics.track('mql-email-captured-client', mergedData);
      }
    }
  };

  const onSubmit: SubmitHandler<FormValues> = (data, event) => {
    if (props.submit.submitType === 'redirect' && event) {
      let newRedirectUrl = props.submit.redirect;
      let i = 0;
      for (const [key, value] of Object.entries(data)) {
        if (i > 0) {
          newRedirectUrl += '&';
        }
        newRedirectUrl += `${key}=${value}`;

        // email field only - the field id of "email" is used in analytics and should not be removed, but we need to add an additional property of 'e=' for the email to auto populate in the redirect
        if (key === 'email') {
          newRedirectUrl += `&e=${value}`;
        }
        i += 1;
      }

      event.target.action = newRedirectUrl;
      submitAnalytics(data);
      event.target.submit();
    }
    if (props.submit.submitType === 'apiEndpoint' && props.submit.api) {
      if (formSubmitted) return;
      fetch(props.submit.api, {
        method: 'POST',
        body: JSON.stringify({ data }),
        headers: { 'Content-Type': 'application/json' },
      })
        .then((res) => {
          submitAnalytics(data);
          setFormSubmitted(true);
          res.json();
        })
        .catch(() => {
          setFormHasError(true);
        });
    }
  };

  const hasOneSingleField = props.fields.length === 1;

  const values = getValues();

  const isEmpty =
    Object.keys(values).length === 0 ||
    values[Object.keys(values)[0]]?.length === 0;

  const handleChange = () => {
    props?.fields.map((field: CMSFormField) => {
      const hasError = Boolean(errors[field.id]);
      if (hasError) {
        clearErrors(field.id);
      }
    });
  };

  return (
    <form
      target="_self"
      onSubmit={handleSubmit(onSubmit)}
      noValidate
      className={cn(
        props.className,
        styles.form,
        hasOneSingleField && styles.singleFieldForm,
        hasErrorInForm && styles.fieldWithError,
        isEmpty && styles.emptyField,
      )}
      ref={ref}
      onChange={handleChange}
    >
      {props?.fields.map((field: CMSFormField) => {
        const fieldKey = field._key;
        const hasError = Boolean(errors[field.id]);

        // set focus back to input field if there is an error

        if (hasError) {
          setFocus(field.id, { shouldSelect: true });
        }

        return (
          <div key={fieldKey} className={styles.inputContainer}>
            <Field
              className={cn(styles.input)}
              formId={formId}
              field={field}
              hasError={hasError}
              disabled={formSubmitted}
              {...register(field.id)}
            />
            {hasError && (
              <span
                id={`${fieldKey}-error`}
                className={styles.fieldErrorMessage}
              >
                {errors[field.id]?.type === 'required' &&
                field?.requiredErrorMessage
                  ? field.requiredErrorMessage
                  : (errors[field.id]?.message as string)}
              </span>
            )}
          </div>
        );
      })}
      {formHasError && (
        <span className={styles.formErrorMessage}>{props.errorMessage}</span>
      )}
      <button
        className={cn(
          styles.submitButton,
          isValid ? buttonStyles.solidLoud : buttonStyles.solidLight,
        )}
        type="submit"
      >
        {formSubmitted ? props.confirmationMessage : props.submit.label}
      </button>
    </form>
  );
};

export default forwardRef(Form);
