import { Children, ReactNode } from 'react';

import styles from './styles.module.css';
import { mapChildrenToRows } from './utils';

export const Rows = ({ children }: { children?: ReactNode }) => {
  const mapped = mapChildrenToRows({ children, rows: 4 });
  return (
    <>
      {Children.count(children) > 0 &&
        mapped.map((child: ReactNode, index: number) => (
          <div key={`row-${index}`} className={styles.row}>
            <div className={styles.innerRow}>{child}</div>
          </div>
        ))}
    </>
  );
};
