import Image from '~/components/atoms/image';
import GlobalData from '~/data/stores/GlobalData';
import { cn } from '~/utils';

import styles from './styles.module.css';
import { DeviceWrapperType } from './types';

const DeviceWrapper = ({
  className,
  deviceType = 'studioDisplay',
  isInView,
}: DeviceWrapperType) => {
  if (GlobalData.data && GlobalData.data.deviceWrappers) {
    return (
      <div className={cn(styles.deviceWrapper, className)}>
        <div className={styles.deviceScreen}>
          {deviceType === 'studioDisplay' && (
            <Image
              source={GlobalData.data.deviceWrappers.appleStudioDisplay}
              className={styles.appleStudioDisplayStand}
              isDisplayed={isInView}
            />
          )}
          {deviceType === 'macbookPro' && (
            <Image
              source={GlobalData.data.deviceWrappers.appleMacbookPro}
              className={styles.macbookPro}
              isDisplayed={isInView}
            />
          )}
        </div>
      </div>
    );
  }
  return null;
};

export default DeviceWrapper;
