import { gsap } from 'gsap';
import SplitText from 'gsap/dist/SplitText';
import { RefObject } from 'react';

import { EaseType } from '~/utils/singletons/Easing';

export const fullAnimation = ({
  $titlesRefs,
  $nonTitlesRefs,
  $typesRefs,
}: {
  $titlesRefs: RefObject<HTMLDivElement[]>;
  $nonTitlesRefs: RefObject<HTMLDivElement[]>;
  $typesRefs: RefObject<HTMLDivElement[]>;
}) => {
  const onCompleteTimeline = () => {
    if ($titlesRefs && splitTitles) {
      splitTitles.revert();
    }
    if (splitNonTitles) {
      splitNonTitles.revert();
    }
  };

  const tl = gsap.timeline({ paused: true, onComplete: onCompleteTimeline });
  let splitTitles: SplitText;
  let splitNonTitles: SplitText;

  document.fonts.ready.then(() => {
    // Title animations
    if ($titlesRefs.current && $titlesRefs.current.length > 0) {
      splitTitles = new SplitText($titlesRefs.current, { type: 'lines' });
      // gsap.set($titlesRefs.current, {
      //   perspective: 800,
      //   perspectiveOrigin: '-200px 100%',
      // });
      const $titlesLines = splitTitles.lines;

      tl.fromTo(
        $titlesLines,
        {
          // x: -10,
          y: 120,
          opacity: 0,
          // rotateX: 34,
          // rotateY: -20,
        },
        {
          duration: 1,
          // rotateX: 0,
          // rotateY: 0,
          // transformOrigin: '200% 0%',
          // x: 0,
          y: 0,
          delay: 0.5,
          stagger: 0.06,
          ease: EaseType.DEFAULT,
        },
        0,
      );
      tl.fromTo(
        $titlesLines,
        {
          opacity: 0,
        },
        {
          duration: 0.8,
          opacity: 1,
          stagger: 0.06,
        },
        // seconds past the start of the previous animation
        '<+=0.05',
      );
    }

    // Non-title animations
    if ($nonTitlesRefs.current && $nonTitlesRefs.current.length > 0) {
      tl.fromTo(
        $nonTitlesRefs.current,
        {
          y: 100,
          opacity: 0,
        },
        {
          duration: 1,
          y: 0,
          ease: EaseType.DEFAULT,
        },
        // seconds before the end of the previous animation
        '>-=0.9',
      );
      tl.fromTo(
        $nonTitlesRefs.current,
        {
          opacity: 0,
        },
        {
          duration: 0.95,
          opacity: 1,
        },
        // seconds past the start of the previous animation
        '<+=0.1',
      );
    }

    // Buttons, forms and other "types" animations
    if ($typesRefs.current && $typesRefs.current.length > 0) {
      tl.fromTo(
        $typesRefs.current,
        {
          y: 100,
          opacity: 0,
        },
        {
          duration: 1,
          y: 0,
          stagger: 0.06,
          ease: EaseType.DEFAULT,
        },
        // seconds past the end of the previous animation
        '<+=0.2',
      );
      tl.fromTo(
        $typesRefs.current,
        {
          opacity: 0,
        },
        {
          duration: 0.95,
          opacity: 1,
          stagger: 0.06,
        },
        // seconds past the start of the previous animation
        '<+=0.1',
      );
    }
  });

  return tl;
};

export const lightAnimation = ({
  $columnRefs,
  shouldWaitForFontLoad = true,
  paused = true,
  amplitude = 1,
}: {
  $columnRefs: RefObject<HTMLDivElement[]>;
  shouldWaitForFontLoad?: boolean;
  paused?: boolean;
  amplitude?: number;
}) => {
  const tl = gsap.timeline({ paused });

  if (amplitude) {
    tl.timeScale(1 / amplitude);
  }

  const updateTimeline = () => {
    if ($columnRefs.current && $columnRefs.current.length > 0) {
      tl.fromTo(
        $columnRefs.current,
        {
          y: 100 * amplitude,
          opacity: 0,
        },
        {
          duration: 0.7,
          delay: 0.06,
          y: 0,
          ease: EaseType.DEFAULT,
          stagger: 0.07,
        },
        0,
      );
      tl.fromTo(
        $columnRefs.current,
        {
          opacity: 0,
        },
        {
          duration: 0.5,
          opacity: 1,
          stagger: 0.07,
        },
        // seconds past the start of the previous animation
        '<+=0.1',
      );
    }
  };

  if (shouldWaitForFontLoad) {
    document.fonts.ready.then(updateTimeline);
  } else {
    updateTimeline();
  }

  return tl;
};
