import { gsap } from 'gsap';
import { RefObject } from 'react';

const animation = (
  {
    $text,
    $cardsWrapper,
    $cards,
  }: {
    $text?: RefObject<HTMLElement>;
    $cardsWrapper: RefObject<HTMLElement>;
    $cards: RefObject<HTMLElement[]>;
  },
  totalHeight: number,
  isMobile: boolean,
) => {
  const tl = gsap.timeline({ paused: true });
  if (!isMobile) {
    if ($text) {
      tl.fromTo(
        $text.current,
        {
          y: '30vh',
        },
        { y: 0, duration: 1 },
        0,
      );
    }
    tl.fromTo(
      $cardsWrapper.current,
      {
        y: '50vh',
      },
      { y: '-5vh', duration: 1 },
      0,
    );

    tl.fromTo(
      $cards.current,
      {
        y: '10vh',
      },
      {
        y: '-10vh',
        duration: 1,
      },
      0,
    );
  }

  return tl;
};

export { animation };
