import { gsap } from 'gsap';
import { CSSProperties, useCallback, useEffect, useRef, useState } from 'react';

import Observer from '~/components/atoms/observer';
import LogoTextItem from '~/components/molecules/logoTextItem';
import { CMSLogoTextItem } from '~/components/molecules/logoTextItem/types';
import { cn, useScrollProgress } from '~/utils';

import TextWithComponentsContentPortableText from '../textWithComponents/ContentPortableText';
import { Rows } from './Rows';
import styles from './styles.module.css';
import { ChicletProps } from './types';
import { ensureEnoughItems } from './utils';

/*
README:
The design requires 4 rows, each row with at least visible 5 elements per row.

On a previous comment on this PR it was requested that we add extra elements per breakpoint.
So compared to design, we have 1 more item per row. Because we're offsetting the rows by half an item, we need
to have 1 extra element to give the ilusion of a wall of logos.

mobile has 3 elements per row (so we need 4 to keep the illusion)
tablet has 4 elements per row (so we need 5 to keep the illusion)
desktop has 5 elements per row (so we need 6 to keep the illusion)

6 elements X 4 rows = 24
*/
const MIN_ITEMS = 24;

const Chiclet = ({
  logoTextItems,
  className,
  background,
  text,
  ragging,
  alignment,
  slug,
}: ChicletProps) => {
  const $element = useRef<HTMLDivElement>(null);
  const progressSetter = useRef<(value: number) => void>();
  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  useEffect(() => {
    progressSetter.current = gsap.quickSetter(
      $element.current,
      '--transition-out-progress',
    ) as (value: number) => void;
  }, []);

  const onProgress = useCallback((progress: number) => {
    if (progressSetter.current)
      progressSetter.current(
        gsap.utils.clamp(0, 1, gsap.utils.normalize(0.6, 1, progress)),
      );
  }, []);

  useScrollProgress($element, onProgress);

  return (
    <section
      ref={$element}
      className={cn(
        styles.chiclet,
        background && styles.hasBackground,
        className,
      )}
      style={
        {
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <Observer
        callback={updateIsInView}
        options={{ rootMargin: '100% 0%' }}
        className={styles.contentWrapper}
      >
        {text && (
          <TextWithComponentsContentPortableText
            className={cn(styles.text)}
            content={text}
            ragging={ragging}
            alignment={alignment}
          />
        )}
      </Observer>
      <Rows>
        {ensureEnoughItems(logoTextItems, MIN_ITEMS).map(
          (item: CMSLogoTextItem) => (
            <div key={item._key} className={styles.chicletItem}>
              <LogoTextItem
                className={styles.chicletInner}
                label={item.label}
                svgOrImage={item.svgOrImage}
                chiclet
                isInView={isInView !== false}
              />
            </div>
          ),
        )}
      </Rows>
    </section>
  );
};

export default Chiclet;
