import { CMSGlobalData } from '~/types/settings';

import { CMSGlobalDataProps } from './types.d';

type StoreProps = {
  data: CMSGlobalDataProps;
};

class GlobalData {
  isInitialized = false;
  private store!: StoreProps;

  initialize(data: CMSGlobalData) {
    if (!this.isInitialized) {
      this.isInitialized = true;
      this.store = {
        data,
      };
    }
  }

  get data(): CMSGlobalDataProps {
    return this.store?.data || null;
  }
}

const instance = new GlobalData();

export default instance;
