import { buttonFragment } from '~/components/atoms/Buttons/Ctas/Button/Button.fragment';
import linkFragment from '~/components/atoms/link/fragment';
import logoFragment from '~/components/molecules/logo/fragment';
import logoLinkFragment from '~/components/molecules/logoLink/fragment';
import navGroupFragment from '~/components/organisms/navigation/navGroup/fragment';

const footer = `
  primaryLinks[] {
    _key,
    ${navGroupFragment}
  },
  primaryLogoLeft {
    logo { ${logoFragment} },
    link { ${linkFragment} }
  },
  bottomLeftCopyright,
  bottomLeftLinks[] {
    _type == 'link' => {
      _key,
      ${linkFragment},
    },
    _type == 'button' => {
      _key,
      ${buttonFragment},
      "to": {
        hash,
        type,
        type == 'internal' => {
          "url": "/" + coalesce(
            coalesce(
            *[_id match "drafts." + ^._ref][0],
            reference->,
          ).slug.current,
          ''
          ),
        },
        type == 'external' => {
          url,
        },
      }
    }
  },
  bottomRightSocial[] {
    _key,
    ${logoLinkFragment}
  }
`;

export default footer;
