import { gsap } from 'gsap';
import { CSSProperties, useCallback, useRef, useState } from 'react';

import Observer from '~/components/atoms/observer';
import Glow from '~/components/molecules/glow';
import { GlowType } from '~/components/molecules/glow/types.d';
import {
  cn,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';

import TextWithComponentsContentPortableText from '../textWithComponents/ContentPortableText';
import LogoItem from './logoItems/LogoItem';
import styles from './styles.module.css';
import { ChicletConstellationLayout, ChicletConstellationProps } from './types';

const progressTransformer = gsap.utils.pipe(
  gsap.utils.normalize(0, 0.85),
  gsap.utils.clamp(0, 1),
);

export const ChicletConstellation = ({
  logoItems,
  background,
  layout,
  className,
  content,
  ragging,
  featuredItem,
  alignment,
  garageDoorRevealParent,
  slug,
}: ChicletConstellationProps) => {
  const $wrapper = useRef<HTMLDivElement>(null);
  const $dummy = useRef<HTMLDivElement>(null);
  const $logoItems = useRef<HTMLDivElement[]>(Array(logoItems.length - 1));
  const $logosContainer = useRef<HTMLDivElement>(null);
  const progressSetter = useRef<(value: number) => void>();
  const onProgress = useCallback(
    (progress: number) => {
      if (progressSetter.current)
        progressSetter.current(
          content && featuredItem ? progressTransformer(progress) : progress,
        );
    },
    [content, featuredItem],
  );
  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  useScrollProgress($dummy, onProgress, {
    $garageDoorElement: garageDoorRevealParent,
  });

  useLayoutEffect(() => {
    progressSetter.current = gsap.quickSetter(
      $wrapper.current,
      '--constellation-progress',
    ) as (value: number) => void;
  }, []);

  return (
    <section
      ref={$wrapper}
      className={cn(
        styles.chicletConstellation,
        background && styles.hasBackground,
        className,
      )}
      style={
        {
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <div ref={$dummy} className={styles.dummy}></div>
      <Observer
        callback={updateIsInView}
        options={{ rootMargin: '100% 0%' }}
        className={cn(
          styles.logos,
          // set layout class
          layout === ChicletConstellationLayout.CLOUD && styles.cloud,
          layout === ChicletConstellationLayout.REVERSE_PYRAMID &&
            styles.reversePyramid,
          content && featuredItem && styles.hasContent,
        )}
        ref={$logosContainer}
      >
        {logoItems.map((item) => {
          return (
            <div
              key={item.logo._key}
              ref={(node: HTMLDivElement) =>
                ($logoItems.current[item.logoIndex] = node)
              }
              className={styles.logoItem}
            >
              <LogoItem
                layout={layout}
                logo={item.logo}
                rowIndex={item.rowIndex}
                isInView={isInView !== false}
              />
            </div>
          );
        })}
      </Observer>
      {content && featuredItem && (
        <div className={styles.contentWrapper}>
          {featuredItem && (
            <LogoItem
              isFeatured
              className={styles.featuredLogo}
              logo={featuredItem}
              rowIndex={0}
              layout={layout}
              isInView={isInView !== false}
            />
          )}
          <TextWithComponentsContentPortableText
            className={cn(
              styles.content,
              layout === ChicletConstellationLayout.REVERSE_PYRAMID &&
                styles.hasReversePyramid,
            )}
            content={content}
            ragging={ragging}
            alignment={alignment}
          />
        </div>
      )}
      {layout === ChicletConstellationLayout.REVERSE_PYRAMID && content && (
        <>
          <Glow
            className={styles.topRightMobileOnlyGlow}
            type={GlowType.BAKED_CANVAS}
            config={{
              intensity: 0.4,
              scale: 2,
              color: '#9947EB',
              type: 'COLOR',
            }}
          />
          <Glow
            className={styles.bottomLeftWithContentGlow}
            type={GlowType.BAKED_CANVAS}
            config={{
              intensity: 0.5,
              scale: 1.6,
              gradient: {
                startColor: '#FD856E',
                stopColor: '#9947EB',
                startPosition: 0.34,
                stopPosition: 0.85,
                orientation: 0,
              },
              type: 'GRADIENT',
            }}
          />
        </>
      )}
    </section>
  );
};
