import { gsap } from 'gsap';
import throttle from 'lodash/throttle';
import { RefObject, useEffect, useRef, useState } from 'react';
import shallow from 'zustand/shallow';

import UIStore from '~/state/ui';
import ScrollPosition from '~/utils/domEvents/ScrollPosition';

import { calculateIndex } from './utils';

export const useHighlightReference = (
  refs: RefObject<HTMLDivElement>[],
  parentRefs: RefObject<HTMLDivElement>[],
) => {
  const offsets = useRef<number[]>([]);

  const [index, updateIndex] = useState(
    calculateIndex(offsets.current, ScrollPosition.y || 0),
  );

  useEffect(() => {
    const calculateOffsets = () => {
      let parentsOffset = 0;
      parentRefs.forEach((parentRef) => {
        if (parentRef.current) {
          gsap.ticker.add(
            () => {
              parentsOffset += parentRef.current
                ? parentRef.current.offsetTop
                : 0;
            },
            true,
            true,
          );
        }
      });
      gsap.ticker.add(
        () => {
          const nextOffsets: number[] = [];
          refs.forEach((ref, index) => {
            if (ref.current) {
              let nextOffset = 0;
              nextOffset =
                100 + (ref.current ? ref.current.offsetTop : 0) + parentsOffset;
              nextOffset = ref.current
                ? (ScrollPosition.y || 0) +
                  ref.current.getBoundingClientRect().top
                : ScrollPosition.y || 0;
              nextOffsets[index] = nextOffset;
            }
          });
          offsets.current = nextOffsets;
        },
        true,
        true,
      );
    };
    calculateOffsets();
    calculateIndex(offsets.current, ScrollPosition.y || 0);

    // Make sure we re-calculate offsets on resize
    const unsubscribeResize = UIStore.subscribe(
      (state) => [state.windowWidth, state.windowHeight],
      calculateOffsets,
      {
        equalityFn: shallow,
      },
    );

    const indexUpdate = () => {
      updateIndex(calculateIndex(offsets.current, ScrollPosition.y || 0));
    };

    const unsubscribeScroll = ScrollPosition.subscribe(
      throttle(indexUpdate, 100),
    );

    return () => {
      unsubscribeScroll();
      unsubscribeResize();
    };
  }, [parentRefs, refs]);

  return index;
};
