import Image from '~/components/atoms/image';
import Svg from '~/components/atoms/svg';
import Glow from '~/components/molecules/glow';
import { GlowType } from '~/components/molecules/glow/types.d';
import Shadow from '~/components/molecules/shadow';
import { cn } from '~/utils';
import addToRefArray from '~/utils/addToRefArray';

import PortableText from '../../PortableText';
import {
  getBlockClassesFromContent,
  getElementAttributesFromOptions,
} from '../../utils';
import getRefFromOptions from '../../utils/getRefFromOptions';
import blockStyles from './styles.module.css';
import { PortableTextToutsProps } from './types';

const Touts = ({
  module,
  value,
  typeName,
  typeCategory,
}: PortableTextToutsProps) => {
  const optionsProps = getElementAttributesFromOptions(
    module,
    typeCategory,
    typeName,
  );
  const { className } = optionsProps;
  const {
    _key,
    cardsLayout,
    touts,
    gridColumnsTotal = 1,
    moduleStyles = null,
  } = value;
  const ref = getRefFromOptions(module, typeCategory, typeName);

  const countClass = blockStyles[`with${value.touts.length}Touts`];
  const renderClass = getBlockClassesFromContent(
    _key,
    value,
    cn(
      className,
      countClass,
      blockStyles.touts,
      cardsLayout && blockStyles.toutsCardsLayout,
      gridColumnsTotal > 1 && blockStyles.isMultipleColumns,
    ),
    [blockStyles, moduleStyles],
  );

  const Inner = cardsLayout ? Shadow : 'div';
  return (
    <div
      className={renderClass}
      ref={(element: HTMLDivElement) => ref && addToRefArray(element, ref)}
    >
      {cardsLayout && (
        <Glow
          className={blockStyles.backgroundGlow}
          type={GlowType.BAKED_CANVAS}
          config={{
            type: 'COLOR',
            color: '#1E242A',
            intensity: 0.75,
            scale: 2,
            dither: 0.8,
          }}
        />
      )}
      {touts.map((tout) => (
        <div key={tout._key} className={cn(blockStyles.toutInnerWrapper)}>
          <Inner
            className={cn(
              blockStyles.tout,
              cardsLayout && blockStyles.cardsLayout,
              cardsLayout && blockStyles.shadow,
            )}
          >
            {tout?.icon?.svg && (
              <Svg
                {...tout.icon.svg}
                className={cn(
                  blockStyles.icon,
                  tout.iconHeight === 'large' && blockStyles.isLarge,
                )}
              />
            )}
            {tout?.icon?.image && (
              <Image
                source={tout.icon.image}
                className={cn(
                  blockStyles.icon,
                  tout.iconHeight === 'large' && blockStyles.isLarge,
                )}
              />
            )}
            <PortableText
              isAutoGrid={false}
              content={tout.content}
              animate={false}
              className={blockStyles.content}
              options={{
                block: {
                  titles: {
                    className: blockStyles.heading,
                  },
                  title6: {
                    className: blockStyles.title6,
                    tagName: 'h4',
                  },
                  eyebrow: {
                    className: blockStyles.heading,
                  },
                  bodies: {
                    className: blockStyles.paragraph,
                  },
                },
              }}
            />
          </Inner>
        </div>
      ))}
    </div>
  );
};

export default Touts;
