import { RefObject, useCallback, useEffect, useState } from 'react';
import { OnProgressProps } from 'react-player/base';
import ReactPlayer from 'react-player/file';
const SESSION_STORAGE_MUTE = 'frame-video-mute';
import useUIStore from '~/state/ui';
import { getItem, setItem } from '~/utils/sessionStorage';

// create interface that works with fullscreen api
interface DocumentWithFullscreen extends Document {
  mozFullScreenElement?: Element;
  webkitFullscreenElement?: Element;
  mozCancelFullScreen?: () => void;
  webkitExitFullscreen?: () => void;
}

interface ElementWithFullScreen extends Element {
  mozRequestFullscreen?: () => void;
  webkitRequestFullscreen?: () => void;
}

const useVideoState = (
  playerRef: RefObject<ReactPlayer>,
  // For fullscreen purposes
  $wrapper: RefObject<HTMLDivElement>,
  isInitiallyMuted: boolean,
) => {
  const [playedSeconds, setPlayedSeconds] = useState<number>(0);
  const [loadedSeconds, setLoadedSeconds] = useState<number>(0);
  const [duration, setDuration] = useState<number>(0);
  const [isPlaying, setIsPlaying] = useState<boolean>(false);
  const [isMuted, setIsMuted] = useState<boolean>(
    isInitiallyMuted || !!getItem(SESSION_STORAGE_MUTE),
  );

  const setHasFullscreenVideo = useUIStore(
    (state) => state.setHasFullscreenVideo,
  );

  const seekTo = useCallback(
    (amount: number) => {
      playerRef.current?.seekTo(amount);
    },
    [playerRef],
  );

  const play = useCallback(() => {
    playerRef.current?.getInternalPlayer().play();
  }, [playerRef]);

  const pause = useCallback(() => {
    playerRef.current?.getInternalPlayer().pause();
  }, [playerRef]);

  const toggleMute = useCallback(() => {
    if (!isMuted) {
      setIsMuted(true);
      setItem(SESSION_STORAGE_MUTE, true);
    } else {
      setIsMuted(false);
      setItem(SESSION_STORAGE_MUTE, false);
    }
  }, [isMuted]);

  const onProgress = useCallback((progress: OnProgressProps) => {
    setPlayedSeconds(progress.playedSeconds);
    setLoadedSeconds(progress.loadedSeconds);
  }, []);

  const onSeek = useCallback((seekValue: number) => {
    setPlayedSeconds(seekValue);
  }, []);

  const onDuration = useCallback((duration: number) => {
    setDuration(duration);
  }, []);

  const onPlay = useCallback(() => {
    setIsPlaying(true);
  }, []);

  const onPause = useCallback(() => {
    setIsPlaying(false);
  }, []);
  const toggleFullScreen = useCallback(() => {
    const element = $wrapper.current as ElementWithFullScreen;
    const doc = document as DocumentWithFullscreen;
    if (
      doc.fullscreenElement ||
      doc.webkitFullscreenElement ||
      doc.mozFullScreenElement
    ) {
      if (doc.webkitExitFullscreen) {
        doc.webkitExitFullscreen();
      } else if (doc.mozCancelFullScreen) {
        doc.mozCancelFullScreen();
      }
      {
        doc.exitFullscreen();
      }

      return;
    } else if (element) {
      if (element.requestFullscreen) {
        element.requestFullscreen();
      } else if (element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen();
      } else if (element.mozRequestFullscreen) {
        element.mozRequestFullscreen();
      } else {
        console.warn('Fullscreen is not supported on your browser');
      }
    }
  }, [$wrapper]);

  // add Event listener for fullscreen change
  useEffect(() => {
    const element = $wrapper.current as ElementWithFullScreen;

    function fullscreenChanged() {
      if (document.fullscreenElement) {
        setHasFullscreenVideo(true);
      } else {
        // Add small delay to prevent triggering scroll event during the transition from fullscreen
        // to normal (which triggers the modal to close completely)
        setTimeout(() => {
          setHasFullscreenVideo(false);
        }, 100);
      }
    }
    element.addEventListener('fullscreenchange', fullscreenChanged);

    return () => {
      element.removeEventListener('fullscreenchange', fullscreenChanged);
    };
  }, [$wrapper, setHasFullscreenVideo]);

  return [
    // states
    playedSeconds,
    loadedSeconds,
    duration,
    isPlaying,
    isMuted,
    // methods
    {
      onDuration,
      onPause,
      onPlay,
      onProgress,
      onSeek,
      pause,
      play,
      seekTo,
      toggleFullScreen,
      toggleMute,
    },
    //   https://fettblog.eu/typescript-react-typeing-custom-hooks/
  ] as const;
};

export default useVideoState;
