import { CSSProperties } from 'react';

import PortableText from '~/components/molecules/portableText';
import SvgOrImage from '~/components/molecules/svgOrImage';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { cn, getSpacersStyles } from '~/utils';

import SpeedBump from '../speedbump';
import styles from './styles.module.css';
import { CaseStudyContent, CMSCaseStudyProps } from './types';

const CaseStudy = ({
  sideRail,
  content,
  hasSpacers,
  accentColor,
  background,
  className,
  slug,
}: CMSCaseStudyProps) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  return (
    <section
      className={cn(
        styles.caseStudy,
        background && styles.hasBackground,
        hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        className,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      {(sideRail.logo || sideRail.sideRailStats || sideRail.sideRailText) && (
        <aside className={styles.sideRail}>
          {sideRail.logo && (
            <SvgOrImage {...sideRail.logo} className={styles.sideRailLogo} />
          )}
          {sideRail.sideRailText && (
            <PortableText
              className={styles.sideRailText}
              isAutoGrid={false}
              content={sideRail.sideRailText}
              options={{
                block: {
                  accents: {
                    tagName: 'h6',
                    className: styles.sideRailAccents,
                  },
                  bodies: {
                    className: styles.sideRailBodies,
                  },
                },
              }}
            />
          )}
          {sideRail.sideRailStats && (
            <PortableText
              className={styles.sideRailStats}
              isAutoGrid={false}
              content={sideRail.sideRailStats}
              options={{
                block: {
                  accents: {
                    tagName: 'h6',
                    className: styles.sideRailAccents,
                  },
                  bodies: {
                    className: styles.sideRailBodies,
                  },
                },
              }}
            />
          )}
        </aside>
      )}
      {content.map((contentItem: CaseStudyContent) => {
        if (contentItem.text) {
          return (
            <div className={styles.textContainer} key={contentItem._key}>
              <PortableText
                className={styles.text}
                isAutoGrid={false}
                animate={false}
                content={contentItem.text}
                options={{
                  block: {
                    bodyLarge: {
                      className: styles.bodyLarge,
                    },
                    bodies: {
                      className: styles.bodies,
                    },
                    title4: {
                      className: styles.smallTitles,
                    },
                    title5: {
                      className: styles.smallTitles,
                    },
                    title6: {
                      className: styles.smallTitles,
                    },
                  },
                }}
              />
            </div>
          );
        }
        if (contentItem.speedbump) {
          return (
            <div className={styles.speedbbumpContainer} key={contentItem._key}>
              <SpeedBump {...contentItem.speedbump} />
            </div>
          );
        }
      })}
    </section>
  );
};

export default CaseStudy;
