const innerFormFragment = `
  _id,
  _key,
  confirmationMessage,
  errorMessage,
  fields,
  submit
`;

const fragment = `
(defined(formReference._ref) || defined(formInline._type)) => {
  _key,
  _type,
  blockType,
  blockType == 'referenced' => {
    'form': formReference {
      ...@-> {
        ${innerFormFragment}
      },
    },
  },
  blockType == 'inline' => {
    'form': formInline {
      ${innerFormFragment}
    },
  },
},
`;
export default fragment;
