import { gsap } from 'gsap';
import {
  Geometry,
  Mesh,
  OGLRenderingContext,
  Pass,
  Post,
  Program,
  Renderer,
  Texture,
  Triangle,
  Vec2,
} from 'ogl-typescript';

import DebugStore from '~/state/debug';
import { hexToComponent } from '~/utils';

import {
  GlowColorSource,
  GlowGradientSource,
  GlowImageSource,
  GlowPosition,
  GlowSource,
} from '../types.d';
import {
  blurFragment,
  colorFragment,
  compositeFragment,
  gradientFragment,
  imageFragment,
  vertex,
} from './glsl/shaders';

const DEFAULT_BLUR_DISTANCE = 3;
const DEFAULT_SCALE = 0.6;

const OFFSET_CONFIG = {
  [GlowPosition.CENTER]: [0.5, 0.5],
  [GlowPosition.TOP_CENTER]: [0.5, 0],
  [GlowPosition.BOTTOM_CENTER]: [0.5, 1],
  [GlowPosition.LEFT_CENTER]: [0, 0.5],
  [GlowPosition.RIGHT_CENTER]: [1, 0.5],
  [GlowPosition.TOP_LEFT]: [0, 0],
  [GlowPosition.TOP_RIGHT]: [1, 0],
  [GlowPosition.BOTTOM_LEFT]: [0, 1],
  [GlowPosition.BOTTOM_RIGHT]: [1, 1],
};

type GlowConfig = {
  blur?: number;
  glowPosition?: GlowPosition;
  glowCircleSize?: number;
  glowImageScale?: number;
};

// DEBUG purposes toggle to true to disable blur
const DISABLE_BLUR = false;

type RequestProps = {
  source: GlowSource;
  targetCanvasContext: CanvasRenderingContext2D;
  config: {
    blur?: number;
    glowPosition?: GlowPosition;
    glowCircleSize?: number;
    glowImageScale?: number;
  };
};

class GlowWebglFactorySSR {
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  requestGlow() {}
}
class GlowWebglFactoryClientSide {
  $canvas: HTMLCanvasElement;
  renderer: Renderer;
  imageMesh: Mesh;
  colorMesh: Mesh;
  gradientMesh: Mesh;
  postBlur: Post;
  postComposite: Post;
  compositePass: Pass;
  blurResolution: Vec2;
  isRendering: boolean;
  isProcessingQueue: boolean;
  queue: RequestProps[];

  constructor() {
    this.$canvas = document.createElement('canvas');

    this.isRendering = false;
    this.isProcessingQueue = false;

    DebugStore.subscribe((state) => {
      if (state.glowCanvas) {
        this.$canvas.style.position = 'fixed';
        this.$canvas.style.top = '0';
        this.$canvas.style.right = '0';
        this.$canvas.style.zIndex = '1000';
        document.body.appendChild(this.$canvas);
      } else {
        this.$canvas.remove();
      }
    });

    this.renderer = new Renderer({
      canvas: this.$canvas,
      alpha: true,
    });

    const {
      colorMesh,
      imageMesh,
      gradientMesh,
      postBlur,
      postComposite,
      compositePass,
      blurResolution,
    } = this.initializeScene(this.renderer.gl);

    this.colorMesh = colorMesh;
    this.gradientMesh = gradientMesh;
    this.imageMesh = imageMesh;
    this.postBlur = postBlur;
    this.postComposite = postComposite;
    this.compositePass = compositePass;
    this.blurResolution = blurResolution;

    this.queue = [];
  }

  private initializeScene(gl: OGLRenderingContext) {
    const imageGeometry = new Geometry(gl, {
      position: {
        size: 2,
        data: new Float32Array([-1, -1, 3, -1, -1, 3]),
      },
      uv: { size: 2, data: new Float32Array([0, 0, 2, 0, 0, 2]) },
    });

    gl.clearColor(0, 0, 0, 1);

    const size = [0, 0];
    const texture = new Texture(gl, {
      generateMipmaps: false,
    });

    const imageProgram = new Program(gl, {
      vertex,
      fragment: imageFragment,
      uniforms: {
        u_resolution: {
          value: [gl.canvas.clientWidth, gl.canvas.clientHeight],
        },

        u_texture: {
          value: texture,
        },
        u_texture_size: {
          value: size,
        },
        //  scale to half the canvas size so that image doesn't stretch out
        u_scale: {
          value: new Vec2(DEFAULT_SCALE, DEFAULT_SCALE),
        },
        u_offset: {
          value: new Vec2(1.0, 1.0),
        },
        u_circleSize: {
          value: 0.35,
        },
        u_intensity: {
          value: 1,
        },
        u_dither: {
          value: 2,
        },
      },
    });

    const colorGeometry = new Triangle(gl);
    const colorProgram = new Program(gl, {
      vertex,
      fragment: colorFragment,
      uniforms: {
        u_resolution: {
          value: [gl.canvas.clientWidth, gl.canvas.clientHeight],
        },
        u_intensity: { value: 1 },
        u_dither: { value: 2 },
        u_blurAmount: { value: 1 },
        u_color: { value: [1, 1, 1] },
      },
    });

    const gradientGeometry = new Triangle(gl);
    const gradientProgram = new Program(gl, {
      vertex,
      fragment: gradientFragment,
      uniforms: {
        u_resolution: {
          value: [gl.canvas.clientWidth, gl.canvas.clientHeight],
        },
        u_intensity: { value: 1 },
        u_dither: { value: 2 },
        u_blurAmount: { value: 1 },
        u_startColor: { value: [1, 1, 1] },
        u_stopColor: { value: [1, 1, 1] },
        u_startPosition: { value: [0, 0] },
        u_stopPosition: { value: [1, 1] },
        u_orientation: { value: 1 },
      },
    });

    //  Post process
    // Create composite post
    const postComposite = new Post(gl);

    // `targetOnly: true` prevents post from rendering to canvas
    const postBlur = new Post(gl, { targetOnly: true });
    const blurResolution = new Vec2(this.renderer.width, this.renderer.height);
    // Add gaussian blur passes
    const horizontalPass = postBlur.addPass({
      fragment: blurFragment,
      uniforms: {
        u_resolution: { value: blurResolution },
        u_direction: { value: new Vec2(1, 0) },
        u_blur_distance: {
          value: DEFAULT_BLUR_DISTANCE * (DISABLE_BLUR ? 0 : 1),
        },
      },
    });
    const verticalPass = postBlur.addPass({
      fragment: blurFragment,
      uniforms: {
        u_resolution: { value: blurResolution },
        u_direction: { value: new Vec2(0, 1) },
        u_blur_distance: {
          value: DEFAULT_BLUR_DISTANCE * (DISABLE_BLUR ? 0 : 1),
        },
      },
    });
    if (!DISABLE_BLUR) {
      // Re-add the gaussian blur passes several times to the array to get smoother results
      for (let i = 0; i < 7; i++) {
        postBlur.passes.push(horizontalPass, verticalPass);
      }
    }

    const compositePass = postComposite.addPass({
      fragment: compositeFragment,
      uniforms: {
        u_resolution: blurResolution,
      },
    });

    return {
      imageMesh: new Mesh(gl, {
        geometry: imageGeometry,
        program: imageProgram,
      }),
      colorMesh: new Mesh(gl, {
        geometry: colorGeometry,
        program: colorProgram,
      }),
      gradientMesh: new Mesh(gl, {
        geometry: gradientGeometry,
        program: gradientProgram,
      }),
      postBlur,
      postComposite,
      compositePass,
      blurResolution,
    };
  }

  requestGlow({ source, targetCanvasContext, config }: RequestProps) {
    this.queue.push({ source, targetCanvasContext, config });

    if (!this.isProcessingQueue) {
      this.processQueue();
    }
  }

  async processQueue() {
    this.isProcessingQueue = true;
    while (this.queue.length > 0) {
      const { source, targetCanvasContext, config } =
        this.queue.shift() as RequestProps;
      if (source && targetCanvasContext && config) {
        await new Promise<void>((resolve) => {
          gsap.ticker.add(() => {
            const width = targetCanvasContext.canvas.width;
            const height = targetCanvasContext.canvas.height;
            if (width === 0 || height === 0) {
              console.warn(
                'Skipping drawing on canvas, Target canvas has styling issue: width or height is equal to zero',
              );
              return;
            }
            this.renderer.gl.clearColor(0, 0, 0, 1);
            this.renderer.setSize(width, height);
            if (source.type === 'IMAGE') {
              this.getImageGlow(source, config, width, height);
            } else if (source.type === 'GRADIENT') {
              this.getGradientGlow(source, width, height);
            } else if (source.type === 'COLOR') {
              this.getColorGlow(source, width, height);
            }
            targetCanvasContext.clearRect(0, 0, width, height);
            targetCanvasContext.drawImage(this.$canvas, 0, 0, width, height);
            resolve();
          }, true);
        });
      }
    }
    this.isProcessingQueue = false;
  }

  getColorGlow(source: GlowColorSource, width: number, height: number) {
    const uniforms = this.colorMesh.program.uniforms;

    uniforms.u_resolution.value = [width, height];

    uniforms.u_color.value = hexToComponent(source.color);

    uniforms.u_intensity.value = source.intensity || 1;
    uniforms.u_dither.value = source.dither || uniforms.u_dither.value;

    this.renderer.render({ scene: this.colorMesh });
  }

  getGradientGlow(source: GlowGradientSource, width: number, height: number) {
    const uniforms = this.gradientMesh.program.uniforms;

    uniforms.u_resolution.value = [width, height];

    uniforms.u_startColor.value = hexToComponent(source.gradient.startColor);
    uniforms.u_stopColor.value = hexToComponent(source.gradient.stopColor);
    uniforms.u_startPosition.value = source.gradient.startPosition;
    uniforms.u_stopPosition.value = source.gradient.stopPosition;
    uniforms.u_orientation.value = source.gradient.orientation;

    uniforms.u_intensity.value = source.intensity || 1;
    uniforms.u_dither.value = source.dither || uniforms.u_dither.value;

    this.renderer.render({ scene: this.gradientMesh });
  }

  getImageGlow(
    source: GlowImageSource,
    config: GlowConfig,
    width: number,
    height: number,
  ) {
    if (source.image) {
      const uniforms = this.imageMesh.program.uniforms;
      if (config.glowPosition) {
        uniforms.u_offset.value = OFFSET_CONFIG[config.glowPosition];
      }
      if (config.glowCircleSize !== undefined) {
        uniforms.u_circleSize.value = config.glowCircleSize;
      }
      if (config.glowImageScale !== undefined) {
        uniforms.u_scale.value.set(DEFAULT_SCALE * config.glowImageScale);
      }
      uniforms.u_texture.value.image = source.image;
      uniforms.u_texture.value.needsUpdate = true;
      uniforms.u_resolution.value = [width, height];
      uniforms.u_intensity.value =
        source.intensity || uniforms.u_intensity.value || 1;
      uniforms.u_dither.value = source.dither || uniforms.u_dither.value;
      uniforms.u_texture_size.value = [source.image.width, source.image.height];
      this.renderer.render({ scene: this.imageMesh });
      if (!DISABLE_BLUR) {
        this.postComposite.resize();
        this.postBlur.resize();

        this.blurResolution.set(
          this.postBlur.options.width,
          this.postBlur.options.height,
        );

        if (config) {
          if (config.blur) {
            this.postBlur.passes.forEach((pass) => {
              pass.uniforms.u_blur_distance.value =
                config.blur || DEFAULT_BLUR_DISTANCE;
            });
          }
        }

        // Disable compositePass pass, so this post will just render the scene for now
        this.compositePass.enabled = false;
        // `targetOnly` prevents post from rendering to the canvas
        this.postComposite.targetOnly = true;
        // This renders the scene to postComposite.uniform.value
        this.postComposite.render({ scene: this.imageMesh });
        // This render the blur effect's  blur passes to postBloom.fbo.read
        // Passing in a `texture` argument avoids the post initially rendering the scene

        this.postBlur.render({
          texture: this.postComposite.uniform.value,
        });
        // Re-enable composite pass
        this.compositePass.enabled = true;
        // // Allow post to render to canvas upon its last pass
        this.postComposite.targetOnly = false;
        // // This renders to canvas, compositing the blur pass on top
        // // pass back in its previous render of the scene to avoid re-rendering

        this.postComposite.render({
          texture: this.postBlur.uniform.value,
        });
      }
    }
  }
}

const GlowWebglFactory =
  typeof window !== 'undefined'
    ? GlowWebglFactoryClientSide
    : GlowWebglFactorySSR;

export default new GlowWebglFactory();
