import { CSSProperties, useState } from 'react';

import Button from '~/components/atoms/Buttons/Ctas/Button';
import { ImageProps } from '~/components/atoms/image/image.types.d';
import Link from '~/components/atoms/link';
import Observer from '~/components/atoms/observer';
import { GlowPosition } from '~/components/molecules/glow/types.d';
import GlowMedia from '~/components/molecules/glowMedia';
import Logo from '~/components/molecules/logo';
import PortableText from '~/components/molecules/portableText';
import Shadow from '~/components/molecules/shadow';
import decorationsStyles from '~/styles/theme/decorations.module.css';
import { cn, getSpacersStyles } from '~/utils';

import styles from './styles.module.css';
import { CMSEditorialCardItem, CMSModuleEditorialCardsProps } from './types';

const EditorialCards = ({
  editorialCardItems,
  hasSpacers,
  accentColor,
  background,
  className,
  slug,
}: CMSModuleEditorialCardsProps) => {
  const spacerValues = getSpacersStyles(hasSpacers?.before, hasSpacers?.after);
  const [isInView, updateIsInView] = useState<false | DOMRect>(false);

  return (
    <section
      className={cn(
        styles.editorialCards,
        background && styles.hasBackground,
        hasSpacers?.before && decorationsStyles.hasSpacerBefore,
        hasSpacers?.after && decorationsStyles.hasSpacerAfter,
        className,
      )}
      style={
        {
          ...(spacerValues as CSSProperties),
          ...(accentColor ? { '--color-accent': `var(${accentColor})` } : {}),
          ...(background ? { '--module-background': background } : {}),
        } as CSSProperties
      }
      id={slug}
    >
      <Observer
        callback={updateIsInView}
        options={{ rootMargin: '200% 0%' }}
        className={styles.content}
      >
        {editorialCardItems &&
          editorialCardItems.map((card: CMSEditorialCardItem) => {
            return (
              <div key={card._key} className={styles.card}>
                <Link className={styles.innerCardContainer} to={card.link}>
                  <GlowMedia
                    sanityMedia={card.image}
                    elementClassName={styles.imageElement}
                    containerClassName={styles.imageContainer}
                    className={styles.glowContainer}
                    glowBackgroundClassName={styles.glow}
                    glowConfig={{
                      intensity: 0.45,
                      scale: 1.25,
                      type: 'IMAGE',
                      position: GlowPosition.TOP_RIGHT,
                    }}
                    aspectRatio={parseInt(styles.imageAspectRatio)}
                    isDisplayed={isInView !== false}
                  >
                    <Shadow className={styles.shadowContainer} />
                  </GlowMedia>
                </Link>
                <Logo
                  svg={card.logo?.svg}
                  image={
                    card.logo?.image &&
                    ({
                      source: card.logo?.image,
                    } as ImageProps)
                  }
                  className={cn(
                    styles.icon,
                    card.logo?.svg && styles.iconSvg,
                    card.logo?.image && styles.iconImage,
                    card.logoHeight === 'tall' && styles.isTall,
                  )}
                />
                <PortableText
                  isAutoGrid={false}
                  animate={false}
                  content={card.text}
                  className={styles.text}
                  options={{
                    block: {
                      bodies: {
                        className: styles.bodies,
                      },
                    },
                  }}
                />
                <Button
                  to={card.link}
                  className={styles.button}
                  buttonVariant="textWithIcon"
                >
                  {card.link.label}
                </Button>
              </div>
            );
          })}
      </Observer>
    </section>
  );
};

export default EditorialCards;
